import React from 'react';

interface ConnectLogoProps {
  isActive?: boolean;
  onClick?: () => void;
  pendingRequests?: number;
}

const ConnectLogo: React.FC<ConnectLogoProps> = ({ isActive = false, onClick, pendingRequests = 0 }) => {
  return (
    <div
      onClick={onClick}
      style={{
        width: 48,
        height: 48,
        borderRadius: isActive ? 16 : 24,
        background: isActive ? 'var(--gradient-primary)' : 'var(--bg-tertiary)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        cursor: 'pointer',
        transition: 'all 0.3s var(--ease-out-cubic)',
        position: 'relative',
        boxShadow: isActive ? 'var(--shadow-glow)' : 'none',
      }}
      title="Connect - Friends & Messages"
    >
      <svg
        width="28"
        height="28"
        viewBox="0 0 24 24"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        {/* Connect logo - stylized "C" with connection nodes */}
        <path
          d="M12 3C7.03 3 3 7.03 3 12C3 16.97 7.03 21 12 21C14.39 21 16.68 20.06 18.36 18.36"
          stroke={isActive ? 'white' : 'var(--primary)'}
          strokeWidth="2.5"
          strokeLinecap="round"
          fill="none"
        />
        <circle
          cx="12"
          cy="12"
          r="3"
          fill={isActive ? 'white' : 'var(--primary)'}
        />
        <circle
          cx="19"
          cy="12"
          r="2"
          fill={isActive ? 'white' : 'var(--primary)'}
        />
        <line
          x1="15"
          y1="12"
          x2="17"
          y2="12"
          stroke={isActive ? 'white' : 'var(--primary)'}
          strokeWidth="2"
          strokeLinecap="round"
        />
      </svg>

      {/* Pending requests badge */}
      {pendingRequests > 0 && (
        <div
          style={{
            position: 'absolute',
            top: -4,
            right: -4,
            minWidth: 18,
            height: 18,
            borderRadius: 9,
            background: 'var(--danger)',
            color: 'white',
            fontSize: 11,
            fontWeight: 600,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            padding: '0 5px',
            boxShadow: '0 2px 4px rgba(0,0,0,0.3)',
          }}
        >
          {pendingRequests > 99 ? '99+' : pendingRequests}
        </div>
      )}
    </div>
  );
};

export default ConnectLogo;
