import React, { useState, useEffect } from 'react';
import { FriendRequest } from '../types';
import {
  getPendingRequests,
  getOutgoingRequests,
  acceptFriendRequest,
  rejectFriendRequest,
  cancelFriendRequest,
  sendFriendRequestByUsername,
} from '../services/friendsApi';
import { getAvatarColor } from '../utils/avatarColors';

interface FriendRequestsProps {
  onBack: () => void;
  onRequestHandled?: () => void;
}

const FriendRequests: React.FC<FriendRequestsProps> = ({ onBack, onRequestHandled }) => {
  const [incomingRequests, setIncomingRequests] = useState<FriendRequest[]>([]);
  const [outgoingRequests, setOutgoingRequests] = useState<FriendRequest[]>([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState<'incoming' | 'outgoing' | 'add'>('incoming');
  const [addUsername, setAddUsername] = useState('');
  const [addError, setAddError] = useState('');
  const [addSuccess, setAddSuccess] = useState('');
  const [addLoading, setAddLoading] = useState(false);

  useEffect(() => {
    loadRequests();
  }, []);

  const loadRequests = async () => {
    try {
      setLoading(true);
      const [incoming, outgoing] = await Promise.all([
        getPendingRequests(),
        getOutgoingRequests(),
      ]);
      setIncomingRequests(incoming);
      setOutgoingRequests(outgoing);
    } catch (error) {
      console.error('Failed to load requests:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleAccept = async (requestId: string) => {
    try {
      await acceptFriendRequest(requestId);
      setIncomingRequests((prev) => prev.filter((r) => r.id !== requestId));
      onRequestHandled?.();
    } catch (error) {
      console.error('Failed to accept request:', error);
    }
  };

  const handleReject = async (requestId: string) => {
    try {
      await rejectFriendRequest(requestId);
      setIncomingRequests((prev) => prev.filter((r) => r.id !== requestId));
      onRequestHandled?.();
    } catch (error) {
      console.error('Failed to reject request:', error);
    }
  };

  const handleCancel = async (requestId: string) => {
    try {
      await cancelFriendRequest(requestId);
      setOutgoingRequests((prev) => prev.filter((r) => r.id !== requestId));
    } catch (error) {
      console.error('Failed to cancel request:', error);
    }
  };

  const handleAddFriend = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!addUsername.trim()) return;

    setAddError('');
    setAddSuccess('');
    setAddLoading(true);

    try {
      await sendFriendRequestByUsername(addUsername.trim());
      setAddSuccess(`Friend request sent to ${addUsername}!`);
      setAddUsername('');
      loadRequests();
    } catch (error: unknown) {
      const errorMessage = (error as { response?: { data?: { error?: string } } })?.response?.data?.error || 'Failed to send request';
      setAddError(errorMessage);
    } finally {
      setAddLoading(false);
    }
  };

  return (
    <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
      {/* Header */}
      <div style={{ padding: '12px 16px', borderBottom: '1px solid var(--border)' }}>
        <div style={{ display: 'flex', alignItems: 'center', gap: 12 }}>
          <button
            onClick={onBack}
            style={{
              background: 'none',
              border: 'none',
              color: 'var(--text-secondary)',
              cursor: 'pointer',
              padding: 4,
              display: 'flex',
            }}
          >
            <svg width="20" height="20" viewBox="0 0 24 24" fill="none">
              <path d="M19 12H5M12 19l-7-7 7-7" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
          <span style={{ fontWeight: 600, fontSize: 14, color: 'var(--text-primary)' }}>
            Friend Requests
          </span>
        </div>

        {/* Tabs */}
        <div style={{ display: 'flex', gap: 8, marginTop: 12 }}>
          {[
            { key: 'incoming', label: 'Incoming', count: incomingRequests.length },
            { key: 'outgoing', label: 'Outgoing', count: outgoingRequests.length },
            { key: 'add', label: 'Add Friend', count: 0 },
          ].map(({ key, label, count }) => (
            <button
              key={key}
              onClick={() => setActiveTab(key as typeof activeTab)}
              style={{
                flex: 1,
                padding: '6px 8px',
                background: activeTab === key ? 'var(--accent-soft)' : 'transparent',
                border: activeTab === key ? '1px solid var(--border-accent)' : '1px solid transparent',
                borderRadius: 'var(--radius-sm)',
                color: activeTab === key ? 'var(--accent)' : 'var(--text-secondary)',
                fontSize: 12,
                fontWeight: 500,
                cursor: 'pointer',
                transition: 'all 0.2s',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                gap: 4,
              }}
            >
              {label}
              {count > 0 && (
                <span
                  style={{
                    background: activeTab === key ? 'var(--accent)' : 'var(--bg-tertiary)',
                    color: activeTab === key ? 'white' : 'var(--text-muted)',
                    borderRadius: 10,
                    padding: '1px 6px',
                    fontSize: 11,
                  }}
                >
                  {count}
                </span>
              )}
            </button>
          ))}
        </div>
      </div>

      {/* Content */}
      <div style={{ flex: 1, overflowY: 'auto', padding: 8 }}>
        {loading ? (
          <div style={{ padding: 24, textAlign: 'center', color: 'var(--text-muted)' }}>
            Loading...
          </div>
        ) : (
          <>
            {/* Incoming requests */}
            {activeTab === 'incoming' && (
              <>
                {incomingRequests.length === 0 ? (
                  <div style={{ padding: 24, textAlign: 'center', color: 'var(--text-muted)' }}>
                    No pending requests
                  </div>
                ) : (
                  incomingRequests.map((request) => (
                    <RequestItem
                      key={request.id}
                      request={request}
                      type="incoming"
                      onAccept={() => handleAccept(request.id)}
                      onReject={() => handleReject(request.id)}
                    />
                  ))
                )}
              </>
            )}

            {/* Outgoing requests */}
            {activeTab === 'outgoing' && (
              <>
                {outgoingRequests.length === 0 ? (
                  <div style={{ padding: 24, textAlign: 'center', color: 'var(--text-muted)' }}>
                    No outgoing requests
                  </div>
                ) : (
                  outgoingRequests.map((request) => (
                    <RequestItem
                      key={request.id}
                      request={request}
                      type="outgoing"
                      onCancel={() => handleCancel(request.id)}
                    />
                  ))
                )}
              </>
            )}

            {/* Add friend */}
            {activeTab === 'add' && (
              <div style={{ padding: 16 }}>
                <form onSubmit={handleAddFriend}>
                  <p style={{ color: 'var(--text-secondary)', fontSize: 13, marginBottom: 16 }}>
                    You can add a friend by their username.
                  </p>
                  <div style={{ display: 'flex', gap: 8 }}>
                    <input
                      type="text"
                      placeholder="Enter username"
                      value={addUsername}
                      onChange={(e) => {
                        setAddUsername(e.target.value);
                        setAddError('');
                        setAddSuccess('');
                      }}
                      style={{
                        flex: 1,
                        padding: '10px 12px',
                        background: 'var(--bg-secondary)',
                        border: addError ? '1px solid var(--danger)' : '1px solid var(--border)',
                        borderRadius: 'var(--radius-sm)',
                        color: 'var(--text-primary)',
                        fontSize: 14,
                        outline: 'none',
                      }}
                    />
                    <button
                      type="submit"
                      disabled={addLoading || !addUsername.trim()}
                      style={{
                        padding: '10px 20px',
                        background: 'var(--gradient-primary)',
                        border: 'none',
                        borderRadius: 'var(--radius-sm)',
                        color: 'white',
                        fontWeight: 500,
                        cursor: addLoading ? 'not-allowed' : 'pointer',
                        opacity: addLoading || !addUsername.trim() ? 0.6 : 1,
                      }}
                    >
                      {addLoading ? 'Sending...' : 'Send'}
                    </button>
                  </div>
                  {addError && (
                    <p style={{ color: 'var(--danger)', fontSize: 13, marginTop: 8 }}>{addError}</p>
                  )}
                  {addSuccess && (
                    <p style={{ color: 'var(--success)', fontSize: 13, marginTop: 8 }}>{addSuccess}</p>
                  )}
                </form>
              </div>
            )}
          </>
        )}
      </div>
    </div>
  );
};

interface RequestItemProps {
  request: FriendRequest;
  type: 'incoming' | 'outgoing';
  onAccept?: () => void;
  onReject?: () => void;
  onCancel?: () => void;
}

const RequestItem: React.FC<RequestItemProps> = ({ request, type, onAccept, onReject, onCancel }) => {
  const user = type === 'incoming' ? request.from_user : request.to_user;
  if (!user) return null;

  const avatarColor = getAvatarColor(user.id);

  return (
    <div
      style={{
        display: 'flex',
        alignItems: 'center',
        gap: 12,
        padding: '12px',
        borderRadius: 'var(--radius-sm)',
        background: 'var(--bg-card)',
        marginBottom: 8,
      }}
    >
      {/* Avatar */}
      <div
        style={{
          width: 40,
          height: 40,
          borderRadius: '50%',
          background: user.avatar_url ? `url(${user.avatar_url}) center/cover` : avatarColor,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          color: 'white',
          fontWeight: 600,
          fontSize: 16,
          flexShrink: 0,
        }}
      >
        {!user.avatar_url && user.username[0].toUpperCase()}
      </div>

      {/* Info */}
      <div style={{ flex: 1, minWidth: 0 }}>
        <div style={{ fontWeight: 500, color: 'var(--text-primary)', fontSize: 14 }}>
          {user.username}
        </div>
        <div style={{ fontSize: 12, color: 'var(--text-muted)' }}>
          {type === 'incoming' ? 'Wants to be your friend' : 'Pending'}
        </div>
      </div>

      {/* Actions */}
      <div style={{ display: 'flex', gap: 8 }}>
        {type === 'incoming' ? (
          <>
            <button
              onClick={onAccept}
              style={{
                width: 36,
                height: 36,
                borderRadius: '50%',
                background: 'var(--success)',
                border: 'none',
                color: 'white',
                cursor: 'pointer',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
              }}
              title="Accept"
            >
              <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
                <path d="M20 6L9 17l-5-5" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </button>
            <button
              onClick={onReject}
              style={{
                width: 36,
                height: 36,
                borderRadius: '50%',
                background: 'var(--bg-tertiary)',
                border: 'none',
                color: 'var(--text-secondary)',
                cursor: 'pointer',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
              }}
              title="Reject"
            >
              <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
                <path d="M18 6L6 18M6 6l12 12" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </button>
          </>
        ) : (
          <button
            onClick={onCancel}
            style={{
              padding: '6px 12px',
              borderRadius: 'var(--radius-sm)',
              background: 'var(--bg-tertiary)',
              border: 'none',
              color: 'var(--text-secondary)',
              fontSize: 13,
              cursor: 'pointer',
            }}
          >
            Cancel
          </button>
        )}
      </div>
    </div>
  );
};

export default FriendRequests;
