import React, { useState, useRef, useEffect, useCallback } from 'react';
import { Channel, User } from '../types';
import { VoiceUser, ScreenShareState } from '../services/webrtc';
import { getAvatarColor } from '../utils/avatarColors';
import {
  SpeakerIcon,
  SpeakerMutedIcon,
  SpeakerLowIcon,
  MicrophoneIcon,
  MicrophoneMutedIcon,
  HeadphonesIcon,
  DeafenedIcon,
  DisconnectIcon,
  WarningIcon,
  ScreenShareIcon,
  ScreenShareActiveIcon,
  ScreenShareStopIcon,
  CloseIcon,
  FullscreenIcon,
  ExitFullscreenIcon,
  PlayIcon,
} from './Icons';

//      localStorage
const SCREEN_SHARE_VOLUME_KEY = 'screenShareVolume';

// Type for HTMLVideoElement with requestVideoFrameCallback (not yet in TypeScript lib)
type HTMLVideoElementWithCallback = HTMLVideoElement & {
  requestVideoFrameCallback?: (callback: (now: DOMHighResTimeStamp, metadata: object) => void) => number;
};

export interface DebugInfo {
  peersCount: number;
  peers: Array<{
    id: string;
    connectionState: string;
    iceState: string;
    signalingState: string;
  }>;
  screenShareState: {
    isSharing: boolean;
    isLocalShare: boolean;
    userId: string | null;
    hasStream: boolean;
  };
  localStreamActive: boolean;
  audioTracksCount: number;
}

interface VoiceChannelViewProps {
  channel: Channel;
  users: VoiceUser[];
  isConnected: boolean;
  isMuted: boolean;
  isDeafened?: boolean;
  error: string | null;
  onToggleMute: () => void;
  onToggleDeafen?: () => void;
  onDisconnect: () => void;
  onUserVolumeChange?: (userId: string, volume: number) => void;
  currentUserId?: string;
  isScreenSharing?: boolean;
  onScreenShareClick?: () => void;
  debugInfo?: DebugInfo | null;
  onRequestDebugInfo?: () => void;
  screenShareState?: ScreenShareState;
  onStopScreenShare?: () => void;
  onWatchingStateChange?: (isWatching: boolean) => void;
  externalIsWatching?: boolean;
  onUserClick?: (user: User, position: { x: number; y: number }) => void;
}

interface VideoStats {
  width: number;
  height: number;
  fps: number;
}

const VoiceChannelView: React.FC<VoiceChannelViewProps> = ({
  channel,
  users,
  isConnected,
  isMuted,
  isDeafened,
  error,
  onToggleMute,
  onToggleDeafen,
  onDisconnect,
  onUserVolumeChange,
  currentUserId,
  isScreenSharing,
  onScreenShareClick,
  debugInfo,
  onRequestDebugInfo,
  screenShareState,
  onStopScreenShare,
  onWatchingStateChange,
  externalIsWatching,
  onUserClick,
}) => {
  const [selectedUser, setSelectedUser] = useState<VoiceUser | null>(null);
  const [popupPosition, setPopupPosition] = useState<{ x: number; y: number } | null>(null);
  const [showDebug, setShowDebug] = useState(false);
  const [videoStats, setVideoStats] = useState<VideoStats | null>(null);
  const popupRef = useRef<HTMLDivElement>(null);
  const screenShareVideoRef = useRef<HTMLVideoElement>(null);
  const screenShareContainerRef = useRef<HTMLDivElement>(null);

  //   
  const [savedVolume] = useState(() => {
    const saved = localStorage.getItem(SCREEN_SHARE_VOLUME_KEY);
    return saved !== null ? parseInt(saved, 10) : 50;
  });
  const [isAudioActivated, setIsAudioActivated] = useState(false);
  const [screenShareVolume, setScreenShareVolume] = useState(0);
  const [showVolumeSlider, setShowVolumeSlider] = useState(false);

  //    ( )
  const [isWatchingStream, setIsWatchingStream] = useState(false);

  //   
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [showFullscreenControls, setShowFullscreenControls] = useState(false);
  const hideControlsTimeoutRef = useRef<NodeJS.Timeout | null>(null);

  const handleHeaderClick = () => {
    setShowDebug(!showDebug);
    if (!showDebug && onRequestDebugInfo) {
      onRequestDebugInfo();
    }
  };

  //      
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (popupRef.current && !popupRef.current.contains(event.target as Node)) {
        setSelectedUser(null);
        setPopupPosition(null);
      }
    };

    if (selectedUser) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [selectedUser]);

  // Ref   ID   (       )
  const currentStreamUserIdRef = useRef<string | null>(null);

  //     screen share
  useEffect(() => {
    const video = screenShareVideoRef.current;
    console.log('[ScreenShare] Stream effect - video:', !!video, 'stream:', !!screenShareState?.stream, 'userId:', screenShareState?.userId);

    if (video && screenShareState?.stream) {
      const isNewStreamer = currentStreamUserIdRef.current !== screenShareState.userId;
      console.log('[ScreenShare] isNewStreamer:', isNewStreamer, 'prev:', currentStreamUserIdRef.current, 'new:', screenShareState.userId);

      //   srcObject
      video.srcObject = screenShareState.stream;

      //       
      if (isNewStreamer) {
        console.log('[ScreenShare] New streamer detected, resetting state');
        currentStreamUserIdRef.current = screenShareState.userId;
        setIsAudioActivated(false);
        setScreenShareVolume(0);
        setIsWatchingStream(false);
      }

      // :  muted,  volume
      video.muted = true;
      video.volume = 0;
      console.log('[ScreenShare] Stream set, muted:', video.muted, 'volume:', video.volume);
    } else if (!screenShareState?.stream) {
      //   -  ref
      currentStreamUserIdRef.current = null;
    }
  }, [screenShareState?.stream, screenShareState?.userId]);

  //      
  useEffect(() => {
    console.log('[ScreenShare] Reset effect check - isSharing:', screenShareState?.isSharing, 'hasStream:', !!screenShareState?.stream);
    if (!screenShareState?.isSharing || !screenShareState?.stream) {
      console.log('[ScreenShare] RESET from stream end effect!');
      setIsWatchingStream(false);
      setIsAudioActivated(false);
      setScreenShareVolume(0);
    }
  }, [screenShareState?.isSharing, screenShareState?.stream]);

  //      
  useEffect(() => {
    console.log('[ScreenShare] Notifying parent, isWatchingStream:', isWatchingStream);
    if (onWatchingStateChange) {
      onWatchingStateChange(isWatchingStream);
    }
  }, [isWatchingStream, onWatchingStateChange]);

  // Ref     externalIsWatching
  const prevExternalIsWatchingRef = useRef<boolean | undefined>(externalIsWatching);

  //      ( " "   )
  //     externalIsWatching  true  false
  useEffect(() => {
    const prevValue = prevExternalIsWatchingRef.current;
    prevExternalIsWatchingRef.current = externalIsWatching;

    //    externalIsWatching   true  false
    if (prevValue === true && externalIsWatching === false && isWatchingStream) {
      console.log('[ScreenShare] RESET from externalIsWatching change (true -> false)');
      setIsWatchingStream(false);
      setIsAudioActivated(false);
      setScreenShareVolume(0);
      if (screenShareVideoRef.current) {
        screenShareVideoRef.current.muted = true;
        screenShareVideoRef.current.volume = 0;
      }
    }
  }, [externalIsWatching, isWatchingStream]);

  //      -    
  const startWatchingStream = useCallback(() => {
    console.log('[ScreenShare] startWatchingStream called');
    console.log('[ScreenShare] screenShareState:', screenShareState);
    console.log('[ScreenShare] videoRef exists:', !!screenShareVideoRef.current);

    setIsWatchingStream(true);

    const video = screenShareVideoRef.current;
    if (video) {
      console.log('[ScreenShare] video.srcObject:', video.srcObject);
      console.log('[ScreenShare] video.readyState:', video.readyState);

      //   srcObject 
      if (screenShareState?.stream && !video.srcObject) {
        console.log('[ScreenShare] Setting srcObject from screenShareState');
        video.srcObject = screenShareState.stream;
      }

      video.muted = true;
      video.volume = 0;
      video.play().then(() => {
        console.log('[ScreenShare] Play started successfully');
      }).catch((err) => {
        console.error('[ScreenShare] Failed to play video:', err);
      });
    } else {
      console.error('[ScreenShare] Video ref is null!');
    }
  }, [screenShareState]);

  //  FPS    
  useEffect(() => {
    const video = screenShareVideoRef.current;
    if (!video || !screenShareState?.stream) {
      setVideoStats(null);
      return;
    }

    let animationFrameId: number;
    let fpsInterval: ReturnType<typeof setInterval>;

    const updateStats = () => {
      if (video.videoWidth && video.videoHeight) {
        setVideoStats(prev => ({
          width: video.videoWidth,
          height: video.videoHeight,
          fps: prev?.fps || 0,
        }));
      }
    };

    //     
    video.addEventListener('loadedmetadata', updateStats);
    video.addEventListener('resize', updateStats);

    //  FPS  requestVideoFrameCallback  
    if ('requestVideoFrameCallback' in video) {
      let lastTime = performance.now();
      let frameCount = 0;

      const countFrame = (now: number) => {
        frameCount++;
        const elapsed = now - lastTime;

        if (elapsed >= 1000) {
          const fps = Math.round((frameCount * 1000) / elapsed);
          setVideoStats(prev => prev ? { ...prev, fps } : null);
          frameCount = 0;
          lastTime = now;
        }

        if (screenShareState?.stream) {
          (video as HTMLVideoElementWithCallback).requestVideoFrameCallback?.(countFrame);
        }
      };

      (video as HTMLVideoElementWithCallback).requestVideoFrameCallback?.(countFrame);
    } else {
      // Fallback:  FPS   
      const videoTrack = screenShareState.stream.getVideoTracks()[0];
      if (videoTrack) {
        const settings = videoTrack.getSettings();
        if (settings.frameRate) {
          setVideoStats(prev => prev ? { ...prev, fps: Math.round(settings.frameRate!) } : null);
        }
      }
    }

    //  
    updateStats();

    return () => {
      video.removeEventListener('loadedmetadata', updateStats);
      video.removeEventListener('resize', updateStats);
      if (animationFrameId) cancelAnimationFrame(animationFrameId);
      if (fpsInterval) clearInterval(fpsInterval);
    };
  }, [screenShareState?.stream]);

  const toggleFullscreen = async () => {
    if (!screenShareContainerRef.current) return;

    try {
      if (!document.fullscreenElement) {
        await screenShareContainerRef.current.requestFullscreen();
      } else {
        await document.exitFullscreen();
      }
    } catch (err) {
      console.error('Fullscreen error:', err);
    }
  };

  //   
  useEffect(() => {
    const handleFullscreenChange = () => {
      const isFs = !!document.fullscreenElement;
      setIsFullscreen(isFs);
      if (isFs) {
        setShowFullscreenControls(true);
      }
    };

    document.addEventListener('fullscreenchange', handleFullscreenChange);
    return () => {
      document.removeEventListener('fullscreenchange', handleFullscreenChange);
      if (hideControlsTimeoutRef.current) {
        clearTimeout(hideControlsTimeoutRef.current);
      }
    };
  }, []);

  //      
  const handleFullscreenMouseMove = useCallback(() => {
    if (!isFullscreen) return;

    setShowFullscreenControls(true);

    if (hideControlsTimeoutRef.current) {
      clearTimeout(hideControlsTimeoutRef.current);
    }

    hideControlsTimeoutRef.current = setTimeout(() => {
      setShowFullscreenControls(false);
    }, 3000);
  }, [isFullscreen]);

  const handleUserClick = (user: VoiceUser, event: React.MouseEvent) => {
    //     
    if (user.id === currentUserId) return;

    //   onUserClick -  
    if (onUserClick) {
      const rect = (event.currentTarget as HTMLElement).getBoundingClientRect();
      //  VoiceUser  User
      const userProfile: User = {
        id: user.id,
        username: user.username,
        email: '',
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString(),
      };
      //      (300px  + 10px )
      onUserClick(userProfile, { x: rect.left - 310, y: rect.top });
    } else {
      // Fallback:  volume popup
      const rect = (event.currentTarget as HTMLElement).getBoundingClientRect();
      setPopupPosition({ x: rect.left + rect.width / 2, y: rect.bottom + 10 });
      setSelectedUser(user);
    }
  };

  const handleUserContextMenu = (user: VoiceUser, event: React.MouseEvent) => {
    //   -  volume popup
    if (user.id === currentUserId) return;
    event.preventDefault();

    const rect = (event.currentTarget as HTMLElement).getBoundingClientRect();
    setPopupPosition({ x: rect.left + rect.width / 2, y: rect.bottom + 10 });
    setSelectedUser(user);
  };

  const handleVolumeChange = (userId: string, volume: number) => {
    if (onUserVolumeChange) {
      onUserVolumeChange(userId, volume);
    }
  };

  //     screen share
  const handleScreenShareVolumeChange = useCallback((newVolume: number) => {
    setScreenShareVolume(newVolume);
    localStorage.setItem(SCREEN_SHARE_VOLUME_KEY, String(newVolume));
    if (screenShareVideoRef.current) {
      screenShareVideoRef.current.volume = newVolume / 100;
      screenShareVideoRef.current.muted = newVolume === 0;
    }
  }, []);

  const activateScreenShareAudio = useCallback(() => {
    if (!isAudioActivated) {
      setIsAudioActivated(true);
      const newVolume = savedVolume > 0 ? savedVolume : 50;
      setScreenShareVolume(newVolume);
      if (screenShareVideoRef.current) {
        screenShareVideoRef.current.volume = newVolume / 100;
        screenShareVideoRef.current.muted = false;
      }
    }
  }, [isAudioActivated, savedVolume]);

  const toggleScreenShareMute = useCallback(() => {
    if (screenShareVolume === 0) {
      const restoredVolume = savedVolume > 0 ? savedVolume : 50;
      handleScreenShareVolumeChange(restoredVolume);
    } else {
      handleScreenShareVolumeChange(0);
    }
  }, [screenShareVolume, savedVolume, handleScreenShareVolumeChange]);

  //  
  const ScreenShareVolumeIcon = screenShareVolume === 0 ? SpeakerMutedIcon : screenShareVolume < 50 ? SpeakerLowIcon : SpeakerIcon;

  // ,     
  const hasScreenShare = screenShareState?.isSharing && screenShareState?.stream;
  const isLocalScreenShare = screenShareState?.userId === currentUserId;

  //    video 
  useEffect(() => {
    const video = screenShareVideoRef.current;
    if (!video) return;

    if (isLocalScreenShare) {
      //    mute
      video.muted = true;
      video.volume = 0;
    } else if (!isWatchingStream) {
      //    -   
      video.muted = true;
      video.volume = 0;
      video.pause();
      console.log('[ScreenShare] Not watching, paused and muted');
    } else if (isAudioActivated) {
      //   -  
      video.muted = screenShareVolume === 0;
      video.volume = Math.max(0, Math.min(1, screenShareVolume / 100));
      video.play().catch(() => {}); //  
      console.log('[ScreenShare] Volume set:', screenShareVolume, 'muted:', video.muted, 'actual volume:', video.volume);
    } else {
      //    - mute   
      video.muted = true;
      video.volume = 0;
      video.play().catch(() => {});
    }
  }, [screenShareVolume, isAudioActivated, isLocalScreenShare, isWatchingStream]);

  //          ( + screen share)
  const getCardSize = (totalCount: number) => {
    //  
    let width = 800;
    let height = 450;

    if (totalCount === 1) {
      width = 800;
      height = 450;
    } else if (totalCount === 2) {
      width = 400;
      height = 225;
    } else if (totalCount <= 4) {
      width = 280;
      height = 158;
    } else if (totalCount <= 6) {
      width = 250;
      height = 140;
    } else if (totalCount <= 9) {
      width = 220;
      height = 124;
    } else {
      width = 180;
      height = 100;
    }

    return { width, height };
  };

  //      3,   
  //  :  ,  screen share
  type GridItem = { type: 'user'; user: VoiceUser } | { type: 'screenShare' };

  const getGridRows = (userList: VoiceUser[], includeScreenShare: boolean): GridItem[][] => {
    const items: GridItem[] = [];

    //   screen share  
    if (includeScreenShare) {
      items.push({ type: 'screenShare' });
    }

    //   
    const sorted = [...userList].sort((a, b) => a.id.localeCompare(b.id));
    sorted.forEach(user => items.push({ type: 'user', user }));

    const rows: GridItem[][] = [];
    for (let i = 0; i < items.length; i += 3) {
      rows.push(items.slice(i, i + 3));
    }

    return rows;
  };

  const totalItems = users.length + (hasScreenShare ? 1 : 0);
  const cardSize = getCardSize(totalItems);
  const gridRows = getGridRows(users, !!hasScreenShare);

  return (
    <div style={styles.container}>
      {/*   */}
      <div style={styles.header} onClick={handleHeaderClick} title="   ">
        <span style={styles.headerIcon}><SpeakerIcon size={20} /></span>
        <h3 style={styles.headerTitle}>{channel.name}</h3>
        <span style={styles.debugToggle}>{showDebug ? '' : ''}</span>
      </div>

      {/*   */}
      {showDebug && (
        <div style={styles.debugPanel}>
          <div style={styles.debugTitle}>  </div>
          <div style={styles.debugSection}>
            <strong>:</strong>
            <div> : {isConnected ? ' ' : ' '}</div>
            <div> : {isMuted ? ' ' : ' '}</div>
            <div> Screen Share: {isScreenSharing ? ' ' : ' '}</div>
            <div> : {users.length}</div>
          </div>
          {debugInfo && (
            <>
              <div style={styles.debugSection}>
                <strong>WebRTC  ({debugInfo.peersCount}):</strong>
                {debugInfo.peers.map(peer => (
                  <div key={peer.id} style={styles.debugPeer}>
                    <div>ID: {peer.id.substring(0, 8)}...</div>
                    <div> Connection: <span style={{ color: peer.connectionState === 'connected' ? '#3ba55c' : '#f04747' }}>{peer.connectionState}</span></div>
                    <div> ICE: <span style={{ color: peer.iceState === 'connected' ? '#3ba55c' : '#faa61a' }}>{peer.iceState}</span></div>
                    <div> Signaling: {peer.signalingState}</div>
                  </div>
                ))}
              </div>
              <div style={styles.debugSection}>
                <strong>Screen Share State:</strong>
                <div> isSharing: {debugInfo.screenShareState.isSharing ? '' : ''}</div>
                <div> isLocalShare: {debugInfo.screenShareState.isLocalShare ? '' : ''}</div>
                <div> userId: {debugInfo.screenShareState.userId || 'null'}</div>
                <div> hasStream: {debugInfo.screenShareState.hasStream ? '' : ''}</div>
              </div>
              <div style={styles.debugSection}>
                <strong> :</strong>
                <div> : {debugInfo.localStreamActive ? '' : ''}</div>
                <div> Audio : {debugInfo.audioTracksCount}</div>
              </div>
            </>
          )}
          <button
            style={styles.debugButton}
            onClick={(e) => {
              e.stopPropagation();
              if (onRequestDebugInfo) onRequestDebugInfo();
            }}
          >
             
          </button>
        </div>
      )}

      {/*     */}
      <div style={styles.content}>
        {/*   */}
        <div style={styles.mainContainer}>
          {error ? (
            <div style={styles.errorContainer}>
              <div style={styles.errorIcon}><WarningIcon size={48} color="#f04747" /></div>
              <div style={styles.errorText}>{error}</div>
              <button onClick={onDisconnect} style={styles.retryButton}>
                
              </button>
            </div>
          ) : !isConnected ? (
            <div style={styles.connectingContainer}>
              <div style={styles.connectingSpinner} />
              <div style={styles.connectingText}>   ...</div>
            </div>
          ) : users.length === 0 && !hasScreenShare ? (
            <div style={styles.emptyContainer}>
              <div style={styles.emptyIcon}><MicrophoneIcon size={64} color="var(--text-muted)" /></div>
              <div style={styles.emptyText}>    </div>
              <div style={styles.emptyHint}> !</div>
            </div>
          ) : (
            <div style={styles.usersContainer}>
              {gridRows.map((row, rowIndex) => (
                <div key={rowIndex} style={styles.usersRow}>
                  {row.map((item, itemIndex) => (
                    item.type === 'screenShare' ? (
                      // Screen Share Card
                      <div
                        key="screen-share"
                        ref={screenShareContainerRef}
                        style={isFullscreen ? styles.screenShareCardFullscreen : {
                          ...styles.screenShareCard,
                          width: cardSize.width,
                          height: cardSize.height,
                        }}
                        onMouseMove={handleFullscreenMouseMove}
                      >
                        <video
                          ref={screenShareVideoRef}
                          autoPlay
                          playsInline
                          style={{
                            ...(isFullscreen ? styles.screenShareVideoFullscreen : styles.screenShareVideo),
                            //      (  )
                            opacity: (!isLocalScreenShare && !isWatchingStream) ? 0 : 1,
                          }}
                          onClick={() => {
                            if (!isLocalScreenShare && !isAudioActivated) {
                              activateScreenShareAudio();
                            } else {
                              toggleFullscreen();
                            }
                          }}
                        />
                        {/*  " "  ,      */}
                        {!isLocalScreenShare && !isWatchingStream && (
                          <div
                            style={styles.watchStreamOverlay}
                            onClick={startWatchingStream}
                          >
                            <div style={styles.watchStreamContent}>
                              <ScreenShareActiveIcon size={48} color="#5865f2" />
                              <span style={styles.watchStreamTitle}>{screenShareState?.username}  </span>
                              <button
                                style={styles.watchStreamButton}
                                onClick={(e) => {
                                  e.stopPropagation();
                                  startWatchingStream();
                                }}
                              >
                                <PlayIcon size={18} color="#fff" />
                                <span> </span>
                              </button>
                            </div>
                          </div>
                        )}
                        {/*  "   " (  ) */}
                        {!isLocalScreenShare && isWatchingStream && !isAudioActivated && (
                          <div
                            style={styles.audioActivateOverlay}
                            onClick={activateScreenShareAudio}
                          >
                            <div style={styles.audioActivateContent}>
                              <SpeakerMutedIcon size={28} color="#fff" />
                              <span>   </span>
                            </div>
                          </div>
                        )}
                        {/*     */}
                        {videoStats && (
                          <div style={styles.qualityBadge}>
                            <span style={styles.qualityResolution}>
                              {videoStats.width}x{videoStats.height}
                            </span>
                            {videoStats.fps > 0 && (
                              <span style={styles.qualityFps}>
                                {videoStats.fps} FPS
                              </span>
                            )}
                          </div>
                        )}
                        <div style={styles.screenShareOverlay}>
                          <div style={styles.screenShareInfo}>
                            <ScreenShareActiveIcon size={16} color="#3ba55c" />
                            <span style={styles.screenShareUsername}>
                              {isLocalScreenShare ? '' : screenShareState?.username}
                            </span>
                          </div>
                          <div style={styles.screenShareControls}>
                            {/*   (  ,  ) */}
                            {!isLocalScreenShare && isWatchingStream && (
                              <div
                                style={styles.volumeControlWrapper}
                                onMouseEnter={() => setShowVolumeSlider(true)}
                                onMouseLeave={() => setShowVolumeSlider(false)}
                              >
                                <button
                                  style={styles.screenShareControlButton}
                                  onClick={() => {
                                    if (!isAudioActivated) {
                                      activateScreenShareAudio();
                                    } else {
                                      toggleScreenShareMute();
                                    }
                                  }}
                                  title={!isAudioActivated ? ' ' : screenShareVolume === 0 ? ' ' : ' '}
                                >
                                  <ScreenShareVolumeIcon size={16} />
                                </button>
                                {showVolumeSlider && isAudioActivated && (
                                  <div style={styles.volumeSliderPopup}>
                                    <input
                                      type="range"
                                      min="0"
                                      max="100"
                                      value={screenShareVolume}
                                      onChange={(e) => handleScreenShareVolumeChange(parseInt(e.target.value, 10))}
                                      style={styles.volumeSliderInput}
                                    />
                                    <span style={styles.volumeSliderValue}>{screenShareVolume}%</span>
                                    {/*     */}
                                    <div style={styles.volumeSliderBridge} />
                                  </div>
                                )}
                              </div>
                            )}
                            {/*    (     ) */}
                            {(isLocalScreenShare || isWatchingStream) && (
                              <button
                                style={styles.screenShareControlButton}
                                onClick={toggleFullscreen}
                                title=" "
                              >
                                <FullscreenIcon size={16} />
                              </button>
                            )}
                            {/*  " "   */}
                            {!isLocalScreenShare && isWatchingStream && (
                              <button
                                style={styles.screenShareStopButton}
                                onClick={() => {
                                  setIsWatchingStream(false);
                                  setIsAudioActivated(false);
                                  setScreenShareVolume(0);
                                  if (screenShareVideoRef.current) {
                                    screenShareVideoRef.current.muted = true;
                                    screenShareVideoRef.current.volume = 0;
                                  }
                                }}
                                title=" "
                              >
                                <ScreenShareStopIcon size={14} />
                              </button>
                            )}
                            {/*  " "   */}
                            {isLocalScreenShare && onStopScreenShare && (
                              <button
                                style={styles.screenShareStopButton}
                                onClick={onStopScreenShare}
                                title=""
                              >
                                <CloseIcon size={14} />
                              </button>
                            )}
                          </div>
                        </div>

                        {/*      */}
                        {isFullscreen && (
                          <div style={{
                            ...styles.fullscreenControlsBar,
                            opacity: showFullscreenControls ? 1 : 0,
                            pointerEvents: showFullscreenControls ? 'auto' : 'none',
                          }}>
                            <div style={styles.fullscreenControlsButtons}>
                              {/*  */}
                              <button
                                onClick={onToggleMute}
                                style={{
                                  ...styles.fullscreenControlButton,
                                  backgroundColor: isMuted ? 'rgba(240, 71, 71, 0.3)' : 'rgba(255, 255, 255, 0.1)',
                                  color: isMuted ? '#f04747' : '#fff',
                                }}
                                title={isMuted ? ' ' : ' '}
                              >
                                {isMuted ? <MicrophoneMutedIcon size={22} /> : <MicrophoneIcon size={22} />}
                              </button>

                              {/*  (Deafen) */}
                              {onToggleDeafen && (
                                <button
                                  onClick={onToggleDeafen}
                                  style={{
                                    ...styles.fullscreenControlButton,
                                    backgroundColor: isDeafened ? 'rgba(240, 71, 71, 0.3)' : 'rgba(255, 255, 255, 0.1)',
                                    color: isDeafened ? '#f04747' : '#fff',
                                  }}
                                  title={isDeafened ? ' ' : ' '}
                                >
                                  {isDeafened ? <DeafenedIcon size={22} /> : <HeadphonesIcon size={22} />}
                                </button>
                              )}

                              {/*   /    */}
                              <button
                                onClick={toggleFullscreen}
                                style={{
                                  ...styles.fullscreenControlButton,
                                  backgroundColor: 'rgba(255, 255, 255, 0.1)',
                                  color: '#fff',
                                }}
                                title="   "
                              >
                                <ExitFullscreenIcon size={22} />
                              </button>

                              {/*    */}
                              {!isLocalScreenShare && isWatchingStream && (
                                <button
                                  onClick={() => {
                                    setIsWatchingStream(false);
                                    setIsAudioActivated(false);
                                    setScreenShareVolume(0);
                                    if (screenShareVideoRef.current) {
                                      screenShareVideoRef.current.muted = true;
                                      screenShareVideoRef.current.volume = 0;
                                    }
                                    //    
                                    if (document.fullscreenElement) {
                                      document.exitFullscreen();
                                    }
                                  }}
                                  style={{
                                    ...styles.fullscreenControlButton,
                                    backgroundColor: 'rgba(240, 71, 71, 0.3)',
                                    color: '#f04747',
                                  }}
                                  title=" "
                                >
                                  <ScreenShareStopIcon size={22} />
                                </button>
                              )}
                            </div>
                          </div>
                        )}
                      </div>
                    ) : (
                      // User Card
                      <div
                        key={item.user.id}
                        style={{
                          ...styles.userCard,
                          width: cardSize.width,
                          height: cardSize.height,
                          cursor: item.user.id !== currentUserId ? 'pointer' : 'default',
                        }}
                        onClick={(e) => handleUserClick(item.user, e)}
                        onContextMenu={(e) => handleUserContextMenu(item.user, e)}
                      >
                        <div
                          style={{
                            ...styles.userAvatar,
                            width: Math.min(80, cardSize.height * 0.4),
                            height: Math.min(80, cardSize.height * 0.4),
                            backgroundColor: getAvatarColor(item.user.id || item.user.username),
                            boxShadow: item.user.isSpeaking
                              ? '0 0 0 4px #3ba55c, 0 0 20px rgba(59, 165, 92, 0.5)'
                              : 'none',
                          }}
                        >
                          <span style={{
                            ...styles.avatarText,
                            fontSize: Math.min(32, cardSize.height * 0.15),
                          }}>
                            {item.user.username[0].toUpperCase()}
                          </span>
                          {item.user.isMuted && (
                            <div style={styles.mutedBadge}>
                              <MicrophoneMutedIcon size={14} color="#f04747" />
                            </div>
                          )}
                          {item.user.isDeafened && (
                            <div style={{
                              ...styles.deafenedBadge,
                              left: item.user.isMuted ? '50%' : '-4px',
                              transform: item.user.isMuted ? 'translateX(-50%)' : 'none',
                            }}>
                              <DeafenedIcon size={14} color="#f04747" />
                            </div>
                          )}
                          {item.user.id !== currentUserId && item.user.volume !== undefined && item.user.volume < 100 && (
                            <div style={styles.volumeBadge}>
                              {item.user.volume === 0 ? <SpeakerMutedIcon size={14} /> : <SpeakerLowIcon size={14} />}
                            </div>
                          )}
                          {item.user.isScreenSharing && (
                            <div style={styles.screenShareBadge}>
                              <ScreenShareActiveIcon size={14} color="#3ba55c" />
                            </div>
                          )}
                        </div>
                        <div
                          style={{
                            ...styles.userName,
                            color: item.user.isSpeaking ? '#fff' : '#b9bbbe',
                          }}
                        >
                          {item.user.username}
                        </div>
                      </div>
                    )
                  ))}
                </div>
              ))}
            </div>
          )}
        </div>

        {/*    */}
        {selectedUser && popupPosition && (
          <div
            ref={popupRef}
            style={{
              ...styles.volumePopup,
              left: popupPosition.x,
              top: popupPosition.y,
              transform: 'translateX(-50%)',
            }}
          >
            <div style={styles.volumePopupHeader}>
              <span style={styles.volumePopupIcon}><SpeakerIcon size={16} /></span>
              <span style={styles.volumePopupTitle}>{selectedUser.username}</span>
            </div>
            <div style={styles.volumeSliderContainer}>
              <span style={styles.volumeLabel}></span>
              <div style={styles.volumeSliderRow}>
                <input
                  type="range"
                  min="0"
                  max="100"
                  value={selectedUser.volume ?? 100}
                  onChange={(e) => handleVolumeChange(selectedUser.id, parseInt(e.target.value))}
                  style={styles.volumeSlider}
                />
                <span style={styles.volumeValue}>{selectedUser.volume ?? 100}%</span>
              </div>
            </div>
          </div>
        )}
      </div>

      {/*    */}
      <div style={styles.controlsBar}>
        <div style={styles.controlsButtons}>
          {/*  */}
          <button
            onClick={onToggleMute}
            style={{
              ...styles.controlButton,
              backgroundColor: isMuted ? 'rgba(240, 71, 71, 0.3)' : 'rgba(255, 255, 255, 0.1)',
              color: isMuted ? '#f04747' : '#fff',
            }}
            title={isMuted ? ' ' : ' '}
          >
            {isMuted ? <MicrophoneMutedIcon size={20} /> : <MicrophoneIcon size={20} />}
          </button>

          {/*  (Deafen) */}
          {onToggleDeafen && (
            <button
              onClick={onToggleDeafen}
              style={{
                ...styles.controlButton,
                backgroundColor: isDeafened ? 'rgba(240, 71, 71, 0.3)' : 'rgba(255, 255, 255, 0.1)',
                color: isDeafened ? '#f04747' : '#fff',
              }}
              title={isDeafened ? ' ' : ' '}
            >
              {isDeafened ? <DeafenedIcon size={20} /> : <HeadphonesIcon size={20} />}
            </button>
          )}

          {/*   */}
          <button
            onClick={onScreenShareClick}
            style={{
              ...styles.controlButton,
              backgroundColor: isScreenSharing ? 'rgba(240, 71, 71, 0.3)' : 'rgba(255, 255, 255, 0.1)',
              color: isScreenSharing ? '#f04747' : '#fff',
            }}
            title={isScreenSharing ? ' ' : ' '}
          >
            {isScreenSharing ? <ScreenShareStopIcon size={20} /> : <ScreenShareIcon size={20} />}
          </button>

          {/*    (    ) */}
          {!isLocalScreenShare && isWatchingStream && (
            <button
              onClick={() => {
                setIsWatchingStream(false);
                setIsAudioActivated(false);
                setScreenShareVolume(0);
                if (screenShareVideoRef.current) {
                  screenShareVideoRef.current.muted = true;
                  screenShareVideoRef.current.volume = 0;
                }
              }}
              style={{
                ...styles.controlButton,
                backgroundColor: 'rgba(240, 71, 71, 0.3)',
                color: '#f04747',
              }}
              title=" "
            >
              <ScreenShareStopIcon size={20} />
            </button>
          )}

          {/*  */}
          <button
            onClick={onDisconnect}
            style={styles.disconnectButton}
            title=""
          >
            <DisconnectIcon size={20} />
          </button>
        </div>
      </div>
    </div>
  );
};

