import React, { useState, useEffect, useRef } from 'react';

interface MicrophoneTestProps {
  onNoiseSuppressionChange: (enabled: boolean) => void;
  noiseSuppression: boolean;
  onSensitivityChange: (settings: SensitivitySettings) => void;
  sensitivitySettings: SensitivitySettings;
  onNoiseGateChange?: (enabled: boolean) => void;
  noiseGate?: boolean;
}

export interface SensitivitySettings {
  mode: 'auto' | 'manual';
  threshold: number; // 0-100
}

const MicrophoneTest: React.FC<MicrophoneTestProps> = ({
  onNoiseSuppressionChange,
  noiseSuppression,
  onSensitivityChange,
  sensitivitySettings,
  onNoiseGateChange,
  noiseGate = true,
}) => {
  const [isTestActive, setIsTestActive] = useState(false);
  const [micLevel, setMicLevel] = useState(0);
  const [devices, setDevices] = useState<MediaDeviceInfo[]>([]);
  const [selectedDevice, setSelectedDevice] = useState<string>('');
  const [error, setError] = useState<string | null>(null);
  const [isAboveThreshold, setIsAboveThreshold] = useState(false);
  const [isLoopbackEnabled, setIsLoopbackEnabled] = useState(false);

  const streamRef = useRef<MediaStream | null>(null);
  const audioContextRef = useRef<AudioContext | null>(null);
  const analyserRef = useRef<AnalyserNode | null>(null);
  const animationRef = useRef<number | null>(null);
  const loopbackAudioRef = useRef<HTMLAudioElement | null>(null);
  const loopbackGainRef = useRef<GainNode | null>(null);
  const loopbackContextRef = useRef<AudioContext | null>(null);
  const gateCloseTimeoutRef = useRef<number | null>(null);
  const noiseFloorRef = useRef<number>(10);
  const noiseFloorSamplesRef = useRef<number[]>([]);

  useEffect(() => {
    loadDevices();
    return () => {
      stopTest();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const loadDevices = async () => {
    try {
      await navigator.mediaDevices.getUserMedia({ audio: true });
      const deviceList = await navigator.mediaDevices.enumerateDevices();
      const audioInputs = deviceList.filter(d => d.kind === 'audioinput');
      setDevices(audioInputs);
      if (audioInputs.length > 0 && !selectedDevice) {
        setSelectedDevice(audioInputs[0].deviceId);
      }
    } catch (err) {
      setError('     ');
    }
  };

  const startTest = async () => {
    setError(null);
    try {
      //        webrtc.ts
      const constraints: MediaStreamConstraints = {
        audio: {
          deviceId: selectedDevice ? { exact: selectedDevice } : undefined,
          echoCancellation: { ideal: true },
          noiseSuppression: { ideal: noiseSuppression },
          autoGainControl: { ideal: true },
          // @ts-ignore - Chrome experimental constraints
          googEchoCancellation: { ideal: true },
          // @ts-ignore
          googAutoGainControl: { ideal: true },
          // @ts-ignore
          googNoiseSuppression: { ideal: true },
          // @ts-ignore
          googHighpassFilter: { ideal: true },
          // @ts-ignore
          googTypingNoiseDetection: { ideal: true },
        } as MediaTrackConstraints,
      };

      streamRef.current = await navigator.mediaDevices.getUserMedia(constraints);
      audioContextRef.current = new AudioContext();
      const source = audioContextRef.current.createMediaStreamSource(streamRef.current);

      analyserRef.current = audioContextRef.current.createAnalyser();
      analyserRef.current.fftSize = 1024; //   webrtc.ts    
      analyserRef.current.smoothingTimeConstant = 0.3;

      if (noiseSuppression) {
        const highpass = audioContextRef.current.createBiquadFilter();
        highpass.type = 'highpass';
        highpass.frequency.value = 80;

        const lowpass = audioContextRef.current.createBiquadFilter();
        lowpass.type = 'lowpass';
        lowpass.frequency.value = 12000;

        const compressor = audioContextRef.current.createDynamicsCompressor();
        compressor.threshold.value = -50;
        compressor.knee.value = 40;
        compressor.ratio.value = 12;
        compressor.attack.value = 0;
        compressor.release.value = 0.25;

        source.connect(highpass);
        highpass.connect(lowpass);
        lowpass.connect(compressor);
        compressor.connect(analyserRef.current);
      } else {
        source.connect(analyserRef.current);
      }

      setIsTestActive(true);
      updateLevel();

      //    
      enableLoopback();
      setIsLoopbackEnabled(true);
    } catch (err: unknown) {
      if (err instanceof Error && err.name === 'NotAllowedError') {
        setError('   ');
      } else {
        setError('    ');
      }
    }
  };

  const stopTest = () => {
    if (animationRef.current) {
      cancelAnimationFrame(animationRef.current);
      animationRef.current = null;
    }

    // Stop loopback (Web Audio API  audio )
    disableLoopback();

    //  noise floor
    noiseFloorRef.current = 10;
    noiseFloorSamplesRef.current = [];

    if (streamRef.current) {
      streamRef.current.getTracks().forEach(track => track.stop());
      streamRef.current = null;
    }

    if (audioContextRef.current) {
      audioContextRef.current.close();
      audioContextRef.current = null;
    }

    analyserRef.current = null;
    setIsTestActive(false);
    setMicLevel(0);
    setIsAboveThreshold(false);
  };

  const enableLoopback = () => {
    if (!streamRef.current) return;

    try {
      //  Web Audio API  loopback  noise gate
      loopbackContextRef.current = new AudioContext();
      const source = loopbackContextRef.current.createMediaStreamSource(streamRef.current);

      // GainNode  noise gate
      loopbackGainRef.current = loopbackContextRef.current.createGain();
      //    gate  noise gate 
      loopbackGainRef.current.gain.value = noiseGate ? 0 : 0.8;

      // : source -> gain -> destination
      source.connect(loopbackGainRef.current);
      loopbackGainRef.current.connect(loopbackContextRef.current.destination);

      //   noise floor
      noiseFloorRef.current = 10;
      noiseFloorSamplesRef.current = [];
    } catch (err) {
      console.error('Failed to setup loopback:', err);
      // Fallback   audio 
      const audio = document.createElement('audio');
      audio.srcObject = streamRef.current;
      audio.autoplay = true;
      audio.volume = 0.8;
      loopbackAudioRef.current = audio;
      audio.play().catch(e => console.error('Failed to play audio:', e));
    }
  };

  const disableLoopback = () => {
    //  timeout
    if (gateCloseTimeoutRef.current) {
      clearTimeout(gateCloseTimeoutRef.current);
      gateCloseTimeoutRef.current = null;
    }

    //  Web Audio loopback
    if (loopbackContextRef.current) {
      loopbackContextRef.current.close();
      loopbackContextRef.current = null;
      loopbackGainRef.current = null;
    }

    //  fallback audio 
    if (loopbackAudioRef.current) {
      loopbackAudioRef.current.pause();
      loopbackAudioRef.current.srcObject = null;
      loopbackAudioRef.current = null;
    }
  };

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const toggleLoopback = () => {
    if (isLoopbackEnabled) {
      disableLoopback();
      setIsLoopbackEnabled(false);
    } else {
      if (isTestActive) {
        enableLoopback();
      }
      setIsLoopbackEnabled(true);
    }
  };

  const updateLevel = () => {
    if (!analyserRef.current) return;

    const dataArray = new Uint8Array(analyserRef.current.frequencyBinCount);
    analyserRef.current.getByteFrequencyData(dataArray);

    //    (85-3000 Hz)   webrtc.ts
    const sampleRate = audioContextRef.current?.sampleRate || 48000;
    const fftSize = analyserRef.current.fftSize;
    const binSize = sampleRate / fftSize;
    const voiceStartBin = Math.floor(85 / binSize);
    const voiceEndBin = Math.floor(3000 / binSize);

    let voiceSum = 0;
    let voiceCount = 0;
    for (let i = voiceStartBin; i <= voiceEndBin && i < dataArray.length; i++) {
      voiceSum += dataArray[i];
      voiceCount++;
    }
    const voiceAverage = voiceCount > 0 ? voiceSum / voiceCount : 0;
    const level = Math.min(100, (voiceAverage / 128) * 100);
    setMicLevel(level);

    //  noise floor
    const NOISE_FLOOR_SAMPLES = 50;
    if (level < noiseFloorRef.current + 10 && noiseFloorSamplesRef.current.length < NOISE_FLOOR_SAMPLES) {
      noiseFloorSamplesRef.current.push(level);
      if (noiseFloorSamplesRef.current.length === NOISE_FLOOR_SAMPLES) {
        noiseFloorRef.current = Math.max(5,
          noiseFloorSamplesRef.current.reduce((a, b) => a + b, 0) / noiseFloorSamplesRef.current.length
        );
      }
    }

    //  
    const threshold = sensitivitySettings.mode === 'auto'
      ? noiseFloorRef.current + 15
      : sensitivitySettings.threshold;
    const isSpeaking = level > threshold;
    setIsAboveThreshold(isSpeaking);

    //  noise gate  loopback (  )
    if (noiseGate && loopbackGainRef.current) {
      if (isSpeaking) {
        //   gate
        if (gateCloseTimeoutRef.current) {
          clearTimeout(gateCloseTimeoutRef.current);
          gateCloseTimeoutRef.current = null;
        }
        //  gate 
        loopbackGainRef.current.gain.value = 0.8;
      } else if (loopbackGainRef.current.gain.value > 0 && !gateCloseTimeoutRef.current) {
        //  gate  
        gateCloseTimeoutRef.current = window.setTimeout(() => {
          if (loopbackGainRef.current) {
            loopbackGainRef.current.gain.value = 0;
          }
          gateCloseTimeoutRef.current = null;
        }, 250);
      }
    } else if (!noiseGate && loopbackGainRef.current) {
      // Noise gate  -  
      loopbackGainRef.current.gain.value = 0.8;
    }

    animationRef.current = requestAnimationFrame(updateLevel);
  };

  const handleDeviceChange = (deviceId: string) => {
    setSelectedDevice(deviceId);
    if (isTestActive) {
      stopTest();
      setTimeout(() => startTest(), 100);
    }
  };

  const handleModeChange = (mode: 'auto' | 'manual') => {
    onSensitivityChange({ ...sensitivitySettings, mode });
  };

  const handleThresholdChange = (threshold: number) => {
    onSensitivityChange({ ...sensitivitySettings, threshold });
  };

  const getLevelColor = () => {
    if (sensitivitySettings.mode === 'manual') {
      return isAboveThreshold ? '#3ba55c' : '#72767d';
    }
    if (micLevel < 30) return '#3ba55c';
    if (micLevel < 70) return '#faa61a';
    return '#f04747';
  };

  const getThresholdPosition = () => {
    if (sensitivitySettings.mode === 'auto') return 25;
    return sensitivitySettings.threshold;
  };

  return (
    <div style={styles.container}>
      <div style={styles.deviceSection}>
        <label style={styles.label}> </label>
        <select
          value={selectedDevice}
          onChange={(e) => handleDeviceChange(e.target.value)}
          style={styles.select}
        >
          {devices.map((device) => (
            <option key={device.deviceId} value={device.deviceId}>
              {device.label || ` ${devices.indexOf(device) + 1}`}
            </option>
          ))}
        </select>
      </div>

      <div style={styles.noiseSection}>
        <label style={styles.toggleLabel}>
          <input
            type="checkbox"
            checked={noiseSuppression}
            onChange={(e) => onNoiseSuppressionChange(e.target.checked)}
            style={styles.checkbox}
          />
          <span style={styles.toggleText}></span>
        </label>
        <span style={styles.toggleHint}>
             ( + Web Audio )
        </span>
      </div>

      <div style={styles.noiseSection}>
        <label style={styles.toggleLabel}>
          <input
            type="checkbox"
            checked={noiseGate}
            onChange={(e) => onNoiseGateChange?.(e.target.checked)}
            style={styles.checkbox}
          />
          <span style={styles.toggleText}>Noise Gate ( )</span>
        </label>
        <span style={styles.toggleHint}>
                  , ,  
        </span>
      </div>

      <div style={styles.sensitivitySection}>
        <label style={styles.label}> </label>

        <div style={styles.modeButtons}>
          <button
            onClick={() => handleModeChange('auto')}
            style={{
              ...styles.modeButton,
              backgroundColor: sensitivitySettings.mode === 'auto' ? '#5865f2' : '#202225',
            }}
          >
            
          </button>
          <button
            onClick={() => handleModeChange('manual')}
            style={{
              ...styles.modeButton,
              backgroundColor: sensitivitySettings.mode === 'manual' ? '#5865f2' : '#202225',
            }}
          >
            
          </button>
        </div>

        {sensitivitySettings.mode === 'auto' ? (
          <span style={styles.modeHint}>
               
          </span>
        ) : (
          <div style={styles.thresholdSection}>
            <div style={styles.thresholdSliderContainer}>
              <input
                type="range"
                min="0"
                max="100"
                value={sensitivitySettings.threshold}
                onChange={(e) => handleThresholdChange(Number(e.target.value))}
                style={styles.thresholdSlider}
              />
              <div style={styles.thresholdLabels}>
                <span></span>
                <span></span>
              </div>
            </div>
            <span style={styles.thresholdValue}>{sensitivitySettings.threshold}%</span>
          </div>
        )}
      </div>

      <div style={styles.testSection}>
        <div style={styles.testHeader}>
          <span style={styles.label}> </span>
          <button
            onClick={isTestActive ? stopTest : startTest}
            style={{
              ...styles.testButton,
              backgroundColor: isTestActive ? '#f04747' : '#3ba55c',
            }}
          >
            {isTestActive ? '' : ''}
          </button>
        </div>

        {error && <div style={styles.error}>{error}</div>}

        <div style={styles.levelContainer}>
          <div style={styles.levelBackground}>
            <div
              style={{
                ...styles.levelBar,
                width: `${micLevel}%`,
                backgroundColor: getLevelColor(),
              }}
            />
            {/*   */}
            <div
              style={{
                ...styles.thresholdIndicator,
                left: `${getThresholdPosition()}%`,
              }}
            />
          </div>
          <span style={styles.levelText}>{Math.round(micLevel)}%</span>
        </div>

        {isTestActive && (
          <div style={styles.statusContainer}>
            <div
              style={{
                ...styles.statusIndicator,
                backgroundColor: isAboveThreshold ? '#3ba55c' : '#72767d',
              }}
            />
            <span style={styles.statusText}>
              {isAboveThreshold ? ' ' : '  '}
            </span>
          </div>
        )}

        {isTestActive && (
          <div style={styles.hint}>
              .     .
          </div>
        )}
      </div>
    </div>
  );
};

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column' as const,
    gap: '16px',
  },
  deviceSection: {
    display: 'flex',
    flexDirection: 'column' as const,
    gap: '8px',
  },
  label: {
    color: '#b9bbbe',
    fontSize: '12px',
    fontWeight: 'bold' as const,
    textTransform: 'uppercase' as const,
  },
  select: {
    padding: '10px',
    backgroundColor: '#202225',
    border: '1px solid #202225',
    borderRadius: '4px',
    color: '#fff',
    fontSize: '14px',
    cursor: 'pointer',
  },
  noiseSection: {
    display: 'flex',
    flexDirection: 'column' as const,
    gap: '4px',
    padding: '12px',
    backgroundColor: '#202225',
    borderRadius: '4px',
  },
  toggleLabel: {
    display: 'flex',
    alignItems: 'center',
    gap: '10px',
    cursor: 'pointer',
  },
  checkbox: {
    width: '18px',
    height: '18px',
    cursor: 'pointer',
    accentColor: '#5865f2',
  },
  toggleText: {
    color: '#fff',
    fontSize: '14px',
  },
  toggleHint: {
    color: '#72767d',
    fontSize: '12px',
    marginLeft: '28px',
  },
  sensitivitySection: {
    display: 'flex',
    flexDirection: 'column' as const,
    gap: '12px',
  },
  modeButtons: {
    display: 'flex',
    gap: '8px',
  },
  modeButton: {
    flex: 1,
    padding: '10px',
    border: 'none',
    borderRadius: '4px',
    color: '#fff',
    fontSize: '13px',
    cursor: 'pointer',
    fontWeight: 500,
    transition: 'background-color 0.2s',
  },
  modeHint: {
    color: '#72767d',
    fontSize: '12px',
  },
  thresholdSection: {
    display: 'flex',
    alignItems: 'center',
    gap: '12px',
  },
  thresholdSliderContainer: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column' as const,
    gap: '4px',
  },
  thresholdSlider: {
    width: '100%',
    height: '6px',
    cursor: 'pointer',
    accentColor: '#5865f2',
  },
  thresholdLabels: {
    display: 'flex',
    justifyContent: 'space-between',
    color: '#72767d',
    fontSize: '11px',
  },
  thresholdValue: {
    color: '#fff',
    fontSize: '14px',
    fontWeight: 'bold' as const,
    minWidth: '40px',
    textAlign: 'right' as const,
  },
  testSection: {
    display: 'flex',
    flexDirection: 'column' as const,
    gap: '12px',
  },
  testHeader: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  testButton: {
    padding: '8px 16px',
    border: 'none',
    borderRadius: '4px',
    color: '#fff',
    fontSize: '14px',
    cursor: 'pointer',
    fontWeight: 'bold' as const,
  },
  error: {
    padding: '8px 12px',
    backgroundColor: '#f04747',
    borderRadius: '4px',
    color: '#fff',
    fontSize: '13px',
  },
  levelContainer: {
    display: 'flex',
    alignItems: 'center',
    gap: '12px',
  },
  levelBackground: {
    flex: 1,
    height: '8px',
    backgroundColor: '#202225',
    borderRadius: '4px',
    overflow: 'visible',
    position: 'relative' as const,
  },
  levelBar: {
    height: '100%',
    borderRadius: '4px',
    transition: 'width 0.05s ease-out',
  },
  thresholdIndicator: {
    position: 'absolute' as const,
    top: '-4px',
    width: '2px',
    height: '16px',
    backgroundColor: '#faa61a',
    transform: 'translateX(-50%)',
    borderRadius: '1px',
  },
  levelText: {
    color: '#b9bbbe',
    fontSize: '12px',
    minWidth: '35px',
    textAlign: 'right' as const,
  },
  statusContainer: {
    display: 'flex',
    alignItems: 'center',
    gap: '8px',
    justifyContent: 'center',
  },
  statusIndicator: {
    width: '10px',
    height: '10px',
    borderRadius: '50%',
    transition: 'background-color 0.2s',
  },
  statusText: {
    color: '#b9bbbe',
    fontSize: '13px',
  },
  hint: {
    color: '#72767d',
    fontSize: '12px',
    textAlign: 'center' as const,
  },
  loopbackSection: {
    display: 'flex',
    flexDirection: 'column' as const,
    alignItems: 'center',
    gap: '8px',
    padding: '12px',
    backgroundColor: '#202225',
    borderRadius: '4px',
  },
  loopbackButton: {
    padding: '10px 20px',
    border: 'none',
    borderRadius: '4px',
    color: '#fff',
    fontSize: '14px',
    fontWeight: 500,
    cursor: 'pointer',
    transition: 'background-color 0.2s',
  },
  loopbackHint: {
    color: '#72767d',
    fontSize: '12px',
    textAlign: 'center' as const,
  },
};

export default MicrophoneTest;
