import axios from 'axios';
import { AuthResponse, Server, Channel, Message, ReactionSummary, AdminStats, SystemStats } from '../types';

// Use relative URL - works on any domain since nginx proxies /api to backend
const API_URL = process.env.REACT_APP_API_URL || '/api';

const api = axios.create({
  baseURL: API_URL,
});

//     
api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Auth
export const register = async (username: string, email: string, password: string): Promise<AuthResponse> => {
  const response = await api.post('/auth/register', { username, email, password });
  return response.data;
};

export const login = async (email: string, password: string): Promise<AuthResponse> => {
  const response = await api.post('/auth/login', { email, password });
  return response.data;
};

export const getCurrentUser = async () => {
  const response = await api.get('/users/me');
  return response.data;
};

export const updateProfile = async (data: { username?: string; avatar_url?: string | null }): Promise<{ id: string; username: string; email: string; avatar_url?: string }> => {
  const response = await api.put('/users/me', data);
  return response.data;
};

// Servers
export const createServer = async (name: string): Promise<Server> => {
  const response = await api.post('/servers', { name });
  return response.data;
};

export const getUserServers = async (): Promise<Server[]> => {
  const response = await api.get('/servers');
  return response.data;
};

export const getServer = async (serverId: string): Promise<Server> => {
  const response = await api.get(`/servers/${serverId}`);
  return response.data;
};

export const getServerChannels = async (serverId: string): Promise<Channel[]> => {
  const response = await api.get(`/servers/${serverId}/channels`);
  return response.data;
};

export const createChannel = async (serverId: string, name: string, type: 'text' | 'voice'): Promise<Channel> => {
  const response = await api.post(`/servers/${serverId}/channels`, { name, type });
  return response.data;
};

export const deleteChannel = async (serverId: string, channelId: string): Promise<void> => {
  await api.delete(`/servers/${serverId}/channels/${channelId}`);
};

// Messages
export const getMessages = async (channelId: string): Promise<Message[]> => {
  const response = await api.get(`/channels/${channelId}/messages`);
  return response.data;
};

export const sendMessage = async (channelId: string, content: string, replyToId?: string): Promise<Message> => {
  const body: { content: string; reply_to_id?: string } = { content };
  if (replyToId) {
    body.reply_to_id = replyToId;
  }
  const response = await api.post(`/channels/${channelId}/messages`, body);
  return response.data;
};

export const editMessage = async (channelId: string, messageId: string, content: string): Promise<Message> => {
  const response = await api.put(`/channels/${channelId}/messages/${messageId}`, { content });
  return response.data;
};

export const deleteMessage = async (channelId: string, messageId: string): Promise<void> => {
  await api.delete(`/channels/${channelId}/messages/${messageId}`);
};

// Reactions
export const addReaction = async (channelId: string, messageId: string, emoji: string): Promise<{ reactions: ReactionSummary[] }> => {
  const response = await api.post(`/channels/${channelId}/messages/${messageId}/reactions`, { emoji });
  return response.data;
};

export const removeReaction = async (channelId: string, messageId: string, emoji: string): Promise<{ reactions: ReactionSummary[] }> => {
  const response = await api.delete(`/channels/${channelId}/messages/${messageId}/reactions/${encodeURIComponent(emoji)}`);
  return response.data;
};

// Invites
export interface Invite {
  id: string;
  code: string;
  server_id: string;
  uses: number;
  max_uses: number;
  expires_at?: string;
  created_at: string;
  server?: Server;
}

export const createInvite = async (serverId: string): Promise<Invite> => {
  const response = await api.post(`/servers/${serverId}/invites`, {});
  return response.data;
};

export const getInvite = async (code: string): Promise<Invite> => {
  const response = await api.get(`/invites/${code}`);
  return response.data;
};

export const joinServer = async (code: string): Promise<{ message: string; server: Server }> => {
  const response = await api.post(`/invites/${code}/join`);
  return response.data;
};

// Voice Participants
export interface VoiceParticipant {
  id: string;
  username: string;
  isMuted: boolean;
  isDeafened?: boolean;
  isScreenSharing?: boolean;
}

export const getVoiceParticipants = async (serverId: string): Promise<Record<string, VoiceParticipant[]>> => {
  const response = await api.get(`/servers/${serverId}/voice-participants`);
  return response.data || {};
};

// Server Members
export interface ServerMember {
  id: string;
  username: string;
  avatar_url?: string | null;
  role: string;
}

export const getServerMembers = async (serverId: string): Promise<ServerMember[]> => {
  const response = await api.get(`/servers/${serverId}/members`);
  return response.data || [];
};

// Online Users (Presence)
export interface OnlineUser {
  id: string;
  username: string;
}

export const getGlobalPresenceWebSocketUrl = (): string => {
  const token = localStorage.getItem('token');
  const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
  const host = window.location.host;
  return `${protocol}//${host}/api/presence?token=${token}`;
};

export const getOnlineUsers = async (serverId: string): Promise<OnlineUser[]> => {
  const response = await api.get(`/servers/${serverId}/online`);
  return response.data || [];
};

// Admin API

export const adminLogin = async (login: string, password: string): Promise<AuthResponse> => {
  const response = await api.post('/admin/login', { login, password });
  return response.data;
};

export const checkAdminStatus = async (): Promise<{ is_admin: boolean }> => {
  const response = await api.get('/admin/check');
  return response.data;
};

export const getAdminStats = async (): Promise<AdminStats> => {
  const response = await api.get('/admin/stats');
  return response.data;
};

export const getSystemStats = async (): Promise<SystemStats> => {
  const response = await api.get('/admin/system');
  return response.data;
};

// Server Admin API

export interface ServerBan {
  id: string;
  server_id: string;
  user_id: string;
  banned_by: string;
  reason?: string;
  created_at: string;
  user?: { id: string; username: string; avatar_url?: string };
  banned_by_user?: { id: string; username: string; avatar_url?: string };
}

export interface ServerLog {
  id: string;
  server_id: string;
  action_type: string;
  actor_id: string;
  target_user_id?: string;
  target_channel_id?: string;
  details?: Record<string, unknown>;
  created_at: string;
  actor?: { id: string; username: string; avatar_url?: string };
  target_user?: { id: string; username: string; avatar_url?: string };
}

export const checkServerOwnership = async (serverId: string): Promise<{ is_owner: boolean }> => {
  const response = await api.get(`/servers/${serverId}/admin/check`);
  return response.data;
};

export const getServerSettings = async (serverId: string): Promise<Server> => {
  const response = await api.get(`/servers/${serverId}/admin/settings`);
  return response.data;
};

export const updateServerSettings = async (serverId: string, name: string): Promise<void> => {
  await api.put(`/servers/${serverId}/admin/settings`, { name });
};

export const deleteServer = async (serverId: string, confirmName: string): Promise<void> => {
  await api.delete(`/servers/${serverId}/admin`, { data: { confirm_name: confirmName } });
};

export const getServerBans = async (serverId: string): Promise<ServerBan[]> => {
  const response = await api.get(`/servers/${serverId}/admin/bans`);
  return response.data;
};

export const banUser = async (serverId: string, userId: string, reason?: string): Promise<void> => {
  await api.post(`/servers/${serverId}/admin/bans`, { user_id: userId, reason });
};

export const unbanUser = async (serverId: string, userId: string): Promise<void> => {
  await api.delete(`/servers/${serverId}/admin/bans`, { data: { user_id: userId } });
};

export const kickUser = async (serverId: string, userId: string, reason?: string): Promise<void> => {
  await api.post(`/servers/${serverId}/admin/kick`, { user_id: userId, reason });
};

export const getServerLogs = async (serverId: string): Promise<ServerLog[]> => {
  const response = await api.get(`/servers/${serverId}/admin/logs`);
  return response.data;
};

export const updateChannel = async (serverId: string, channelId: string, name: string): Promise<void> => {
  await api.put(`/servers/${serverId}/admin/channels/${channelId}`, { name });
};

export const deleteChannelAdmin = async (serverId: string, channelId: string): Promise<void> => {
  await api.delete(`/servers/${serverId}/admin/channels/${channelId}`);
};

export default api;
