/**
 * Centralized Presence Service
 * Single source of truth for all online/offline status across the app
 */

export type PresenceStatus = 'online' | 'offline' | 'idle' | 'dnd';

export interface PresenceUser {
  id: string;
  username: string;
  status: PresenceStatus;
  lastSeen?: number;
}

export type PresenceEventType =
  | 'connected'
  | 'disconnected'
  | 'user_online'
  | 'user_offline'
  | 'presence_update'
  | 'error';

export interface PresenceEvent {
  type: PresenceEventType;
  userId?: string;
  serverId?: string;
  users?: string[];
}

type PresenceListener = (event: PresenceEvent) => void;

class PresenceService {
  private static instance: PresenceService;

  private ws: WebSocket | null = null;
  private isConnected: boolean = false;
  private reconnectAttempts: number = 0;
  private maxReconnectAttempts: number = 10;
  private reconnectTimeout: number | null = null;
  private pingInterval: number | null = null;

  // Central store of online users
  private onlineUsers: Set<string> = new Set();
  private onlineUsersByServer: Map<string, Set<string>> = new Map();

  // Event listeners
  private listeners: Set<PresenceListener> = new Set();

  // Current subscription
  private currentServerId: string | null = null;

  private constructor() {}

  static getInstance(): PresenceService {
    if (!PresenceService.instance) {
      PresenceService.instance = new PresenceService();
    }
    return PresenceService.instance;
  }

  /**
   * Connect to presence WebSocket
   */
  connect(): void {
    if (this.ws && (this.ws.readyState === WebSocket.OPEN || this.ws.readyState === WebSocket.CONNECTING)) {
      console.log('[Presence] Already connected or connecting');
      return;
    }

    const token = localStorage.getItem('token');
    if (!token) {
      console.log('[Presence] No token, skipping connection');
      return;
    }

    const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
    const host = window.location.host;
    const wsUrl = `${protocol}//${host}/api/presence?token=${token}`;

    console.log('[Presence] Connecting...');

    try {
      this.ws = new WebSocket(wsUrl);

      this.ws.onopen = () => {
        console.log('[Presence] Connected');
        this.isConnected = true;
        this.reconnectAttempts = 0;
        this.startPingInterval();
        this.emit({ type: 'connected' });

        // Re-subscribe to current server if any
        if (this.currentServerId) {
          this.subscribeToServer(this.currentServerId);
        }
      };

      this.ws.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);
          this.handleMessage(data);
        } catch (err) {
          console.error('[Presence] Failed to parse message:', err);
        }
      };

      this.ws.onerror = (error) => {
        console.error('[Presence] WebSocket error:', error);
        this.emit({ type: 'error' });
      };

      this.ws.onclose = () => {
        console.log('[Presence] Disconnected');
        this.isConnected = false;
        this.stopPingInterval();
        this.emit({ type: 'disconnected' });

        // Attempt reconnect
        this.scheduleReconnect();
      };
    } catch (error) {
      console.error('[Presence] Failed to create WebSocket:', error);
      this.scheduleReconnect();
    }
  }

  /**
   * Disconnect from presence WebSocket
   */
  disconnect(): void {
    console.log('[Presence] Disconnecting...');
    this.reconnectAttempts = this.maxReconnectAttempts; // Prevent auto-reconnect

    if (this.reconnectTimeout) {
      clearTimeout(this.reconnectTimeout);
      this.reconnectTimeout = null;
    }

    this.stopPingInterval();

    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }

    this.isConnected = false;
    this.onlineUsers.clear();
    this.onlineUsersByServer.clear();
  }

  private scheduleReconnect(): void {
    if (this.reconnectAttempts >= this.maxReconnectAttempts) {
      console.log('[Presence] Max reconnect attempts reached');
      return;
    }

    this.reconnectAttempts++;
    const delay = Math.min(1000 * Math.pow(2, this.reconnectAttempts - 1), 30000);

    console.log(`[Presence] Reconnecting in ${delay}ms (attempt ${this.reconnectAttempts}/${this.maxReconnectAttempts})`);

    this.reconnectTimeout = window.setTimeout(() => {
      this.connect();
    }, delay);
  }

  private startPingInterval(): void {
    this.stopPingInterval();
    this.pingInterval = window.setInterval(() => {
      if (this.ws && this.ws.readyState === WebSocket.OPEN) {
        this.ws.send(JSON.stringify({ type: 'ping' }));
      }
    }, 30000);
  }

  private stopPingInterval(): void {
    if (this.pingInterval) {
      clearInterval(this.pingInterval);
      this.pingInterval = null;
    }
  }

  private handleMessage(data: any): void {
    const { type, server, users } = data;

    if (type === 'presence_update' && server && Array.isArray(users)) {
      // Update server-specific online users
      const userIds = users.map((u: any) => u.id || u);
      const userSet = new Set<string>(userIds);

      // Track previous state to detect changes
      const previousSet = this.onlineUsersByServer.get(server) || new Set();

      // Update store
      this.onlineUsersByServer.set(server, userSet);

      // Update global online users set
      this.updateGlobalOnlineUsers();

      // Detect who came online/offline
      userIds.forEach(userId => {
        if (!previousSet.has(userId)) {
          this.emit({ type: 'user_online', userId, serverId: server });
        }
      });
      Array.from(previousSet).forEach(userId => {
        if (!userSet.has(userId)) {
          this.emit({ type: 'user_offline', userId, serverId: server });
        }
      });

      // Emit general update
      this.emit({ type: 'presence_update', serverId: server, users: userIds });
    }
  }

  private updateGlobalOnlineUsers(): void {
    // Merge all server-specific sets into global set
    this.onlineUsers.clear();
    this.onlineUsersByServer.forEach((users) => {
      users.forEach((userId) => {
        this.onlineUsers.add(userId);
      });
    });
  }

  /**
   * Subscribe to presence updates for a specific server
   */
  subscribeToServer(serverId: string): void {
    this.currentServerId = serverId;

    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      console.log(`[Presence] Subscribing to server ${serverId}`);
      this.ws.send(JSON.stringify({ type: 'subscribe', server: serverId }));
    }
  }

  /**
   * Check if a user is online (globally)
   */
  isOnline(userId: string): boolean {
    return this.onlineUsers.has(userId);
  }

  /**
   * Check if a user is online on a specific server
   */
  isOnlineOnServer(userId: string, serverId: string): boolean {
    const serverUsers = this.onlineUsersByServer.get(serverId);
    return serverUsers?.has(userId) || false;
  }

  /**
   * Get all online users (globally)
   */
  getOnlineUsers(): string[] {
    return Array.from(this.onlineUsers);
  }

  /**
   * Get online users for a specific server
   */
  getOnlineUsersForServer(serverId: string): string[] {
    const serverUsers = this.onlineUsersByServer.get(serverId);
    return serverUsers ? Array.from(serverUsers) : [];
  }

  /**
   * Get online user IDs as a Set (for efficient lookups)
   */
  getOnlineUserSet(): Set<string> {
    return new Set(this.onlineUsers);
  }

  /**
   * Get online user IDs for a server as a Set
   */
  getOnlineUserSetForServer(serverId: string): Set<string> {
    return this.onlineUsersByServer.get(serverId) || new Set();
  }

  /**
   * Get connection status
   */
  getIsConnected(): boolean {
    return this.isConnected;
  }

  /**
   * Add event listener
   */
  addListener(listener: PresenceListener): void {
    this.listeners.add(listener);
  }

  /**
   * Remove event listener
   */
  removeListener(listener: PresenceListener): void {
    this.listeners.delete(listener);
  }

  private emit(event: PresenceEvent): void {
    this.listeners.forEach((listener) => {
      try {
        listener(event);
      } catch (err) {
        console.error('[Presence] Listener error:', err);
      }
    });
  }

  /**
   * Force update - useful when friends list changes
   */
  forceUpdate(): void {
    this.emit({
      type: 'presence_update',
      users: Array.from(this.onlineUsers)
    });
  }
}

// Export singleton instance
export const presenceService = PresenceService.getInstance();
export default presenceService;
