import { useState, useEffect, useRef, useImperativeHandle, forwardRef } from 'react';
import { Channel } from '../types';
import { VoiceService, VoiceUser, ConnectionStatus, ScreenShareSettings, ScreenShareState } from '../services/webrtc';

interface VoiceChannelProps {
  channel: Channel;
  onLeave: () => void;
  onUsersChange: (users: VoiceUser[]) => void;
  onConnectionChange: (connected: boolean) => void;
  onError: (error: string | null) => void;
  onStatusChange?: (status: ConnectionStatus) => void;
  onPingChange?: (ping: number) => void;
  onScreenShareChange?: (state: ScreenShareState) => void;
  initialMuted?: boolean;
  initialDeafened?: boolean;
}

export interface VoiceChannelRef {
  toggleMute: () => { isMuted: boolean; wasUndeafened: boolean };
  leave: () => void;
  isMuted: () => boolean;
  setUserVolume: (userId: string, volume: number) => void;
  setDeafened: (deafened: boolean) => void;
  startScreenShare: (settings?: ScreenShareSettings) => Promise<void>;
  stopScreenShare: () => Promise<void>;
  getScreenShareState: () => ScreenShareState;
  getDebugInfo: () => {
    peersCount: number;
    peers: Array<{
      id: string;
      connectionState: string;
      iceState: string;
      signalingState: string;
    }>;
    screenShareState: {
      isSharing: boolean;
      isLocalShare: boolean;
      userId: string | null;
      hasStream: boolean;
    };
    localStreamActive: boolean;
    audioTracksCount: number;
  } | null;
}

const VoiceChannel = forwardRef<VoiceChannelRef, VoiceChannelProps>(
  ({ channel, onLeave, onUsersChange, onConnectionChange, onError, onStatusChange, onPingChange, onScreenShareChange, initialMuted = false, initialDeafened = false }, ref) => {
    const [isMuted, setIsMuted] = useState(initialMuted);
    const voiceServiceRef = useRef<VoiceService | null>(null);

    //  refs  callbacks    
    const callbacksRef = useRef({ onLeave, onUsersChange, onConnectionChange, onError, onStatusChange, onPingChange, onScreenShareChange });
    callbacksRef.current = { onLeave, onUsersChange, onConnectionChange, onError, onStatusChange, onPingChange, onScreenShareChange };

    // Refs    -    props
    const initialMutedRef = useRef(initialMuted);
    const initialDeafenedRef = useRef(initialDeafened);
    initialMutedRef.current = initialMuted;
    initialDeafenedRef.current = initialDeafened;

    useImperativeHandle(ref, () => ({
      toggleMute: () => {
        if (voiceServiceRef.current) {
          const result = voiceServiceRef.current.toggleMute();
          setIsMuted(result.isMuted);
          return result;
        }
        return { isMuted: false, wasUndeafened: false };
      },
      leave: () => {
        if (voiceServiceRef.current) {
          voiceServiceRef.current.leaveChannel();
        }
        onLeave();
      },
      isMuted: () => isMuted,
      setUserVolume: (userId: string, volume: number) => {
        if (voiceServiceRef.current) {
          voiceServiceRef.current.setUserVolume(userId, volume);
        }
      },
      setDeafened: (deafened: boolean) => {
        if (voiceServiceRef.current) {
          voiceServiceRef.current.setDeafened(deafened);
        }
      },
      startScreenShare: async (settings?: ScreenShareSettings) => {
        if (voiceServiceRef.current) {
          await voiceServiceRef.current.startScreenShare(settings);
        }
      },
      stopScreenShare: async () => {
        if (voiceServiceRef.current) {
          await voiceServiceRef.current.stopScreenShare();
        }
      },
      getScreenShareState: () => {
        if (voiceServiceRef.current) {
          return voiceServiceRef.current.getScreenShareState();
        }
        return {
          isSharing: false,
          isLocalShare: false,
          userId: null,
          username: null,
          stream: null,
        };
      },
      getDebugInfo: () => {
        if (voiceServiceRef.current) {
          return voiceServiceRef.current.getDebugInfo();
        }
        return null;
      },
    }));

    useEffect(() => {
      const userStr = localStorage.getItem('user');
      if (!userStr) return;

      const user = JSON.parse(userStr);
      const voiceService = new VoiceService(user.id, user.username);
      voiceServiceRef.current = voiceService;

      voiceService.setCallbacks(
        (updatedUsers) => {
          callbacksRef.current.onUsersChange(updatedUsers);
          callbacksRef.current.onConnectionChange(true);
          callbacksRef.current.onError(null);
        },
        (errorMsg) => {
          callbacksRef.current.onError(errorMsg);
          callbacksRef.current.onConnectionChange(false);
        },
        (status) => {
          callbacksRef.current.onStatusChange?.(status);
        },
        (ping) => {
          callbacksRef.current.onPingChange?.(ping);
        }
      );

      voiceService.setScreenShareCallback((state) => {
        callbacksRef.current.onScreenShareChange?.(state);
      });

      //     
      voiceService.setInitialState(initialMutedRef.current, initialDeafenedRef.current);
      if (initialMutedRef.current) {
        setIsMuted(true);
      }

      voiceService.joinChannel(channel.id).catch(() => {
        callbacksRef.current.onError('     ');
        callbacksRef.current.onConnectionChange(false);
      });

      return () => {
        voiceService.leaveChannel();
        callbacksRef.current.onUsersChange([]);
        callbacksRef.current.onConnectionChange(false);
      };
    }, [channel.id]);

    //      
    return null;
  }
);

VoiceChannel.displayName = 'VoiceChannel';

export default VoiceChannel;
