import React, { useState } from 'react';
import { ScreenShareSettings } from '../services/webrtc';
import { CloseIcon, ScreenShareIcon } from './Icons';

interface ScreenShareModalProps {
  isOpen: boolean;
  onClose: () => void;
  onStart: (settings: ScreenShareSettings) => void;
  initialSettings?: ScreenShareSettings;
}

const ScreenShareModal: React.FC<ScreenShareModalProps> = ({
  isOpen,
  onClose,
  onStart,
  initialSettings,
}) => {
  const [resolution, setResolution] = useState<'480p' | '720p' | '1080p'>(
    initialSettings?.resolution || '720p'
  );
  const [frameRate, setFrameRate] = useState<30 | 60>(
    initialSettings?.frameRate || 30
  );
  const [includeAudio, setIncludeAudio] = useState(
    initialSettings?.includeAudio ?? true
  );
  const [adaptiveBitrate, setAdaptiveBitrate] = useState(
    initialSettings?.adaptiveBitrate ?? true
  );

  if (!isOpen) return null;

  const handleStart = () => {
    onStart({
      resolution,
      frameRate,
      includeAudio,
      adaptiveBitrate,
    });
  };

  return (
    <div style={styles.overlay} onClick={onClose}>
      <div style={styles.modal} onClick={(e) => e.stopPropagation()}>
        <div style={styles.header}>
          <div style={styles.headerTitle}>
            <ScreenShareIcon size={24} />
            <span> </span>
          </div>
          <button style={styles.closeButton} onClick={onClose}>
            <CloseIcon size={20} />
          </button>
        </div>

        <div style={styles.content}>
          {/*  */}
          <div style={styles.section}>
            <label style={styles.label}></label>
            <div style={styles.buttonGroup}>
              {(['480p', '720p', '1080p'] as const).map((res) => (
                <button
                  key={res}
                  style={{
                    ...styles.optionButton,
                    ...(resolution === res ? styles.optionButtonActive : {}),
                  }}
                  onClick={() => setResolution(res)}
                >
                  {res}
                </button>
              ))}
            </div>
            <span style={styles.hint}>
              {resolution === '480p' && ' ,  '}
              {resolution === '720p' && ' '}
              {resolution === '1080p' && ' ,  '}
            </span>
          </div>

          {/* FPS */}
          <div style={styles.section}>
            <label style={styles.label}> </label>
            <div style={styles.buttonGroup}>
              {([30, 60] as const).map((fps) => (
                <button
                  key={fps}
                  style={{
                    ...styles.optionButton,
                    ...(frameRate === fps ? styles.optionButtonActive : {}),
                  }}
                  onClick={() => setFrameRate(fps)}
                >
                  {fps} FPS
                </button>
              ))}
            </div>
            <span style={styles.hint}>
              {frameRate === 30 && ' ,    '}
              {frameRate === 60 && ' ,  '}
            </span>
          </div>

          {/*   */}
          <div style={styles.section}>
            <label style={styles.checkboxLabel}>
              <input
                type="checkbox"
                checked={includeAudio}
                onChange={(e) => setIncludeAudio(e.target.checked)}
                style={styles.checkbox}
              />
              <span>  </span>
            </label>
            <span style={styles.hint}>
                     .
                  .
            </span>
          </div>

          {/*   */}
          <div style={styles.section}>
            <label style={styles.checkboxLabel}>
              <input
                type="checkbox"
                checked={adaptiveBitrate}
                onChange={(e) => setAdaptiveBitrate(e.target.checked)}
                style={styles.checkbox}
              />
              <span> </span>
            </label>
            <span style={styles.hint}>
                   
            </span>
          </div>
        </div>

        <div style={styles.footer}>
          <button style={styles.cancelButton} onClick={onClose}>
            
          </button>
          <button style={styles.startButton} onClick={handleStart}>
             
          </button>
        </div>
      </div>
    </div>
  );
};

const styles: { [key: string]: React.CSSProperties } = {
  overlay: {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.7)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 2000,
  },
  modal: {
    backgroundColor: 'var(--bg-secondary)',
    borderRadius: '8px',
    width: '440px',
    maxWidth: '90vw',
    boxShadow: '0 8px 32px rgba(0, 0, 0, 0.5)',
  },
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: '16px 20px',
    borderBottom: '1px solid var(--border)',
  },
  headerTitle: {
    display: 'flex',
    alignItems: 'center',
    gap: '12px',
    color: 'var(--text-primary)',
    fontSize: '18px',
    fontWeight: 600,
  },
  closeButton: {
    background: 'none',
    border: 'none',
    color: 'var(--text-muted)',
    cursor: 'pointer',
    padding: '4px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: '4px',
  },
  content: {
    padding: '20px',
    display: 'flex',
    flexDirection: 'column',
    gap: '20px',
  },
  section: {
    display: 'flex',
    flexDirection: 'column',
    gap: '8px',
  },
  label: {
    color: 'var(--text-secondary)',
    fontSize: '12px',
    fontWeight: 600,
    textTransform: 'uppercase',
  },
  buttonGroup: {
    display: 'flex',
    gap: '8px',
  },
  optionButton: {
    flex: 1,
    padding: '10px 16px',
    backgroundColor: 'var(--bg-tertiary)',
    border: '2px solid transparent',
    borderRadius: '4px',
    color: 'var(--text-secondary)',
    fontSize: '14px',
    fontWeight: 500,
    cursor: 'pointer',
    transition: 'all 0.15s ease',
  },
  optionButtonActive: {
    backgroundColor: 'rgba(88, 101, 242, 0.2)',
    borderColor: '#5865f2',
    color: '#fff',
  },
  hint: {
    color: 'var(--text-muted)',
    fontSize: '12px',
    lineHeight: 1.4,
  },
  checkboxLabel: {
    display: 'flex',
    alignItems: 'center',
    gap: '10px',
    color: 'var(--text-primary)',
    fontSize: '14px',
    cursor: 'pointer',
  },
  checkbox: {
    width: '18px',
    height: '18px',
    accentColor: '#5865f2',
    cursor: 'pointer',
  },
  footer: {
    display: 'flex',
    justifyContent: 'flex-end',
    gap: '12px',
    padding: '16px 20px',
    borderTop: '1px solid var(--border)',
  },
  cancelButton: {
    padding: '10px 20px',
    backgroundColor: 'transparent',
    border: 'none',
    borderRadius: '4px',
    color: 'var(--text-secondary)',
    fontSize: '14px',
    fontWeight: 500,
    cursor: 'pointer',
  },
  startButton: {
    padding: '10px 24px',
    backgroundColor: '#5865f2',
    border: 'none',
    borderRadius: '4px',
    color: '#fff',
    fontSize: '14px',
    fontWeight: 500,
    cursor: 'pointer',
  },
};

export default ScreenShareModal;
