import React, { useState, useEffect } from 'react';
import { Friend } from '../types';
import { getFriends } from '../services/friendsApi';
import { openDMChannel } from '../services/dmApi';
import { getAvatarColor } from '../utils/avatarColors';
import { usePresence } from '../contexts/PresenceContext';

interface FriendsListProps {
  onSelectDM: (channelId: string) => void;
  onOpenRequests: () => void;
  onSelectUser?: (userId: string) => void;
  pendingRequestCount?: number;
}

const FriendsList: React.FC<FriendsListProps> = ({
  onSelectDM,
  onOpenRequests,
  onSelectUser,
  pendingRequestCount = 0,
}) => {
  // Use centralized presence service
  const { isOnline } = usePresence();
  const [friends, setFriends] = useState<Friend[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [filter, setFilter] = useState<'all' | 'online'>('all');

  useEffect(() => {
    loadFriends();
  }, []);

  const loadFriends = async () => {
    try {
      setLoading(true);
      const data = await getFriends();
      setFriends(data);
    } catch (error) {
      console.error('Failed to load friends:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleOpenDM = async (friend: Friend) => {
    try {
      if (friend.dm_channel_id) {
        onSelectDM(friend.dm_channel_id);
      } else {
        const channel = await openDMChannel(friend.id);
        onSelectDM(channel.id);
      }
    } catch (error) {
      console.error('Failed to open DM:', error);
    }
  };

  const filteredFriends = friends
    .map(f => ({ ...f, is_online: isOnline(f.id) }))
    .filter(friend => {
      const matchesSearch = friend.username.toLowerCase().includes(searchQuery.toLowerCase());
      const matchesFilter = filter === 'all' || (filter === 'online' && friend.is_online);
      return matchesSearch && matchesFilter;
    })
    .sort((a, b) => {
      // Online first
      if (a.is_online && !b.is_online) return -1;
      if (!a.is_online && b.is_online) return 1;
      return a.username.localeCompare(b.username);
    });

  const onlineFriends = filteredFriends.filter(f => f.is_online);
  const offlineFriends = filteredFriends.filter(f => !f.is_online);

  if (loading) {
    return (
      <div style={{ padding: 16, color: 'var(--text-muted)', textAlign: 'center' }}>
        Loading friends...
      </div>
    );
  }

  return (
    <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
      {/* Search and Filter */}
      <div style={{ padding: '12px 12px 8px' }}>
        {/* Search */}
        <input
          type="text"
          placeholder=" ..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          style={{
            width: '100%',
            padding: '8px 12px',
            background: 'var(--bg-tertiary)',
            border: '1px solid var(--border)',
            borderRadius: 'var(--radius-sm)',
            color: 'var(--text-primary)',
            fontSize: 13,
            outline: 'none',
          }}
        />

        {/* Filter tabs */}
        <div style={{ display: 'flex', gap: 8, marginTop: 10 }}>
          {(['all', 'online'] as const).map((f) => (
            <button
              key={f}
              onClick={() => setFilter(f)}
              style={{
                flex: 1,
                padding: '6px 12px',
                background: filter === f ? 'var(--bg-modifier-selected)' : 'transparent',
                border: 'none',
                borderRadius: 'var(--radius-sm)',
                color: filter === f ? 'var(--text-primary)' : 'var(--text-secondary)',
                fontSize: 13,
                fontWeight: 500,
                cursor: 'pointer',
                transition: 'all 0.15s ease',
              }}
            >
              {f === 'all' ? ` (${friends.length})` : ` (${onlineFriends.length})`}
            </button>
          ))}
        </div>
      </div>

      {/* Friends List */}
      <div style={{ flex: 1, overflowY: 'auto', padding: '8px 8px' }}>
        {filteredFriends.length === 0 ? (
          <div style={{ padding: 24, textAlign: 'center', color: 'var(--text-muted)' }}>
            {searchQuery ? '  ' : '  '}
          </div>
        ) : (
          <>
            {/* Online section */}
            {filter === 'all' && onlineFriends.length > 0 && (
              <div style={{ marginBottom: 8 }}>
                <div style={{ padding: '8px 8px 4px', fontSize: 11, fontWeight: 600, color: 'var(--text-muted)', textTransform: 'uppercase' }}>
                    {onlineFriends.length}
                </div>
                {onlineFriends.map((friend) => (
                  <FriendItem
                    key={friend.id}
                    friend={friend}
                    onOpenDM={() => handleOpenDM(friend)}
                    onSelectUser={onSelectUser}
                  />
                ))}
              </div>
            )}

            {/* Offline section (only in 'all' filter) */}
            {filter === 'all' && offlineFriends.length > 0 && (
              <div>
                <div style={{ padding: '8px 8px 4px', fontSize: 11, fontWeight: 600, color: 'var(--text-muted)', textTransform: 'uppercase' }}>
                    {offlineFriends.length}
                </div>
                {offlineFriends.map((friend) => (
                  <FriendItem
                    key={friend.id}
                    friend={friend}
                    onOpenDM={() => handleOpenDM(friend)}
                    onSelectUser={onSelectUser}
                  />
                ))}
              </div>
            )}

            {/* Online filter - show all online friends */}
            {filter === 'online' && onlineFriends.map((friend) => (
              <FriendItem
                key={friend.id}
                friend={friend}
                onOpenDM={() => handleOpenDM(friend)}
                onSelectUser={onSelectUser}
              />
            ))}
          </>
        )}
      </div>
    </div>
  );
};

interface FriendItemProps {
  friend: Friend & { is_online: boolean };
  onOpenDM: () => void;
  onSelectUser?: (userId: string) => void;
}

const FriendItem: React.FC<FriendItemProps> = ({ friend, onOpenDM, onSelectUser }) => {
  const avatarColor = getAvatarColor(friend.id);

  return (
    <div
      onClick={onOpenDM}
      style={{
        display: 'flex',
        alignItems: 'center',
        gap: 12,
        padding: '10px 12px',
        borderRadius: 'var(--radius-sm)',
        cursor: 'pointer',
        transition: 'background 0.2s',
      }}
      onMouseEnter={(e) => (e.currentTarget.style.background = 'var(--bg-modifier-hover)')}
      onMouseLeave={(e) => (e.currentTarget.style.background = 'transparent')}
    >
      {/* Avatar */}
      <div
        style={{
          position: 'relative',
          width: 36,
          height: 36,
          borderRadius: '50%',
          background: friend.avatar_url ? `url(${friend.avatar_url}) center/cover` : avatarColor,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          color: 'white',
          fontWeight: 600,
          fontSize: 14,
          flexShrink: 0,
        }}
        onClick={(e) => {
          e.stopPropagation();
          onSelectUser?.(friend.id);
        }}
      >
        {!friend.avatar_url && friend.username[0].toUpperCase()}

        {/* Status indicator */}
        <div
          style={{
            position: 'absolute',
            bottom: -2,
            right: -2,
            width: 14,
            height: 14,
            borderRadius: '50%',
            background: friend.is_online ? 'var(--online)' : 'var(--offline)',
            border: '3px solid var(--bg-secondary)',
          }}
        />
      </div>

      {/* Name */}
      <div style={{ flex: 1, minWidth: 0 }}>
        <div
          style={{
            fontWeight: 500,
            fontSize: 14,
            color: 'var(--text-primary)',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
          }}
        >
          {friend.username}
        </div>
        <div style={{ fontSize: 12, color: friend.is_online ? 'var(--online)' : 'var(--text-muted)' }}>
          {friend.is_online ? ' ' : '  '}
        </div>
      </div>

      {/* Message icon */}
      <div style={{ color: 'var(--text-muted)' }}>
        <svg width="20" height="20" viewBox="0 0 24 24" fill="none">
          <path d="M21 15a2 2 0 01-2 2H7l-4 4V5a2 2 0 012-2h14a2 2 0 012 2v10z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
        </svg>
      </div>
    </div>
  );
};

export default FriendsList;
