import axios from 'axios';
import { DMChannel, DMMessage, VoiceCallState } from '../types';

const API_URL = process.env.REACT_APP_API_URL || '/api';

const api = axios.create({
  baseURL: API_URL,
});

api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Get all DM channels
export const getDMChannels = async (): Promise<DMChannel[]> => {
  const response = await api.get('/dm');
  return response.data || [];
};

// Get single DM channel by ID
export const getDMChannel = async (channelId: string): Promise<DMChannel> => {
  const response = await api.get(`/dm/${channelId}`);
  return response.data;
};

// Open or create DM channel with user
export const openDMChannel = async (userId: string): Promise<DMChannel> => {
  const response = await api.post(`/dm/open/${userId}`);
  return response.data;
};

// Get messages from DM channel
export const getDMMessages = async (channelId: string): Promise<DMMessage[]> => {
  const response = await api.get(`/dm/${channelId}/messages`);
  return response.data || [];
};

// Send message in DM channel
export const sendDMMessage = async (channelId: string, content: string): Promise<DMMessage> => {
  const response = await api.post(`/dm/${channelId}/messages`, { content });
  return response.data;
};

// Edit DM message
export const editDMMessage = async (channelId: string, messageId: string, content: string): Promise<DMMessage> => {
  const response = await api.put(`/dm/${channelId}/messages/${messageId}`, { content });
  return response.data;
};

// Delete DM message
export const deleteDMMessage = async (channelId: string, messageId: string): Promise<void> => {
  await api.delete(`/dm/${channelId}/messages/${messageId}`);
};

// Voice calls
export const startCall = async (channelId: string): Promise<VoiceCallState> => {
  const response = await api.post(`/dm/${channelId}/call/start`);
  return response.data;
};

export const acceptCall = async (channelId: string): Promise<VoiceCallState> => {
  const response = await api.post(`/dm/${channelId}/call/accept`);
  return response.data;
};

export const endCall = async (channelId: string): Promise<void> => {
  await api.post(`/dm/${channelId}/call/end`);
};

export const getCallStatus = async (channelId: string): Promise<VoiceCallState | { status: 'none' }> => {
  const response = await api.get(`/dm/${channelId}/call/status`);
  return response.data;
};

// WebSocket URL helpers
export const getDMWebSocketUrl = (channelId: string): string => {
  const token = localStorage.getItem('token');
  const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
  const host = window.location.host;
  return `${protocol}//${host}/api/dm/${channelId}/ws?token=${token}`;
};

export const getDMVoiceWebSocketUrl = (channelId: string): string => {
  const token = localStorage.getItem('token');
  const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
  const host = window.location.host;
  return `${protocol}//${host}/api/dm/${channelId}/voice?token=${token}`;
};

export const getNotificationsWebSocketUrl = (): string => {
  const token = localStorage.getItem('token');
  const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
  const host = window.location.host;
  return `${protocol}//${host}/api/notifications?token=${token}`;
};

export default api;
