import { debugLogger } from './debugLogger';

export interface VoiceUser {
  id: string;
  username: string;
  isMuted: boolean;
  isDeafened: boolean;
  isSpeaking: boolean;
  isScreenSharing: boolean;
  volume: number; // 0-100
}

export interface ScreenShareSettings {
  resolution: '480p' | '720p' | '1080p';
  frameRate: 30 | 60;
  includeAudio: boolean;
  adaptiveBitrate: boolean;
}

export interface ScreenShareState {
  isSharing: boolean;
  isLocalShare: boolean;
  userId: string | null;
  username: string | null;
  stream: MediaStream | null;
  hasAudio?: boolean;
  audioWarning?: string | null;
}

export const RESOLUTION_PRESETS = {
  '480p': { width: 854, height: 480, bitrate: 1000000 },
  '720p': { width: 1280, height: 720, bitrate: 2500000 },
  '1080p': { width: 1920, height: 1080, bitrate: 4000000 },
} as const;

export type ConnectionStatus =
  | 'disconnected'
  | 'connecting'
  | 'authenticating'
  | 'connecting_rtc'
  | 'connected'
  | 'reconnecting'
  | 'ice_checking'
  | 'ice_disconnected';

export const ConnectionStatusLabels: Record<ConnectionStatus, string> = {
  disconnected: '',
  connecting: '...',
  authenticating: '...',
  connecting_rtc: '  RTC...',
  connected: ' ',
  reconnecting: '...',
  ice_checking: ' ICE...',
  ice_disconnected: 'ICE ',
};

// Heartbeat configuration
export interface HeartbeatConfig {
  interval: number;      //   heartbeat (ms)
  timeout: number;       //   ACK (ms)
  maxMissed: number;     //   heartbeat  
}

// Encoding parameters for audio/video
export interface EncodingParams {
  audio: {
    maxBitrate: number;      //    (bps)
    dtx: boolean;            // Discontinuous Transmission (   )
    fec: boolean;            // Forward Error Correction
    stereo: boolean;         //  
  };
  video: {
    maxBitrate: number;      //    (bps)
    maxFramerate: number;    //  FPS
    scalabilityMode: string; //   (L1T1, L1T2, L1T3)
    priority: 'very-low' | 'low' | 'medium' | 'high';
  };
}

// Default encoding params
export const DEFAULT_ENCODING_PARAMS: EncodingParams = {
  audio: {
    maxBitrate: 64000,    // 64 kbps  
    dtx: true,            //  DTX   
    fec: true,            // FEC  
    stereo: false,        //   
  },
  video: {
    maxBitrate: 2500000,  // 2.5 Mbps  
    maxFramerate: 30,     // 30 FPS
    scalabilityMode: 'L1T2', // Temporal scalability
    priority: 'medium',
  },
};

// Connection state for a single peer
export interface PeerConnectionState {
  peerId: string;
  connectionState: RTCPeerConnectionState;
  iceConnectionState: RTCIceConnectionState;
  iceGatheringState: RTCIceGathererState | RTCIceGatheringState;
  signalingState: RTCSignalingState;
  lastStateChange: number;
  reconnectAttempts: number;
}

// Signal types for WebSocket communication
interface SignalBase {
  type: string;
  userId?: string;
  username?: string;
}

interface SignalData extends SignalBase {
  isMuted?: boolean;
  isDeafened?: boolean;
  isScreenSharing?: boolean;
  isSpeaking?: boolean;
  offer?: RTCSessionDescriptionInit;
  answer?: RTCSessionDescriptionInit;
  candidate?: RTCIceCandidateInit;
  users?: Array<{ id: string; username: string; isMuted: boolean; isDeafened?: boolean; isScreenSharing?: boolean }>;
  hasAudio?: boolean;
  settings?: ScreenShareSettings;
  timestamp?: number;
  seq?: number;           // Heartbeat sequence number
  targetUserId?: string;  // Target user for directed signals
  [key: string]: unknown;
}

// RTCStats types for bitrate monitoring
interface RTCOutboundRtpStats {
  type: 'outbound-rtp';
  kind: string;
  packetsSent?: number;
  packetsLost?: number;
}

// Error with name property
interface ErrorWithName extends Error {
  name: string;
}

function isErrorWithName(error: unknown): error is ErrorWithName {
  return error instanceof Error && 'name' in error;
}

export class VoiceService {
  private localStream: MediaStream | null = null;
  private peerConnections: Map<string, RTCPeerConnection> = new Map();
  private ws: WebSocket | null = null;
  private channelId: string | null = null;
  private userId: string;
  private username: string;
  private onUsersChange: ((users: VoiceUser[]) => void) | null = null;
  private onError: ((error: string) => void) | null = null;
  private onStatusChange: ((status: ConnectionStatus) => void) | null = null;
  private onPingChange: ((ping: number) => void) | null = null;
  private connectionStatus: ConnectionStatus = 'disconnected';
  private isMuted: boolean = false;
  private isDeafened: boolean = false;
  private initialMuted: boolean = false;
  private initialDeafened: boolean = false;
  private rtcConnectedPeers: Set<string> = new Set();
  private pingInterval: number | null = null;
  private lastPingTime: number = 0;
  private currentPing: number = 0;

  // Heartbeat with sequence numbers (Discord-style)
  private heartbeatConfig: HeartbeatConfig = {
    interval: 5000,    // 5 seconds
    timeout: 10000,    // 10 seconds timeout
    maxMissed: 3,      // 3 missed = reconnect
  };
  private heartbeatSeq: number = 0;          //  sequence number
  private lastAckedSeq: number = -1;         //   seq
  private missedHeartbeats: number = 0;      //   heartbeat
  private heartbeatTimeout: number | null = null;  //   ACK

  // Encoding parameters
  private encodingParams: EncodingParams = DEFAULT_ENCODING_PARAMS;

  // Peer connection states for state machine
  private peerStates: Map<string, PeerConnectionState> = new Map();
  private users: Map<string, VoiceUser> = new Map();
  private audioElements: Map<string, HTMLAudioElement> = new Map();
  private userGainNodes: Map<string, { context: AudioContext; gainNode: GainNode }> = new Map();
  private userVolumes: Map<string, number> = new Map();
  private audioContext: AudioContext | null = null;
  private analyser: AnalyserNode | null = null;
  private vadInterval: number | null = null;
  private isConnecting: boolean = false;
  private isConnected: boolean = false;
  private reconnectAttempts: number = 0;
  private maxReconnectAttempts: number = 5;
  private reconnectTimeout: number | null = null;
  private iceRestartTimers: Map<string, number> = new Map();

  // Screen sharing
  private screenStream: MediaStream | null = null;
  private screenShareState: ScreenShareState = {
    isSharing: false,
    isLocalShare: false,
    userId: null,
    username: null,
    stream: null,
  };
  private screenShareSettings: ScreenShareSettings = {
    resolution: '720p',
    frameRate: 30,
    includeAudio: true,
    adaptiveBitrate: true,
  };
  private onScreenShareChange: ((state: ScreenShareState) => void) | null = null;
  private screenSenders: Map<string, RTCRtpSender> = new Map();
  private bitrateMonitorInterval: number | null = null;
  private currentBitrate: number = 2500000;
  private pendingScreenStreams: Map<string, MediaStream> = new Map(); // ,   
  private visibilityHandler: (() => void) | null = null;
  private screenShareVerifyInterval: number | null = null;
  private screenShareVerifyAttempts: number = 0;

  private config: RTCConfiguration = {
    iceServers: [
      // STUN servers
      { urls: 'stun:stun.l.google.com:19302' },
      { urls: 'stun:stun1.l.google.com:19302' },
      { urls: 'stun:r1gate.ru:3478' },
      // TURN server
      {
        urls: 'turn:r1gate.ru:3478',
        username: 'r1gate',
        credential: 'SecurePass123',
      },
      {
        urls: 'turn:r1gate.ru:3478?transport=tcp',
        username: 'r1gate',
        credential: 'SecurePass123',
      },
    ],
    iceCandidatePoolSize: 10, // Pre-gather candidates for faster connection
  };

  constructor(userId: string, username: string) {
    this.userId = userId;
    this.username = username;
    //      localStorage
    this.loadUserVolumes();
  }

  //    muted/deafened ( )
  setInitialState(muted: boolean, deafened: boolean): void {
    this.initialMuted = muted;
    this.initialDeafened = deafened;
    this.isMuted = muted;
    this.isDeafened = deafened;
  }

  private loadUserVolumes(): void {
    try {
      const saved = localStorage.getItem('userVolumes');
      if (saved) {
        const parsed = JSON.parse(saved);
        Object.entries(parsed).forEach(([id, volume]) => {
          this.userVolumes.set(id, volume as number);
        });
      }
    } catch (e) {
      console.error('Failed to load user volumes:', e);
    }
  }

  private saveUserVolumes(): void {
    try {
      const obj: Record<string, number> = {};
      this.userVolumes.forEach((volume, id) => {
        obj[id] = volume;
      });
      localStorage.setItem('userVolumes', JSON.stringify(obj));
    } catch (e) {
      console.error('Failed to save user volumes:', e);
    }
  }

  setCallbacks(
    onUsersChange: (users: VoiceUser[]) => void,
    onError: (error: string) => void,
    onStatusChange?: (status: ConnectionStatus) => void,
    onPingChange?: (ping: number) => void
  ) {
    this.onUsersChange = onUsersChange;
    this.onError = onError;
    this.onStatusChange = onStatusChange || null;
    this.onPingChange = onPingChange || null;
  }

  private setConnectionStatus(status: ConnectionStatus) {
    const prevStatus = this.connectionStatus;
    this.connectionStatus = status;
    this.onStatusChange?.(status);

    //    
    if (status === 'connected' && prevStatus !== 'connected') {
      debugLogger.success('    ');
    } else if (status === 'reconnecting') {
      debugLogger.warning('...');
    }
  }

  private startPingInterval() {
    if (this.pingInterval) return;

    // Reset heartbeat state
    this.heartbeatSeq = 0;
    this.lastAckedSeq = -1;
    this.missedHeartbeats = 0;

    this.sendHeartbeat();
    this.pingInterval = window.setInterval(() => {
      this.sendHeartbeat();
    }, this.heartbeatConfig.interval);

    debugLogger.websocket(`Heartbeat started (interval: ${this.heartbeatConfig.interval}ms)`);
  }

  private stopPingInterval() {
    if (this.pingInterval) {
      clearInterval(this.pingInterval);
      this.pingInterval = null;
    }
    if (this.heartbeatTimeout) {
      clearTimeout(this.heartbeatTimeout);
      this.heartbeatTimeout = null;
    }
    this.heartbeatSeq = 0;
    this.lastAckedSeq = -1;
    this.missedHeartbeats = 0;
  }

  private sendHeartbeat() {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      const seq = this.heartbeatSeq++;
      this.lastPingTime = Date.now();

      //  heartbeat  sequence number
      this.sendSignal({
        type: 'heartbeat',
        seq: seq,
        timestamp: this.lastPingTime,
      });

      //      heartbeat
      if (seq > 0 && this.lastAckedSeq < seq - 1) {
        this.missedHeartbeats++;
        debugLogger.warning(`Heartbeat ACK missed`, `seq: ${seq - 1}, missed: ${this.missedHeartbeats}/${this.heartbeatConfig.maxMissed}`);

        if (this.missedHeartbeats >= this.heartbeatConfig.maxMissed) {
          debugLogger.error(`Too many missed heartbeats`, `Reconnecting...`);
          this.handleHeartbeatFailure();
          return;
        }
      }

      //     ACK
      if (this.heartbeatTimeout) {
        clearTimeout(this.heartbeatTimeout);
      }
      this.heartbeatTimeout = window.setTimeout(() => {
        //     ACK  
        if (this.lastAckedSeq < seq) {
          this.missedHeartbeats++;
          debugLogger.warning(`Heartbeat timeout`, `seq: ${seq}, missed: ${this.missedHeartbeats}`);
        }
      }, this.heartbeatConfig.timeout);

      // Debug log  10 heartbeats
      if (seq % 10 === 0) {
        debugLogger.websocket(`Heartbeat seq=${seq}, lastAcked=${this.lastAckedSeq}, missed=${this.missedHeartbeats}`);
      }
    }
  }

  private handleHeartbeatAck(seq: number, serverTimestamp?: number) {
    const now = Date.now();

    //  ping   round-trip time
    if (serverTimestamp) {
      //    timestamp -      RTT
      this.currentPing = now - serverTimestamp;
    } else {
      this.currentPing = now - this.lastPingTime;
    }
    this.onPingChange?.(this.currentPing);

    //  sequence
    if (seq > this.lastAckedSeq) {
      this.lastAckedSeq = seq;
      this.missedHeartbeats = 0; //     ACK

      //  
      if (this.heartbeatTimeout) {
        clearTimeout(this.heartbeatTimeout);
        this.heartbeatTimeout = null;
      }
    }
  }

  private handleHeartbeatFailure() {
    //  heartbeat
    this.stopPingInterval();

    //  
    if (this.ws) {
      this.ws.close(4000, 'Heartbeat failure');
    }
  }

  // Legacy support for servers that still send pong
  private handlePong(timestamp: number) {
    const now = Date.now();
    this.currentPing = now - timestamp;
    this.onPingChange?.(this.currentPing);
  }

  // Get heartbeat statistics
  getHeartbeatStats(): { seq: number; lastAcked: number; missed: number; ping: number } {
    return {
      seq: this.heartbeatSeq,
      lastAcked: this.lastAckedSeq,
      missed: this.missedHeartbeats,
      ping: this.currentPing,
    };
  }

  // Configure heartbeat parameters
  setHeartbeatConfig(config: Partial<HeartbeatConfig>): void {
    this.heartbeatConfig = { ...this.heartbeatConfig, ...config };
    debugLogger.info('Heartbeat config updated', JSON.stringify(this.heartbeatConfig));
  }

  async joinChannel(channelId: string): Promise<void> {
    // Prevent duplicate connections
    if (this.isConnecting || this.isConnected) {
      debugLogger.warning('      ');
      return;
    }
    this.isConnecting = true;
    this.channelId = channelId;
    this.setConnectionStatus('connecting');
    debugLogger.info('   ', `channelId: ${channelId}`);

    //     localStorage
    const noiseSuppressionSetting = localStorage.getItem('noiseSuppression');
    const noiseSuppression = noiseSuppressionSetting !== null ? JSON.parse(noiseSuppressionSetting) : true;

    try {
      //       
      this.localStream = await navigator.mediaDevices.getUserMedia({
        audio: {
          echoCancellation: { ideal: true },
          noiseSuppression: { ideal: noiseSuppression },
          autoGainControl: { ideal: true },
          // Chrome-specific  
          // @ts-ignore - Chrome experimental constraints
          googEchoCancellation: { ideal: true },
          // @ts-ignore
          googAutoGainControl: { ideal: true },
          // @ts-ignore
          googNoiseSuppression: { ideal: true },
          // @ts-ignore -    ( )
          googHighpassFilter: { ideal: true },
          // @ts-ignore -   
          googTypingNoiseDetection: { ideal: true },
          // @ts-ignore
          googAudioMirroring: { ideal: false },
        } as MediaTrackConstraints,
        video: false,
      });

      //      (  )
      this.users.set(this.userId, {
        id: this.userId,
        username: this.username,
        isMuted: this.isMuted,
        isDeafened: this.isDeafened,
        isSpeaking: false,
        isScreenSharing: false,
        volume: 100,
      });
      this.notifyUsersChange();

      //  mute     
      if (this.isMuted && this.localStream) {
        this.localStream.getAudioTracks().forEach((track) => {
          track.enabled = false;
        });
      }

      //    
      this.startVoiceActivityDetection();

      //     
      this.setupVisibilityHandler();

      this.setConnectionStatus('authenticating');

      //   WebSocket  
      const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
      const host = window.location.host;
      const wsUrl = `${protocol}//${host}/api`;
      const token = localStorage.getItem('token');
      this.ws = new WebSocket(`${wsUrl}/voice/${channelId}?token=${token}`);

      this.ws.onopen = () => {
        debugLogger.websocket('WebSocket ');
        this.isConnecting = false;
        this.isConnected = true;
        this.reconnectAttempts = 0; //     
        this.setConnectionStatus('connecting_rtc');
        //  join    muted/deafened
        this.sendSignal({
          type: 'join',
          userId: this.userId,
          username: this.username,
          isMuted: this.isMuted,
          isDeafened: this.isDeafened,
        });
        this.startPingInterval();
      };

      this.ws.onmessage = (event) => {
        const data = JSON.parse(event.data);
        this.handleSignal(data);
      };

      this.ws.onerror = (error) => {
        debugLogger.error('WebSocket ', String(error));
        //      
        if (this.reconnectAttempts >= this.maxReconnectAttempts) {
          this.onError?.('    ');
        }
      };

      this.ws.onclose = () => {
        debugLogger.websocket('WebSocket ');
        this.isConnected = false;
        this.stopPingInterval();

        //      
        if (this.channelId && this.reconnectAttempts < this.maxReconnectAttempts) {
          this.reconnectAttempts++;
          this.setConnectionStatus('reconnecting');
          debugLogger.websocket(` ${this.reconnectAttempts}/${this.maxReconnectAttempts}`);

          //  : 1s, 2s, 4s, 8s, 16s
          const delay = Math.min(1000 * Math.pow(2, this.reconnectAttempts - 1), 16000);
          this.reconnectTimeout = window.setTimeout(() => {
            this.reconnectWebSocket();
          }, delay);
        } else {
          this.setConnectionStatus('disconnected');
          if (this.reconnectAttempts >= this.maxReconnectAttempts) {
            this.onError?.('   ');
          }
        }
      };
    } catch (error: unknown) {
      console.error('Failed to join voice channel:', error);
      this.isConnecting = false;
      this.isConnected = false;
      if (isErrorWithName(error) && error.name === 'NotAllowedError') {
        this.onError?.('   ');
      } else {
        this.onError?.('     ');
      }
      throw error;
    }
  }

  private sendSignal(data: SignalData) {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify(data));
    }
  }

  private reconnectWebSocket() {
    if (!this.channelId) return;

    console.log('Reconnecting WebSocket...');
    const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
    const host = window.location.host;
    const wsUrl = `${protocol}//${host}/api`;
    const token = localStorage.getItem('token');

    try {
      this.ws = new WebSocket(`${wsUrl}/voice/${this.channelId}?token=${token}`);

      this.ws.onopen = () => {
        console.log('Voice WebSocket reconnected');
        this.reconnectAttempts = 0; //    
        this.isConnected = true;
        this.setConnectionStatus('connecting_rtc');
        //  join    muted/deafened
        this.sendSignal({
          type: 'join',
          userId: this.userId,
          username: this.username,
          isMuted: this.isMuted,
          isDeafened: this.isDeafened,
        });
        this.startPingInterval();
      };

      this.ws.onmessage = (event) => {
        const data = JSON.parse(event.data);
        this.handleSignal(data);
      };

      this.ws.onerror = (error) => {
        console.error('Voice WebSocket reconnect error:', error);
      };

      this.ws.onclose = () => {
        console.log('Voice WebSocket closed after reconnect');
        this.isConnected = false;
        this.stopPingInterval();

        if (this.channelId && this.reconnectAttempts < this.maxReconnectAttempts) {
          this.reconnectAttempts++;
          this.setConnectionStatus('reconnecting');
          const delay = Math.min(1000 * Math.pow(2, this.reconnectAttempts - 1), 16000);
          this.reconnectTimeout = window.setTimeout(() => {
            this.reconnectWebSocket();
          }, delay);
        } else {
          this.setConnectionStatus('disconnected');
          if (this.reconnectAttempts >= this.maxReconnectAttempts) {
            this.onError?.('   ');
          }
        }
      };
    } catch (error) {
      console.error('Failed to reconnect WebSocket:', error);
    }
  }

  private async handleSignal(data: SignalData) {
    console.log('Received signal:', data.type, data.userId || '');
    switch (data.type) {
      case 'user-joined':
        if (data.userId && data.username) {
          await this.handleUserJoined(data.userId, data.username, data.isMuted || false, data.isDeafened || false);
        }
        break;
      case 'user-left':
        if (data.userId) {
          this.handleUserLeft(data.userId);
        }
        break;
      case 'users-list':
        if (data.users) {
          this.handleUsersList(data.users);
        }
        break;
      case 'offer':
        if (data.userId && data.offer) {
          await this.handleOffer(data.userId, data.offer);
        }
        break;
      case 'answer':
        if (data.userId && data.answer) {
          await this.handleAnswer(data.userId, data.answer);
        }
        break;
      case 'ice-candidate':
        if (data.userId && data.candidate) {
          await this.handleIceCandidate(data.userId, data.candidate);
        }
        break;
      case 'mute-status':
        if (data.userId) {
          this.handleMuteStatus(data.userId, data.isMuted ?? false);
        }
        break;
      case 'speaking-status':
        if (data.userId) {
          this.handleSpeakingStatus(data.userId, data.isSpeaking ?? false);
        }
        break;
      case 'deafened-status':
        if (data.userId) {
          this.handleDeafenedStatus(data.userId, data.isDeafened ?? false);
        }
        break;
      case 'pong':
        if (typeof data.timestamp === 'number') {
          this.handlePong(data.timestamp);
        }
        break;
      case 'heartbeat-ack':
        //    sequence number
        if (typeof data.seq === 'number') {
          this.handleHeartbeatAck(data.seq, data.timestamp as number | undefined);
        }
        break;
      case 'screen-share-start':
        if (data.userId && data.username) {
          this.handleScreenShareStart(data.userId, data.username);
        }
        break;
      case 'screen-share-stop':
        if (data.userId) {
          this.handleScreenShareStop(data.userId);
        }
        break;
      case 'request-screen-share':
        if (data.userId) {
          this.handleRequestScreenShare(data.userId);
        }
        break;
      case 'user-status-update':
        //    (muted  deafened )
        if (data.userId) {
          this.handleUserStatusUpdate(data.userId, data.isMuted ?? false, data.isDeafened ?? false);
        }
        break;
    }
  }

  private handleUsersList(users: Array<{ id: string; username: string; isMuted: boolean; isDeafened?: boolean; isScreenSharing?: boolean }>) {
    const otherUsers = users.filter(u => u.id !== this.userId);
    let screenSharingUserId: string | null = null;
    let screenSharingUsername: string | null = null;

    otherUsers.forEach((user) => {
      const savedVolume = this.userVolumes.get(user.id) ?? 100;
      this.users.set(user.id, {
        id: user.id,
        username: user.username,
        isMuted: user.isMuted,
        isDeafened: user.isDeafened || false,
        isSpeaking: false,
        isScreenSharing: user.isScreenSharing || false,
        volume: savedVolume,
      });

      //    
      if (user.isScreenSharing) {
        screenSharingUserId = user.id;
        screenSharingUsername = user.username;
      }
    });

    //  -    -  
    if (screenSharingUserId && screenSharingUsername) {
      this.screenShareState = {
        isSharing: true,
        isLocalShare: false,
        userId: screenSharingUserId,
        username: screenSharingUsername,
        stream: null,
      };
      this.onScreenShareChange?.(this.screenShareState);
    }

    this.notifyUsersChange();

    //     -   
    if (otherUsers.length === 0) {
      this.setConnectionStatus('connected');
      return;
    }

    //     
    //   ID     offer
    otherUsers.forEach((user) => {
      const shouldCreateOffer = this.userId < user.id;
      console.log(`Creating connection to ${user.username}, shouldCreateOffer: ${shouldCreateOffer}`);
      this.createPeerConnection(user.id, shouldCreateOffer);
    });

    //  -  -     
    if (screenSharingUserId) {
      const targetId = screenSharingUserId;
      //    
      setTimeout(() => {
        console.log('Requesting screen share from', targetId);
        this.sendSignal({
          type: 'request-screen-share',
          userId: this.userId,
          targetUserId: targetId,
        });
      }, 2000);
    }
  }

  private async handleUserJoined(userId: string, username: string, isMuted: boolean = false, isDeafened: boolean = false) {
    if (userId === this.userId) return;

    debugLogger.user(`${username}   `);

    const savedVolume = this.userVolumes.get(userId) ?? 100;
    this.users.set(userId, {
      id: userId,
      username: username,
      isMuted: isMuted,
      isDeafened: isDeafened,
      isSpeaking: false,
      isScreenSharing: false,
      volume: savedVolume,
    });
    this.notifyUsersChange();

    //  peer connection   
    //   ID     offer ( "glare")
    //    ID  offer
    const shouldCreateOffer = this.userId < userId;
    await this.createPeerConnection(userId, shouldCreateOffer);

    //      -    
    if (this.screenShareState.isSharing && this.screenShareState.userId === this.userId && this.screenStream) {
      console.log(`Adding screen share tracks to new user ${username}`);
      //    peer connection 
      setTimeout(() => {
        this.addScreenTrackToNewPeer(userId);
      }, 1000);
    }
  }

  private handleUserLeft(userId: string) {
    const user = this.users.get(userId);
    const username = user?.username || userId.substring(0, 8);
    debugLogger.user(`${username}  `);

    this.users.delete(userId);
    this.closePeerConnection(userId);
    this.notifyUsersChange();
  }

  private async createPeerConnection(userId: string, createOffer: boolean) {
    if (this.peerConnections.has(userId)) {
      debugLogger.webrtc(`Peer ${userId.substring(0, 8)}  `);
      return;
    }

    debugLogger.webrtc(` peer connection`, `userId: ${userId.substring(0, 8)}, createOffer: ${createOffer}`);
    const pc = new RTCPeerConnection(this.config);
    this.peerConnections.set(userId, pc);

    //    
    if (this.localStream) {
      const tracks = this.localStream.getTracks();
      console.log(`Adding ${tracks.length} local tracks to peer connection`);
      for (const track of tracks) {
        console.log(`Adding track: ${track.kind}, enabled=${track.enabled}, muted=${track.muted}`);
        const sender = pc.addTrack(track, this.localStream!);
        // Apply encoding parameters
        this.applyEncodingToSender(sender);
      }
    } else {
      console.warn('No local stream available when creating peer connection!');
    }

    //  ICE 
    pc.onicecandidate = (event) => {
      if (event.candidate) {
        this.sendSignal({
          type: 'ice-candidate',
          userId: this.userId,
          targetUserId: userId,
          candidate: event.candidate,
        });
      }
    };

    //    (  )
    pc.ontrack = (event) => {
      const track = event.track;
      console.log('Received remote track from:', userId);
      console.log('Track info:', {
        kind: track.kind,
        enabled: track.enabled,
        muted: track.muted,
        readyState: track.readyState,
        streamId: event.streams[0]?.id,
        streamActive: event.streams[0]?.active,
      });

      const stream = event.streams[0];

      //    (screen share)
      if (track.kind === 'video') {
        console.log('=== SCREEN SHARE VIDEO RECEIVED ===');
        console.log('From userId:', userId);
        console.log('Current screenShareState.userId:', this.screenShareState.userId);
        console.log('Stream active:', stream?.active);
        console.log('Video tracks:', stream?.getVideoTracks().length);

        //   
        const user = this.users.get(userId);
        const username = user?.username || 'Unknown';

        //       -  stream
        if (this.screenShareState.userId === userId) {
          console.log('Updating screenShareState with new stream');
          //     React  
          this.screenShareState = {
            ...this.screenShareState,
            stream: stream,
          };
          this.onScreenShareChange?.(this.screenShareState);
        } else {
          //    -   
          // (  WebSocket ,         )
          console.log('Video track received, activating screen share from:', userId);

          //   
          if (user) {
            user.isScreenSharing = true;
            this.notifyUsersChange();
          }

          this.screenShareState = {
            isSharing: true,
            isLocalShare: false,
            userId: userId,
            username: username,
            stream: stream,
          };

          this.onScreenShareChange?.(this.screenShareState);
        }

        //  unmute  
        track.onunmute = () => {
          console.log('Screen share track unmuted from:', userId);
          //  stream   
          if (this.screenShareState.userId === userId && this.screenShareState.stream !== stream) {
            this.screenShareState = {
              ...this.screenShareState,
              stream: stream,
            };
            this.onScreenShareChange?.(this.screenShareState);
          }
        };

        //    -   readyState === 'ended'
        //    mute -      WebSocket 
        track.onended = () => {
          console.log('Screen share track ENDED from:', userId, 'readyState:', track.readyState);
          //     
          if (track.readyState === 'ended') {
            if (this.screenShareState.userId === userId) {
              console.log('Track truly ended, stopping screen share');
              this.handleScreenShareStop(userId);
            }
          }
        };

        //   onmute -    
        //  screen share   WebSocket  screen-share-stop
        track.onmute = () => {
          console.log('Screen share track muted from:', userId, '(ignored - waiting for explicit stop signal)');
        };

        //   screen share    video   UI
        //    audio  -    VoiceChannelView
        const screenAudioTracks = stream.getAudioTracks();
        if (screenAudioTracks.length > 0) {
          console.log('Screen share has audio tracks:', screenAudioTracks.length);
          console.log('Audio will be played through video element in UI');
        }

        return;
      }

      //   -     
      const existingAudio = this.audioElements.get(userId);
      if (existingAudio) {
        existingAudio.srcObject = null;
        existingAudio.remove();
      }
      const audio = document.createElement('audio');
      audio.srcObject = stream;
      audio.autoplay = true;
      //    audio  -  Web Audio API   
      audio.volume = 0;
      audio.muted = true;

      //        
      try {
        const audioCtx = new AudioContext();
        const sourceNode = audioCtx.createMediaStreamSource(stream);

        //  GainNode   
        const gainNode = audioCtx.createGain();
        const savedVolume = this.userVolumes.get(userId) ?? 100;
        //    -  0,  
        gainNode.gain.value = this.isDeafened ? 0 : savedVolume / 100;

        const analyser = audioCtx.createAnalyser();
        analyser.fftSize = 256;

        // : source -> gainNode -> destination
        //  source -> analyser  
        sourceNode.connect(gainNode);
        gainNode.connect(audioCtx.destination);
        sourceNode.connect(analyser);

        //  GainNode    
        this.userGainNodes.set(userId, { context: audioCtx, gainNode });

        const dataArray = new Uint8Array(analyser.frequencyBinCount);
        let logCounter = 0;
        const checkAudioLevel = () => {
          analyser.getByteFrequencyData(dataArray);
          const average = dataArray.reduce((a, b) => a + b, 0) / dataArray.length;
          logCounter++;
          if (logCounter % 50 === 0 && average > 0) {
            console.log(`Audio level from ${userId}: ${average.toFixed(1)}`);
          }
          if (this.audioElements.has(userId)) {
            requestAnimationFrame(checkAudioLevel);
          }
        };
        checkAudioLevel();
        console.log('Web Audio API with volume control setup for', userId);
      } catch (err) {
        console.error('Failed to setup audio monitoring:', err);
      }

      //    
      audio.onloadedmetadata = () => {
        console.log('Audio metadata loaded for', userId, {
          duration: audio.duration,
          paused: audio.paused,
          volume: audio.volume,
          muted: audio.muted,
        });
      };

      audio.onplay = () => {
        console.log('Audio element started playing for', userId);
      };

      audio.onpause = () => {
        console.log('Audio paused for', userId);
      };

      //   DOM
      audio.style.display = 'none';
      document.body.appendChild(audio);

      //   
      audio.play().then(() => {
        console.log('Audio play() succeeded for user:', userId);
      }).catch((err) => {
        console.error('Failed to play audio:', err);
        const resumeAudio = () => {
          audio.play().then(() => console.log('Audio resumed on click for', userId));
          document.removeEventListener('click', resumeAudio);
        };
        document.addEventListener('click', resumeAudio);
      });

      this.audioElements.set(userId, audio);
    };

    pc.onconnectionstatechange = () => {
      const state = pc.connectionState;

      // Update state machine
      this.updatePeerState(userId, pc);

      if (state === 'failed') {
        debugLogger.error(`Peer ${userId.substring(0, 8)} connection failed`, `ICE: ${pc.iceConnectionState}`);
      } else if (state === 'connected') {
        debugLogger.success(`Peer ${userId.substring(0, 8)} `);
      } else if (state === 'closed') {
        // Clean up peer state
        this.peerStates.delete(userId);
      }
    };

    pc.oniceconnectionstatechange = () => {
      const iceState = pc.iceConnectionState;

      // Update state machine
      this.updatePeerState(userId, pc);

      if (iceState === 'connected' || iceState === 'completed') {
        debugLogger.success(`ICE  `, `peer: ${userId.substring(0, 8)}`);
        this.rtcConnectedPeers.add(userId);

        //   ICE restart  
        const timer = this.iceRestartTimers.get(userId);
        if (timer) {
          clearTimeout(timer);
          this.iceRestartTimers.delete(userId);
        }

        // Reset reconnect attempts on success
        const peerState = this.peerStates.get(userId);
        if (peerState) {
          peerState.reconnectAttempts = 0;
        }

        // State machine will update global status via updatePeerState
      } else if (iceState === 'checking') {
        // ICE is checking candidates
        debugLogger.webrtc(`Peer ${userId.substring(0, 8)} ICE checking...`);
      } else if (iceState === 'disconnected') {
        debugLogger.warning(`ICE   ${userId.substring(0, 8)}`, '  2');
        this.rtcConnectedPeers.delete(userId);

        // Increment reconnect attempts
        const peerState = this.peerStates.get(userId);
        if (peerState) {
          peerState.reconnectAttempts++;
        }

        //    ICE restart
        if (!this.iceRestartTimers.has(userId)) {
          const timer = window.setTimeout(() => {
            this.iceRestartTimers.delete(userId);
            this.performIceRestart(userId);
          }, 2000);
          this.iceRestartTimers.set(userId, timer);
        }
      } else if (iceState === 'failed') {
        debugLogger.error(`ICE failed  ${userId.substring(0, 8)}`, ' restart');
        this.rtcConnectedPeers.delete(userId);

        // Increment reconnect attempts
        const peerState = this.peerStates.get(userId);
        if (peerState) {
          peerState.reconnectAttempts++;
        }

        //  restart  failed
        this.performIceRestart(userId);
      }
    };

    pc.onicegatheringstatechange = () => {
      debugLogger.webrtc(`Peer ${userId.substring(0, 8)} ICE gathering: ${pc.iceGatheringState}`);
      this.updatePeerState(userId, pc);
    };

    pc.onsignalingstatechange = () => {
      debugLogger.webrtc(`Peer ${userId.substring(0, 8)} signaling: ${pc.signalingState}`);
      this.updatePeerState(userId, pc);
    };

    // Initialize peer state
    this.updatePeerState(userId, pc);

    //  offer   
    if (createOffer) {
      try {
        const offer = await pc.createOffer();
        await pc.setLocalDescription(offer);
        this.sendSignal({
          type: 'offer',
          userId: this.userId,
          targetUserId: userId,
          offer: offer,
        });
      } catch (error) {
        console.error('Failed to create offer:', error);
      }
    }
  }

  private async handleOffer(userId: string, offer: RTCSessionDescriptionInit) {
    let pc = this.peerConnections.get(userId);

    //        offer (glare situation)
    if (pc && pc.signalingState === 'have-local-offer') {
      // "Polite peer" (  ID)   offer   
      const isPolite = this.userId > userId;
      if (isPolite) {
        console.log(`Glare detected with ${userId}, rolling back as polite peer`);
        await pc.setLocalDescription({ type: 'rollback' });
      } else {
        // "Impolite peer"   offer
        console.log(`Glare detected with ${userId}, ignoring as impolite peer`);
        return;
      }
    }

    //   , 
    if (!pc) {
      await this.createPeerConnection(userId, false);
      pc = this.peerConnections.get(userId);
    }
    if (!pc) return;

    try {
      await pc.setRemoteDescription(new RTCSessionDescription(offer));
      const answer = await pc.createAnswer();
      await pc.setLocalDescription(answer);
      this.sendSignal({
        type: 'answer',
        userId: this.userId,
        targetUserId: userId,
        answer: answer,
      });
    } catch (error) {
      console.error('Failed to handle offer:', error);
    }
  }

  private async handleAnswer(userId: string, answer: RTCSessionDescriptionInit) {
    const pc = this.peerConnections.get(userId);
    if (!pc) return;

    //         answer
    if (pc.signalingState !== 'have-local-offer') {
      console.log(`Ignoring answer from ${userId}, signaling state is ${pc.signalingState}`);
      return;
    }

    try {
      await pc.setRemoteDescription(new RTCSessionDescription(answer));
    } catch (error) {
      console.error('Failed to handle answer:', error);
    }
  }

  private async handleIceCandidate(userId: string, candidate: RTCIceCandidateInit) {
    const pc = this.peerConnections.get(userId);
    if (!pc) return;

    try {
      await pc.addIceCandidate(new RTCIceCandidate(candidate));
    } catch (error) {
      console.error('Failed to add ICE candidate:', error);
    }
  }

  private async performIceRestart(userId: string) {
    const pc = this.peerConnections.get(userId);
    if (!pc) {
      console.log(`No peer connection for ${userId}, cannot restart ICE`);
      return;
    }

    //   
    if (pc.connectionState === 'closed') {
      console.log(`Connection to ${userId} is closed, recreating`);
      this.closePeerConnection(userId);
      const shouldCreateOffer = this.userId < userId;
      await this.createPeerConnection(userId, shouldCreateOffer);
      return;
    }

    console.log(`Performing ICE restart for ${userId}`);
    try {
      const offer = await pc.createOffer({ iceRestart: true });
      await pc.setLocalDescription(offer);
      this.sendSignal({
        type: 'offer',
        userId: this.userId,
        targetUserId: userId,
        offer: offer,
      });
    } catch (error) {
      console.error(`Failed to perform ICE restart for ${userId}:`, error);
      //    
      this.closePeerConnection(userId);
      const shouldCreateOffer = this.userId < userId;
      await this.createPeerConnection(userId, shouldCreateOffer);
    }
  }

  private handleMuteStatus(userId: string, isMuted: boolean) {
    const user = this.users.get(userId);
    if (user) {
      const wasChanged = user.isMuted !== isMuted;
      user.isMuted = isMuted;
      this.notifyUsersChange();
      if (wasChanged) {
        debugLogger.user(`${user.username} ${isMuted ? '' : ''} `);
      }
    }
  }

  private handleSpeakingStatus(userId: string, isSpeaking: boolean) {
    const user = this.users.get(userId);
    if (user) {
      user.isSpeaking = isSpeaking;
      this.notifyUsersChange();
    }
  }

  private handleDeafenedStatus(userId: string, isDeafened: boolean) {
    const user = this.users.get(userId);
    if (user) {
      const wasChanged = user.isDeafened !== isDeafened;
      user.isDeafened = isDeafened;
      this.notifyUsersChange();
      if (wasChanged) {
        debugLogger.user(`${user.username} ${isDeafened ? '' : ''} `);
      }
    }
  }

  private handleUserStatusUpdate(userId: string, isMuted: boolean, isDeafened: boolean) {
    const user = this.users.get(userId);
    if (user) {
      user.isMuted = isMuted;
      user.isDeafened = isDeafened;
      this.notifyUsersChange();
      debugLogger.user(`${user.username}  : muted=${isMuted}, deafened=${isDeafened}`);
    }
  }

  private closePeerConnection(userId: string) {
    const pc = this.peerConnections.get(userId);
    if (pc) {
      pc.close();
      this.peerConnections.delete(userId);
    }

    // Clean up peer state
    this.peerStates.delete(userId);
    this.rtcConnectedPeers.delete(userId);

    const audio = this.audioElements.get(userId);
    if (audio) {
      audio.pause();
      audio.srcObject = null;
      audio.remove(); //   DOM
      this.audioElements.delete(userId);
    }

    //  AudioContext   
    const gainNodeData = this.userGainNodes.get(userId);
    if (gainNodeData) {
      gainNodeData.context.close();
      this.userGainNodes.delete(userId);
    }

    // Update global connection status
    this.updateGlobalConnectionStatus();
  }

  private notifyUsersChange() {
    if (this.onUsersChange) {
      const usersList = Array.from(this.users.values());
      this.onUsersChange(usersList);
    }
  }

  private async startVoiceActivityDetection() {
    if (!this.localStream) return;

    try {
      //      (   noise gate)
      const audioTrack = this.localStream.getAudioTracks()[0];
      if (!audioTrack) {
        console.warn('No audio track for VAD');
        return;
      }

      console.log('Starting Voice Activity Detection...');

      console.log('Audio track for VAD:', audioTrack.label, 'enabled:', audioTrack.enabled, 'muted:', audioTrack.muted);

      //         
      //         Noise Gate
      const vadStream = await navigator.mediaDevices.getUserMedia({
        audio: {
          deviceId: audioTrack.getSettings().deviceId ? { exact: audioTrack.getSettings().deviceId } : undefined,
          echoCancellation: true,
          noiseSuppression: true,
        }
      });

      //  VAD stream  
      (this as any).vadStream = vadStream;

      this.audioContext = new AudioContext();

      //   AudioContext 
      if (this.audioContext.state === 'suspended') {
        await this.audioContext.resume();
        console.log('AudioContext resumed for VAD');
      }

      const source = this.audioContext.createMediaStreamSource(vadStream);
      this.analyser = this.audioContext.createAnalyser();
      this.analyser.fftSize = 512;
      this.analyser.smoothingTimeConstant = 0.3; //  
      source.connect(this.analyser);

      console.log('VAD started successfully with independent stream');

      const dataArray = new Uint8Array(this.analyser.frequencyBinCount);
      let gateCloseTimeout: number | null = null;
      let speakingOffTimeout: number | null = null;
      let lastSpeakingState = false;
      let isGateOpen = false;
      let debugCounter = 0;

      console.log('VAD interval starting, analyser bins:', this.analyser.frequencyBinCount);

      this.vadInterval = window.setInterval(() => {
        debugCounter++;

        //  
        const sensitivitySettingsStr = localStorage.getItem('sensitivitySettings');
        const sensitivitySettings = sensitivitySettingsStr
          ? JSON.parse(sensitivitySettingsStr)
          : { mode: 'auto', threshold: 25 };

        //   noise gate
        const noiseGateSetting = localStorage.getItem('noiseGate');
        const noiseGateEnabled = noiseGateSetting !== null ? JSON.parse(noiseGateSetting) : true;

        if (!this.analyser) {
          if (debugCounter % 100 === 0) console.log('VAD: no analyser');
          return;
        }

        if (this.isMuted) {
          if (lastSpeakingState) {
            lastSpeakingState = false;
            this.updateSpeakingStatus(false);
          }
          return;
        }

        this.analyser.getByteFrequencyData(dataArray);

        //   
        let sum = 0;
        let maxVal = 0;
        for (let i = 0; i < dataArray.length; i++) {
          sum += dataArray[i];
          if (dataArray[i] > maxVal) maxVal = dataArray[i];
        }
        const average = sum / dataArray.length;
        const normalizedLevel = Math.min(100, (average / 128) * 100);

        // Debug:    2  (40   50ms )
        if (debugCounter % 40 === 0) {
          console.log(`VAD: avg=${average.toFixed(1)}, max=${maxVal}, normalized=${normalizedLevel.toFixed(1)}, muted=${this.isMuted}`);
        }

        //  (   )
        const threshold = sensitivitySettings.mode === 'auto' ? 10 : sensitivitySettings.threshold;
        const isSpeaking = normalizedLevel > threshold;

        // Noise Gate -    ( )
        if (noiseGateEnabled && this.localStream) {
          const audioTracks = this.localStream.getAudioTracks();

          if (isSpeaking) {
            if (gateCloseTimeout) {
              clearTimeout(gateCloseTimeout);
              gateCloseTimeout = null;
            }
            if (!isGateOpen) {
              isGateOpen = true;
              audioTracks.forEach(track => {
                if (!this.isMuted) track.enabled = true;
              });
            }
          } else if (isGateOpen && !gateCloseTimeout) {
            gateCloseTimeout = window.setTimeout(() => {
              isGateOpen = false;
              audioTracks.forEach(track => track.enabled = false);
              gateCloseTimeout = null;
            }, 250);
          }
        } else if (!noiseGateEnabled && this.localStream && !isGateOpen) {
          isGateOpen = true;
          this.localStream.getAudioTracks().forEach(track => {
            if (!this.isMuted) track.enabled = true;
          });
        }

        //  speaking:  ,   500
        if (isSpeaking) {
          //     
          if (speakingOffTimeout) {
            clearTimeout(speakingOffTimeout);
            speakingOffTimeout = null;
          }
          if (!lastSpeakingState) {
            lastSpeakingState = true;
            this.updateSpeakingStatus(true);
          }
        } else if (lastSpeakingState && !speakingOffTimeout) {
          //    500
          speakingOffTimeout = window.setTimeout(() => {
            lastSpeakingState = false;
            this.updateSpeakingStatus(false);
            speakingOffTimeout = null;
          }, 500);
        }
      }, 50);
    } catch (error) {
      console.error('Failed to start VAD:', error);
    }
  }

  private stopVoiceActivityDetection() {
    if (this.vadInterval) {
      clearInterval(this.vadInterval);
      this.vadInterval = null;
    }
    if (this.audioContext) {
      this.audioContext.close();
      this.audioContext = null;
    }
    this.analyser = null;

    //   VAD stream
    const vadStream = (this as any).vadStream as MediaStream | undefined;
    if (vadStream) {
      vadStream.getTracks().forEach(track => track.stop());
      (this as any).vadStream = null;
    }
  }

  private setupVisibilityHandler() {
    this.visibilityHandler = () => {
      if (document.visibilityState === 'visible') {
        this.resumeAudioContexts();
      }
    };
    document.addEventListener('visibilitychange', this.visibilityHandler);
  }

  private removeVisibilityHandler() {
    if (this.visibilityHandler) {
      document.removeEventListener('visibilitychange', this.visibilityHandler);
      this.visibilityHandler = null;
    }
  }

  private async resumeAudioContexts() {
    debugLogger.info('     ');

    //   AudioContext  VAD
    if (this.audioContext && this.audioContext.state === 'suspended') {
      try {
        await this.audioContext.resume();
        debugLogger.info('VAD AudioContext ');
      } catch (e) {
        console.error('Failed to resume VAD AudioContext:', e);
      }
    }

    //       
    const entries = Array.from(this.userGainNodes.entries());
    for (let i = 0; i < entries.length; i++) {
      const [userId, gainNodeData] = entries[i];
      if (gainNodeData.context.state === 'suspended') {
        try {
          await gainNodeData.context.resume();
        } catch (e) {
          console.error(`Failed to resume AudioContext for user ${userId}:`, e);
        }
      }
    }

    //  WebSocket 
    if (this.ws && this.ws.readyState !== WebSocket.OPEN && this.channelId) {
      debugLogger.warning('WebSocket , ...');
      this.reconnectWebSocket();
    }
  }

  private updateSpeakingStatus(isSpeaking: boolean) {
    const user = this.users.get(this.userId);
    if (user && user.isSpeaking !== isSpeaking) {
      user.isSpeaking = isSpeaking;
      console.log(`Speaking status changed: ${isSpeaking}`);
      this.notifyUsersChange();

      //    
      this.sendSignal({
        type: 'speaking-status',
        userId: this.userId,
        isSpeaking: isSpeaking,
      });
    }
  }

  toggleMute(): { isMuted: boolean; wasUndeafened: boolean } {
    this.isMuted = !this.isMuted;
    let wasUndeafened = false;

    //      
    if (!this.isMuted && this.isDeafened) {
      this.setDeafened(false);
      wasUndeafened = true;
    }

    if (this.localStream) {
      this.localStream.getAudioTracks().forEach((track) => {
        track.enabled = !this.isMuted;
      });
    }

    debugLogger.info(` ${this.isMuted ? '' : ''} `);

    //   
    const user = this.users.get(this.userId);
    if (user) {
      user.isMuted = this.isMuted;
      //       speaking
      if (this.isMuted && user.isSpeaking) {
        user.isSpeaking = false;
        this.sendSignal({
          type: 'speaking-status',
          userId: this.userId,
          isSpeaking: false,
        });
      }
      this.notifyUsersChange();
    }

    //   
    this.sendSignal({
      type: 'mute-status',
      userId: this.userId,
      isMuted: this.isMuted,
    });

    return { isMuted: this.isMuted, wasUndeafened };
  }

  leaveChannel() {
    if (this.channelId) {
      debugLogger.user('   ');
    }

    //  screen share  
    if (this.screenShareState.isSharing && this.screenShareState.userId === this.userId) {
      this.stopScreenShare();
    }

    // Reset connection flags
    this.isConnecting = false;
    this.isConnected = false;
    this.reconnectAttempts = this.maxReconnectAttempts; //  

    //   reconnect
    if (this.reconnectTimeout) {
      clearTimeout(this.reconnectTimeout);
      this.reconnectTimeout = null;
    }

    //   ICE restart
    this.iceRestartTimers.forEach((timer) => clearTimeout(timer));
    this.iceRestartTimers.clear();

    //   
    this.stopPingInterval();

    //    
    this.sendSignal({ type: 'leave', userId: this.userId });

    //    
    this.stopVoiceActivityDetection();

    //     
    this.removeVisibilityHandler();

    //   
    this.peerConnections.forEach((pc, id) => {
      this.closePeerConnection(id);
    });
    this.peerConnections.clear();

    //   
    if (this.localStream) {
      this.localStream.getTracks().forEach((track) => track.stop());
      this.localStream = null;
    }

    //  WebSocket
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }

    //    
    this.users.clear();
    this.peerStates.clear();
    this.rtcConnectedPeers.clear();
    this.channelId = null;
    this.notifyUsersChange();
  }

  getIsMuted(): boolean {
    return this.isMuted;
  }

  setUserVolume(userId: string, volume: number): void {
    //    0  100
    const clampedVolume = Math.max(0, Math.min(100, volume));

    //  
    this.userVolumes.set(userId, clampedVolume);
    this.saveUserVolumes(); //   localStorage

    //  GainNode   (   )
    const gainNodeData = this.userGainNodes.get(userId);
    if (gainNodeData && !this.isDeafened) {
      gainNodeData.gainNode.gain.value = clampedVolume / 100;
    }

    //    
    const user = this.users.get(userId);
    if (user) {
      user.volume = clampedVolume;
      this.notifyUsersChange();
    }
  }

  getUserVolume(userId: string): number {
    return this.userVolumes.get(userId) ?? 100;
  }

  setDeafened(deafened: boolean): void {
    this.isDeafened = deafened;

    debugLogger.info(` ${deafened ? '' : ''} `);

    //      
    const user = this.users.get(this.userId);
    if (user) {
      user.isDeafened = deafened;
      this.notifyUsersChange();
    }

    //    
    this.sendSignal({
      type: 'deafened-status',
      userId: this.userId,
      isDeafened: deafened,
    });

    // /    
    this.userGainNodes.forEach((gainNodeData, odUserId) => {
      if (deafened) {
        gainNodeData.gainNode.gain.value = 0;
      } else {
        //     
        const volume = this.userVolumes.get(odUserId) ?? 100;
        gainNodeData.gainNode.gain.value = volume / 100;
      }
    });
  }

  getIsDeafened(): boolean {
    return this.isDeafened;
  }

  // ==================== Screen Sharing ====================

  setScreenShareCallback(callback: (state: ScreenShareState) => void): void {
    this.onScreenShareChange = callback;
  }

  setScreenShareSettings(settings: Partial<ScreenShareSettings>): void {
    this.screenShareSettings = { ...this.screenShareSettings, ...settings };
  }

  getScreenShareSettings(): ScreenShareSettings {
    return { ...this.screenShareSettings };
  }

  getScreenShareState(): ScreenShareState {
    return { ...this.screenShareState };
  }

  getDebugInfo(): {
    peersCount: number;
    peers: Array<{
      id: string;
      connectionState: string;
      iceState: string;
      signalingState: string;
    }>;
    screenShareState: {
      isSharing: boolean;
      isLocalShare: boolean;
      userId: string | null;
      hasStream: boolean;
    };
    localStreamActive: boolean;
    audioTracksCount: number;
  } {
    const peers = Array.from(this.peerConnections.entries()).map(([id, pc]) => ({
      id,
      connectionState: pc.connectionState,
      iceState: pc.iceConnectionState,
      signalingState: pc.signalingState,
    }));

    return {
      peersCount: this.peerConnections.size,
      peers,
      screenShareState: {
        isSharing: this.screenShareState.isSharing,
        isLocalShare: this.screenShareState.isLocalShare,
        userId: this.screenShareState.userId,
        hasStream: !!this.screenShareState.stream,
      },
      localStreamActive: this.localStream?.active ?? false,
      audioTracksCount: this.audioElements.size,
    };
  }

  async startScreenShare(settings?: ScreenShareSettings): Promise<void> {
    //    
    if (settings) {
      this.setScreenShareSettings(settings);
    }

    //     - 
    if (this.screenShareState.isSharing && this.screenShareState.userId !== this.userId) {
      debugLogger.warning('Screen share ', `${this.screenShareState.username}  `);
      throw new Error(`${this.screenShareState.username}   `);
    }

    if (this.screenShareState.isSharing && this.screenShareState.userId === this.userId) {
      debugLogger.info('Screen share  ');
      return;
    }

    const resolution = RESOLUTION_PRESETS[this.screenShareSettings.resolution];
    this.currentBitrate = resolution.bitrate;

    try {
      debugLogger.screenshare(' screen share...', `${this.screenShareSettings.resolution}, ${this.screenShareSettings.frameRate}fps`);

      this.screenStream = await navigator.mediaDevices.getDisplayMedia({
        video: {
          width: { ideal: resolution.width },
          height: { ideal: resolution.height },
          frameRate: { ideal: this.screenShareSettings.frameRate },
        } as MediaTrackConstraints,
        audio: this.screenShareSettings.includeAudio,
      });

      //   
      const videoTracks = this.screenStream.getVideoTracks();
      const audioTracks = this.screenStream.getAudioTracks();
      debugLogger.screenshare('Screen share ', `video: ${videoTracks.length}, audio: ${audioTracks.length}`);

      //      
      this.screenStream.getVideoTracks()[0].onended = () => {
        debugLogger.screenshare('Screen share  ');
        this.stopScreenShare();
      };

      //       
      const hasAudio = audioTracks.length > 0;
      let audioWarning: string | null = null;

      if (hasAudio) {
        debugLogger.screenshare('  ');
      } else if (this.screenShareSettings.includeAudio) {
        //    ,     
        const isLinux = navigator.userAgent.toLowerCase().includes('linux');
        const isMac = navigator.userAgent.toLowerCase().includes('mac');

        if (isLinux) {
          audioWarning = ' Linux      ';
          debugLogger.warning(' ', audioWarning);
        } else if (isMac) {
          audioWarning = ' macOS    ';
          debugLogger.warning(' ', audioWarning);
        } else {
          audioWarning = '     ';
          debugLogger.info('  ', '   "Share audio"');
        }
      }

      //    
      const user = this.users.get(this.userId);
      if (user) {
        user.isScreenSharing = true;
        this.notifyUsersChange();
      }

      this.screenShareState = {
        isSharing: true,
        isLocalShare: true,
        userId: this.userId,
        username: this.username,
        stream: this.screenStream,
        hasAudio,
        audioWarning,
      };

      this.onScreenShareChange?.(this.screenShareState);

      //       
      //      
      this.sendSignal({
        type: 'screen-share-start',
        userId: this.userId,
        username: this.username,
      });

      //       
      await new Promise(resolve => setTimeout(resolve, 300));

      //      peer connections
      await this.addScreenTrackToPeers();

      //     
      if (this.screenShareSettings.adaptiveBitrate) {
        this.startBitrateMonitoring();
      }

      //    
      this.startScreenShareVerification();

      debugLogger.success('Screen share ');
    } catch (error: unknown) {
      const errorMessage = error instanceof Error ? error.message : String(error);
      debugLogger.error(' screen share', errorMessage);
      if (isErrorWithName(error) && error.name === 'NotAllowedError') {
        throw new Error('   ');
      }
      throw error;
    }
  }

  async stopScreenShare(): Promise<void> {
    if (!this.screenShareState.isSharing || this.screenShareState.userId !== this.userId) {
      return;
    }

    debugLogger.screenshare(' screen share...');

    //     
    this.stopBitrateMonitoring();
    this.stopScreenShareVerification();

    //    peer connections  
    await this.removeScreenTrackFromPeers();

    //   
    if (this.screenStream) {
      this.screenStream.getTracks().forEach(track => track.stop());
      this.screenStream = null;
    }

    //    
    this.sendSignal({
      type: 'screen-share-stop',
      userId: this.userId,
    });

    //   
    const user = this.users.get(this.userId);
    if (user) {
      user.isScreenSharing = false;
      this.notifyUsersChange();
    }

    this.screenShareState = {
      isSharing: false,
      isLocalShare: false,
      userId: null,
      username: null,
      stream: null,
    };

    this.onScreenShareChange?.(this.screenShareState);
    debugLogger.screenshare('Screen share ');
  }

  private handleScreenShareStart(userId: string, username: string): void {
    debugLogger.screenshare(` screen-share-start  ${username}`, `userId: ${userId.substring(0, 8)}`);
    console.log('Pending streams:', Array.from(this.pendingScreenStreams.keys()));
    console.log('Current screenShareState:', {
      isSharing: this.screenShareState.isSharing,
      userId: this.screenShareState.userId,
      hasStream: !!this.screenShareState.stream,
    });

    //   
    const user = this.users.get(userId);
    if (user) {
      user.isScreenSharing = true;
      this.notifyUsersChange();
    }

    //     -  
    if (userId !== this.userId) {
      //      
      const pendingStream = this.pendingScreenStreams.get(userId);

      // :    stream!
      //     stream   userId -  
      const existingStream = (this.screenShareState.userId === userId && this.screenShareState.stream)
        ? this.screenShareState.stream
        : null;

      const streamToUse = pendingStream || existingStream || null;

      console.log('Stream selection:', {
        hasPending: !!pendingStream,
        hasExisting: !!existingStream,
        willUse: !!streamToUse,
      });

      this.screenShareState = {
        isSharing: true,
        isLocalShare: false,
        userId: userId,
        username: username,
        stream: streamToUse,
      };

      //  pending 
      if (pendingStream) {
        this.pendingScreenStreams.delete(userId);
        console.log('Using pending screen stream, active:', pendingStream.active);
      } else if (!streamToUse) {
        console.log('No stream available, waiting for video track...');
        //    -    
        this.sendSignal({
          type: 'request-screen-share',
          userId: this.userId,
          targetUserId: userId,
        });
      }

      console.log('Calling onScreenShareChange with state:', {
        isSharing: this.screenShareState.isSharing,
        userId: this.screenShareState.userId,
        hasStream: !!this.screenShareState.stream,
      });
      this.onScreenShareChange?.(this.screenShareState);
    }
  }

  //     screen share  
  private async handleRequestScreenShare(requesterId: string): Promise<void> {
    console.log('=== REQUEST SCREEN SHARE ===');
    console.log('Requester:', requesterId);
    console.log('Am I sharing?', this.screenShareState.isSharing && this.screenShareState.userId === this.userId);

    //     - 
    if (!this.screenShareState.isSharing || this.screenShareState.userId !== this.userId || !this.screenStream) {
      console.log('Not sharing, ignoring request');
      return;
    }

    console.log('Sending screen share to requester:', requesterId);

    //    
    await this.addScreenTrackToNewPeer(requesterId);
  }

  private handleScreenShareStop(userId: string): void {
    console.log('=== SCREEN SHARE STOP ===');
    console.log('userId:', userId);
    console.log('Current screenShareState.userId:', this.screenShareState.userId);

    //  pending 
    this.pendingScreenStreams.delete(userId);

    //   screen share 
    const screenAudioKey = `${userId}-screen`;
    const screenAudio = this.audioElements.get(screenAudioKey);
    if (screenAudio) {
      screenAudio.pause();
      screenAudio.srcObject = null;
      screenAudio.remove();
      this.audioElements.delete(screenAudioKey);
    }

    //   
    const user = this.users.get(userId);
    if (user) {
      user.isScreenSharing = false;
      this.notifyUsersChange();
    }

    //     
    if (this.screenShareState.userId === userId) {
      console.log('Clearing screen share state');

      this.screenShareState = {
        isSharing: false,
        isLocalShare: false,
        userId: null,
        username: null,
        stream: null,
      };

      this.onScreenShareChange?.(this.screenShareState);
    }
  }

  private async addScreenTrackToNewPeer(peerId: string): Promise<void> {
    if (!this.screenStream) {
      console.log('No screen stream to add to new peer');
      return;
    }

    const pc = this.peerConnections.get(peerId);
    if (!pc) {
      console.log(`No peer connection for ${peerId}`);
      return;
    }

    const videoTrack = this.screenStream.getVideoTracks()[0];
    const audioTrack = this.screenStream.getAudioTracks()[0];

    console.log(`=== ADDING SCREEN TRACKS TO NEW PEER ${peerId} ===`);

    try {
      //   
      const videoSender = pc.addTrack(videoTrack, this.screenStream);
      this.screenSenders.set(`${peerId}-video`, videoSender);

      //   
      const params = videoSender.getParameters();
      if (!params.encodings || params.encodings.length === 0) {
        params.encodings = [{}];
      }
      params.encodings[0].maxBitrate = this.currentBitrate;
      await videoSender.setParameters(params);

      //     
      if (audioTrack) {
        const audioSender = pc.addTrack(audioTrack, this.screenStream);
        this.screenSenders.set(`${peerId}-audio`, audioSender);

        try {
          const audioParams = audioSender.getParameters();
          if (!audioParams.encodings || audioParams.encodings.length === 0) {
            audioParams.encodings = [{}];
          }
          audioParams.encodings[0].maxBitrate = 128000;
          await audioSender.setParameters(audioParams);
        } catch (err) {
          console.error('Failed to set audio bitrate:', err);
        }
      }

      //    retry
      await this.renegotiateConnectionWithRetry(peerId, 3);
      console.log(`Screen tracks added to new peer ${peerId}`);
    } catch (error) {
      console.error(`Failed to add screen tracks to new peer ${peerId}:`, error);
    }
  }

  private async addScreenTrackToPeers(): Promise<void> {
    if (!this.screenStream) {
      console.log('No screen stream to add');
      return;
    }

    const videoTrack = this.screenStream.getVideoTracks()[0];
    const audioTrack = this.screenStream.getAudioTracks()[0];

    console.log('=== ADDING SCREEN TRACKS TO PEERS ===');
    console.log('Video track:', videoTrack?.id, 'enabled:', videoTrack?.enabled);
    console.log('Audio track:', audioTrack?.id);

    const peers = Array.from(this.peerConnections.entries());
    console.log('Number of peer connections:', peers.length);

    //  1:        
    const peersToRenegotiate: string[] = [];

    for (let i = 0; i < peers.length; i++) {
      const [peerId, pc] = peers[i];
      console.log(`Adding tracks to peer ${peerId}, connection state: ${pc.connectionState}`);
      try {
        //   
        const videoSender = pc.addTrack(videoTrack, this.screenStream);
        this.screenSenders.set(`${peerId}-video`, videoSender);
        console.log(`Video track added to peer ${peerId}`);

        //      
        const params = videoSender.getParameters();
        if (!params.encodings || params.encodings.length === 0) {
          params.encodings = [{}];
        }
        params.encodings[0].maxBitrate = this.currentBitrate;
        await videoSender.setParameters(params);

        //     
        if (audioTrack) {
          const audioSender = pc.addTrack(audioTrack, this.screenStream);
          this.screenSenders.set(`${peerId}-audio`, audioSender);

          //      
          try {
            const audioParams = audioSender.getParameters();
            if (!audioParams.encodings || audioParams.encodings.length === 0) {
              audioParams.encodings = [{}];
            }
            //      (128 kbps)
            audioParams.encodings[0].maxBitrate = 128000;
            await audioSender.setParameters(audioParams);
            console.log(`Audio bitrate set to 128 kbps for peer ${peerId}`);
          } catch (err) {
            console.error('Failed to set audio bitrate:', err);
          }
        }

        peersToRenegotiate.push(peerId);
        console.log(`Added screen tracks to peer ${peerId}`);
      } catch (error) {
        console.error(`Failed to add screen tracks to peer ${peerId}:`, error);
      }
    }

    //  2:      retry
    console.log(`Starting parallel renegotiation for ${peersToRenegotiate.length} peers`);
    const renegotiatePromises = peersToRenegotiate.map(peerId =>
      this.renegotiateConnectionWithRetry(peerId, 3)
    );

    await Promise.all(renegotiatePromises);
    console.log('All renegotiations completed');
  }

  private async removeScreenTrackFromPeers(): Promise<void> {
    console.log('=== REMOVING SCREEN TRACKS FROM PEERS ===');
    const peersToRenegotiate = new Set<string>();

    const senders = Array.from(this.screenSenders.entries());
    for (let i = 0; i < senders.length; i++) {
      const [key, sender] = senders[i];
      const peerId = key.split('-')[0];
      const pc = this.peerConnections.get(peerId);
      if (pc) {
        try {
          pc.removeTrack(sender);
          peersToRenegotiate.add(peerId);
          console.log(`Removed track from peer ${peerId}`);
        } catch (error) {
          console.error(`Failed to remove track from peer ${peerId}:`, error);
        }
      }
    }
    this.screenSenders.clear();

    //        
    const peers = Array.from(peersToRenegotiate);
    for (let i = 0; i < peers.length; i++) {
      await this.renegotiateConnection(peers[i]);
    }
  }

  private async renegotiateConnectionWithRetry(peerId: string, maxRetries: number): Promise<boolean> {
    for (let attempt = 1; attempt <= maxRetries; attempt++) {
      console.log(`Renegotiation attempt ${attempt}/${maxRetries} for peer ${peerId}`);
      const success = await this.renegotiateConnection(peerId);
      if (success) {
        console.log(`Renegotiation successful for peer ${peerId}`);
        return true;
      }

      //   retry   
      if (attempt < maxRetries) {
        const delay = 500 * attempt; // 500ms, 1000ms, 1500ms...
        console.log(`Retry in ${delay}ms for peer ${peerId}`);
        await new Promise(resolve => setTimeout(resolve, delay));
      }
    }

    console.warn(`All renegotiation attempts failed for peer ${peerId}`);
    return false;
  }

  private async renegotiateConnection(peerId: string): Promise<boolean> {
    const pc = this.peerConnections.get(peerId);
    if (!pc) {
      console.log(`No peer connection for ${peerId}`);
      return false;
    }

    //   
    if (pc.connectionState === 'closed' || pc.connectionState === 'failed') {
      console.log(`Peer ${peerId} connection is ${pc.connectionState}, cannot renegotiate`);
      return false;
    }

    //       
    const waitForStable = async (): Promise<boolean> => {
      //   3  ( )
      for (let i = 0; i < 30; i++) {
        const state = pc.signalingState;
        if (state === 'stable') return true;
        if (state === 'closed') return false;
        await new Promise(resolve => setTimeout(resolve, 100));
      }
      return pc.signalingState === 'stable';
    };

    try {
      console.log(`Renegotiating connection with ${peerId}, signaling: ${pc.signalingState}, connection: ${pc.connectionState}`);

      //   
      const isStable = await waitForStable();
      if (!isStable) {
        console.log(`Cannot renegotiate with ${peerId}, signaling state is ${pc.signalingState}`);
        return false;
      }

      const offer = await pc.createOffer();
      await pc.setLocalDescription(offer);
      this.sendSignal({
        type: 'offer',
        userId: this.userId,
        targetUserId: peerId,
        offer: offer,
      });
      console.log(`Renegotiation offer sent to ${peerId}`);
      return true;
    } catch (error) {
      console.error(`Failed to renegotiate with ${peerId}:`, error);
      return false;
    }
  }

  private startBitrateMonitoring(): void {
    if (this.bitrateMonitorInterval) return;

    this.bitrateMonitorInterval = window.setInterval(async () => {
      const peers = Array.from(this.peerConnections.entries());
      for (let i = 0; i < peers.length; i++) {
        const [peerId, pc] = peers[i];
        try {
          const stats = await pc.getStats();

          stats.forEach((report) => {
            if (report.type === 'outbound-rtp' && report.kind === 'video') {
              const rtpReport = report as RTCOutboundRtpStats;
              const packetsSent = rtpReport.packetsSent || 0;
              const packetsLost = rtpReport.packetsLost || 0;
              const packetLoss = packetsSent > 0 ? packetsLost / packetsSent : 0;

              //      
              if (packetLoss > 0.05) {
                //  5%  -  
                this.adjustBitrate(0.7);
              } else if (packetLoss < 0.01 && packetsSent > 100) {
                //  1%     -  
                this.adjustBitrate(1.1);
              }
            }
          });
        } catch (error) {
          console.error(`Failed to get stats for peer ${peerId}:`, error);
        }
      }
    }, 3000); //   3 
  }

  private stopBitrateMonitoring(): void {
    if (this.bitrateMonitorInterval) {
      clearInterval(this.bitrateMonitorInterval);
      this.bitrateMonitorInterval = null;
    }
  }

  private async adjustBitrate(multiplier: number): Promise<void> {
    const MIN_BITRATE = 300000;   // 300 Kbps
    const MAX_BITRATE = 4000000;  // 4 Mbps

    const newBitrate = Math.min(
      MAX_BITRATE,
      Math.max(MIN_BITRATE, this.currentBitrate * multiplier)
    );

    //     
    if (Math.abs(newBitrate - this.currentBitrate) < 100000) return;

    this.currentBitrate = newBitrate;
    console.log(`Adjusting bitrate to ${Math.round(this.currentBitrate / 1000)} Kbps`);

    //   video senders
    const senders = Array.from(this.screenSenders.entries());
    for (let i = 0; i < senders.length; i++) {
      const [key, sender] = senders[i];
      if (key.endsWith('-video')) {
        try {
          const params = sender.getParameters();
          if (params.encodings && params.encodings[0]) {
            params.encodings[0].maxBitrate = this.currentBitrate;
            await sender.setParameters(params);
          }
        } catch (error) {
          console.error('Failed to adjust bitrate:', error);
        }
      }
    }
  }

  //     screen share  
  private startScreenShareVerification(): void {
    if (this.screenShareVerifyInterval) return;

    this.screenShareVerifyAttempts = 0;
    const MAX_VERIFY_ATTEMPTS = 5; //  5 

    this.screenShareVerifyInterval = window.setInterval(async () => {
      this.screenShareVerifyAttempts++;

      if (this.screenShareVerifyAttempts > MAX_VERIFY_ATTEMPTS) {
        this.stopScreenShareVerification();
        return;
      }

      if (!this.screenShareState.isSharing || this.screenShareState.userId !== this.userId || !this.screenStream) {
        this.stopScreenShareVerification();
        return;
      }

      console.log(`Screen share verification attempt ${this.screenShareVerifyAttempts}/${MAX_VERIFY_ATTEMPTS}`);

      //     
      this.sendSignal({
        type: 'screen-share-start',
        userId: this.userId,
        username: this.username,
      });

      //   peer -     sender', 
      //        mute  
      const peers = Array.from(this.peerConnections.entries());
      for (let i = 0; i < peers.length; i++) {
        const [peerId, pc] = peers[i];

        //     video sender   
        const existingSender = this.screenSenders.get(`${peerId}-video`);
        if (!existingSender) {
          console.log(`Missing screen sender for peer ${peerId}, adding...`);
          await this.addScreenTrackToNewPeer(peerId);
        } else {
          //   sender     peer connection
          const senders = pc.getSenders();
          const isActive = senders.includes(existingSender);
          if (!isActive) {
            console.log(`Screen sender inactive for peer ${peerId}, re-adding...`);
            this.screenSenders.delete(`${peerId}-video`);
            this.screenSenders.delete(`${peerId}-audio`);
            await this.addScreenTrackToNewPeer(peerId);
          }
          //  sender  -  ,  
        }
      }
    }, 3000); //  3 
  }

  private stopScreenShareVerification(): void {
    if (this.screenShareVerifyInterval) {
      clearInterval(this.screenShareVerifyInterval);
      this.screenShareVerifyInterval = null;
    }
    this.screenShareVerifyAttempts = 0;
  }

  // ==================== Encoding Parameters ====================

  setEncodingParams(params: Partial<EncodingParams>): void {
    if (params.audio) {
      this.encodingParams.audio = { ...this.encodingParams.audio, ...params.audio };
    }
    if (params.video) {
      this.encodingParams.video = { ...this.encodingParams.video, ...params.video };
    }
    debugLogger.info('Encoding params updated', JSON.stringify(this.encodingParams));

    // Apply to existing senders
    this.applyEncodingParamsToAllPeers();
  }

  getEncodingParams(): EncodingParams {
    return { ...this.encodingParams };
  }

  private async applyEncodingParamsToAllPeers(): Promise<void> {
    const peers = Array.from(this.peerConnections.entries());

    for (const [peerId, pc] of peers) {
      const senders = pc.getSenders();

      for (const sender of senders) {
        if (!sender.track) continue;

        try {
          const params = sender.getParameters();
          if (!params.encodings || params.encodings.length === 0) {
            params.encodings = [{}];
          }

          if (sender.track.kind === 'audio') {
            // Apply audio encoding params
            params.encodings[0].maxBitrate = this.encodingParams.audio.maxBitrate;
            // Note: DTX and FEC are typically negotiated via SDP, not encoding params
          } else if (sender.track.kind === 'video') {
            // Apply video encoding params
            params.encodings[0].maxBitrate = this.encodingParams.video.maxBitrate;
            params.encodings[0].maxFramerate = this.encodingParams.video.maxFramerate;
            params.encodings[0].scaleResolutionDownBy = 1; // No downscaling by default
            // Priority affects packet scheduling
            (params.encodings[0] as any).priority = this.encodingParams.video.priority;
            (params.encodings[0] as any).networkPriority = this.encodingParams.video.priority;
          }

          await sender.setParameters(params);
          debugLogger.webrtc(`Encoding params applied to ${sender.track.kind} sender for peer ${peerId.substring(0, 8)}`);
        } catch (error) {
          console.error(`Failed to apply encoding params to peer ${peerId}:`, error);
        }
      }
    }
  }

  // Apply encoding params when adding new track
  private async applyEncodingToSender(sender: RTCRtpSender): Promise<void> {
    if (!sender.track) return;

    try {
      const params = sender.getParameters();
      if (!params.encodings || params.encodings.length === 0) {
        params.encodings = [{}];
      }

      if (sender.track.kind === 'audio') {
        params.encodings[0].maxBitrate = this.encodingParams.audio.maxBitrate;
      } else if (sender.track.kind === 'video') {
        params.encodings[0].maxBitrate = this.encodingParams.video.maxBitrate;
        params.encodings[0].maxFramerate = this.encodingParams.video.maxFramerate;
        (params.encodings[0] as any).priority = this.encodingParams.video.priority;
      }

      await sender.setParameters(params);
    } catch (error) {
      console.error('Failed to apply encoding to sender:', error);
    }
  }

  // ==================== Connection State Machine ====================

  private updatePeerState(peerId: string, pc: RTCPeerConnection): void {
    const state: PeerConnectionState = {
      peerId,
      connectionState: pc.connectionState,
      iceConnectionState: pc.iceConnectionState,
      iceGatheringState: pc.iceGatheringState,
      signalingState: pc.signalingState,
      lastStateChange: Date.now(),
      reconnectAttempts: this.peerStates.get(peerId)?.reconnectAttempts || 0,
    };

    const prevState = this.peerStates.get(peerId);
    this.peerStates.set(peerId, state);

    // Log state transitions
    if (prevState) {
      if (prevState.connectionState !== state.connectionState) {
        debugLogger.webrtc(`Peer ${peerId.substring(0, 8)} connection: ${prevState.connectionState}  ${state.connectionState}`);
      }
      if (prevState.iceConnectionState !== state.iceConnectionState) {
        debugLogger.webrtc(`Peer ${peerId.substring(0, 8)} ICE: ${prevState.iceConnectionState}  ${state.iceConnectionState}`);
      }
    }

    // Update global connection status based on peer states
    this.updateGlobalConnectionStatus();
  }

  private updateGlobalConnectionStatus(): void {
    const states = Array.from(this.peerStates.values());

    if (states.length === 0) {
      // No peers, but we might be connected to WS
      if (this.ws && this.ws.readyState === WebSocket.OPEN) {
        this.setConnectionStatus('connected');
      }
      return;
    }

    // Count connection states
    const connected = states.filter(s => s.connectionState === 'connected').length;
    const connecting = states.filter(s =>
      s.connectionState === 'connecting' ||
      s.connectionState === 'new' ||
      s.iceConnectionState === 'checking'
    ).length;
    const failed = states.filter(s =>
      s.connectionState === 'failed' ||
      s.iceConnectionState === 'failed'
    ).length;
    const disconnected = states.filter(s =>
      s.connectionState === 'disconnected' ||
      s.iceConnectionState === 'disconnected'
    ).length;

    // Determine overall status
    if (connected > 0) {
      this.setConnectionStatus('connected');
    } else if (connecting > 0 && failed === 0) {
      this.setConnectionStatus('connecting_rtc');
    } else if (failed > 0 || disconnected === states.length) {
      this.setConnectionStatus('reconnecting');
    }
  }

  // Get detailed peer connection states
  getPeerStates(): PeerConnectionState[] {
    return Array.from(this.peerStates.values());
  }

  // Get connection summary
  getConnectionSummary(): {
    status: ConnectionStatus;
    totalPeers: number;
    connectedPeers: number;
    heartbeat: { seq: number; missed: number; ping: number };
  } {
    const states = Array.from(this.peerStates.values());
    return {
      status: this.connectionStatus,
      totalPeers: states.length,
      connectedPeers: states.filter(s => s.connectionState === 'connected').length,
      heartbeat: {
        seq: this.heartbeatSeq,
        missed: this.missedHeartbeats,
        ping: this.currentPing,
      },
    };
  }
}
