import React, { useState, useEffect, useRef } from 'react';
import { User, VoiceCallState } from '../types';
import { endCall } from '../services/dmApi';
import { getAvatarColor } from '../utils/avatarColors';

interface DMVoiceCallProps {
  call: VoiceCallState;
  otherUser: User | null;
  currentUser: User;
  channelId: string;
  onCallEnd: () => void;
}

const DMVoiceCall: React.FC<DMVoiceCallProps> = ({
  call,
  otherUser,
  currentUser,
  channelId,
  onCallEnd,
}) => {
  const [isMuted, setIsMuted] = useState(false);
  const [isDeafened, setIsDeafened] = useState(false);
  const [callDuration, setCallDuration] = useState(0);
  const [isSpeaking, setIsSpeaking] = useState(false);
  const localStreamRef = useRef<MediaStream | null>(null);
  const audioContextRef = useRef<AudioContext | null>(null);
  const analyserRef = useRef<AnalyserNode | null>(null);

  const avatarColor = otherUser ? getAvatarColor(otherUser.id) : '#666';

  useEffect(() => {
    // Start duration timer
    const timer = setInterval(() => {
      setCallDuration((prev) => prev + 1);
    }, 1000);

    // Setup audio
    setupAudio();

    return () => {
      clearInterval(timer);
      cleanup();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const setupAudio = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      localStreamRef.current = stream;

      // Setup audio context for speaking detection
      audioContextRef.current = new AudioContext();
      const source = audioContextRef.current.createMediaStreamSource(stream);
      analyserRef.current = audioContextRef.current.createAnalyser();
      analyserRef.current.fftSize = 256;
      source.connect(analyserRef.current);

      // Start speaking detection
      detectSpeaking();
    } catch (error) {
      console.error('Failed to get audio stream:', error);
    }
  };

  const detectSpeaking = () => {
    if (!analyserRef.current) return;

    const bufferLength = analyserRef.current.frequencyBinCount;
    const dataArray = new Uint8Array(bufferLength);

    const check = () => {
      if (!analyserRef.current) return;

      analyserRef.current.getByteFrequencyData(dataArray);
      const average = dataArray.reduce((a, b) => a + b) / bufferLength;
      setIsSpeaking(average > 30);

      requestAnimationFrame(check);
    };

    check();
  };

  const cleanup = () => {
    if (localStreamRef.current) {
      localStreamRef.current.getTracks().forEach((track) => track.stop());
    }
    if (audioContextRef.current) {
      audioContextRef.current.close();
    }
  };

  const toggleMute = () => {
    if (localStreamRef.current) {
      localStreamRef.current.getAudioTracks().forEach((track) => {
        track.enabled = isMuted;
      });
    }
    setIsMuted(!isMuted);
  };

  const toggleDeafen = () => {
    setIsDeafened(!isDeafened);
    // In a real implementation, this would mute incoming audio
  };

  const handleEndCall = async () => {
    try {
      await endCall(channelId);
      onCallEnd();
    } catch (error) {
      console.error('Failed to end call:', error);
      onCallEnd();
    }
  };

  const formatDuration = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  return (
    <div
      style={{
        position: 'fixed',
        inset: 0,
        zIndex: 1500,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        background: 'linear-gradient(180deg, var(--bg-deep) 0%, var(--bg-primary) 100%)',
      }}
    >
      {/* Header with minimize button */}
      <div
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          padding: 16,
          display: 'flex',
          justifyContent: 'flex-end',
        }}
      >
        <button
          onClick={() => {
            // In a real app, this would minimize to a small floating widget
            // For now, we just keep the call going
          }}
          style={{
            width: 40,
            height: 40,
            borderRadius: '50%',
            background: 'var(--bg-tertiary)',
            border: 'none',
            color: 'var(--text-secondary)',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
          title="Minimize"
        >
          <svg width="20" height="20" viewBox="0 0 24 24" fill="none">
            <path d="M8 3v3a2 2 0 01-2 2H3M21 8h-3a2 2 0 01-2-2V3M3 16h3a2 2 0 012 2v3M16 21v-3a2 2 0 012-2h3" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </button>
      </div>

      {/* Main content */}
      <div style={{ textAlign: 'center' }}>
        {/* Other user avatar */}
        <div
          style={{
            width: 120,
            height: 120,
            borderRadius: '50%',
            background: otherUser?.avatar_url
              ? `url(${otherUser.avatar_url}) center/cover`
              : avatarColor,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: 'white',
            fontWeight: 700,
            fontSize: 42,
            margin: '0 auto 24px',
            boxShadow: isSpeaking ? '0 0 0 4px var(--primary-glow)' : 'none',
            transition: 'box-shadow 0.2s',
          }}
        >
          {!otherUser?.avatar_url && otherUser?.username[0].toUpperCase()}
        </div>

        {/* Name */}
        <h2
          style={{
            fontSize: 28,
            fontWeight: 600,
            color: 'var(--text-primary)',
            margin: 0,
          }}
        >
          {otherUser?.username || 'Unknown'}
        </h2>

        {/* Call duration */}
        <p
          style={{
            fontSize: 16,
            color: 'var(--text-secondary)',
            margin: '8px 0 0',
          }}
        >
          {formatDuration(callDuration)}
        </p>
      </div>

      {/* Controls */}
      <div
        style={{
          position: 'absolute',
          bottom: 40,
          display: 'flex',
          gap: 16,
          alignItems: 'center',
        }}
      >
        {/* Mute button */}
        <button
          onClick={toggleMute}
          style={{
            width: 56,
            height: 56,
            borderRadius: '50%',
            background: isMuted ? 'var(--danger)' : 'var(--bg-tertiary)',
            border: 'none',
            color: isMuted ? 'white' : 'var(--text-primary)',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.2s',
          }}
          title={isMuted ? 'Unmute' : 'Mute'}
        >
          {isMuted ? (
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
              <line x1="1" y1="1" x2="23" y2="23" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
              <path d="M9 9v3a3 3 0 005.12 2.12M15 9.34V4a3 3 0 00-5.94-.6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              <path d="M17 16.95A7 7 0 015 12v-2m14 0v2a7 7 0 01-.11 1.23" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              <line x1="12" y1="19" x2="12" y2="23" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              <line x1="8" y1="23" x2="16" y2="23" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          ) : (
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
              <path d="M12 1a3 3 0 00-3 3v8a3 3 0 006 0V4a3 3 0 00-3-3z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              <path d="M19 10v2a7 7 0 01-14 0v-2" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              <line x1="12" y1="19" x2="12" y2="23" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              <line x1="8" y1="23" x2="16" y2="23" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          )}
        </button>

        {/* Deafen button */}
        <button
          onClick={toggleDeafen}
          style={{
            width: 56,
            height: 56,
            borderRadius: '50%',
            background: isDeafened ? 'var(--danger)' : 'var(--bg-tertiary)',
            border: 'none',
            color: isDeafened ? 'white' : 'var(--text-primary)',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.2s',
          }}
          title={isDeafened ? 'Undeafen' : 'Deafen'}
        >
          {isDeafened ? (
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
              <line x1="1" y1="1" x2="23" y2="23" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
              <path d="M9 9a3 3 0 015.12 2.12M15 9.34V4a3 3 0 00-5.94-.6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              <path d="M23 12a11 11 0 01-11 11C6.477 23 1 17.523 1 12S6.477 1 12 1a10.96 10.96 0 016.29 1.97" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          ) : (
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
              <path d="M3 18v-6a9 9 0 0118 0v6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              <path d="M21 19a2 2 0 01-2 2h-1a2 2 0 01-2-2v-3a2 2 0 012-2h3v5zM3 19a2 2 0 002 2h1a2 2 0 002-2v-3a2 2 0 00-2-2H3v5z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          )}
        </button>

        {/* End call button */}
        <button
          onClick={handleEndCall}
          style={{
            width: 64,
            height: 64,
            borderRadius: '50%',
            background: 'var(--danger)',
            border: 'none',
            color: 'white',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.2s',
          }}
          onMouseEnter={(e) => {
            e.currentTarget.style.transform = 'scale(1.1)';
            e.currentTarget.style.boxShadow = '0 0 20px rgba(248, 113, 113, 0.4)';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.transform = 'scale(1)';
            e.currentTarget.style.boxShadow = 'none';
          }}
          title="End call"
        >
          <svg width="28" height="28" viewBox="0 0 24 24" fill="none">
            <path d="M23.27 16.7l-2.54-2.54a2 2 0 00-2.82 0l-1.02 1.02a16 16 0 01-6.08-6.08l1.02-1.02a2 2 0 000-2.82L9.3.73a2 2 0 00-2.83 0L4.58 2.62A3.88 3.88 0 003.5 5.2a23.91 23.91 0 0015.3 15.3 3.88 3.88 0 002.58-1.08l1.89-1.89a2 2 0 000-2.83z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" transform="rotate(135 12 12)"/>
          </svg>
        </button>
      </div>
    </div>
  );
};

export default DMVoiceCall;
