import React, { createContext, useContext, useEffect, useState, useCallback, useMemo } from 'react';
import { presenceService, PresenceEvent } from '../services/presenceService';

interface PresenceContextValue {
  // Check if a user is online
  isOnline: (userId: string) => boolean;

  // Check if a user is online on a specific server
  isOnlineOnServer: (userId: string, serverId: string) => boolean;

  // Get all online user IDs
  onlineUserIds: string[];

  // Get online user IDs as Set (for efficient lookups)
  onlineUserSet: Set<string>;

  // Get online users for a specific server
  getOnlineUsersForServer: (serverId: string) => string[];

  // Subscribe to a server's presence updates
  subscribeToServer: (serverId: string) => void;

  // Connection status
  isConnected: boolean;

  // Force re-render (useful after friends list changes)
  refresh: () => void;
}

const PresenceContext = createContext<PresenceContextValue | null>(null);

interface PresenceProviderProps {
  children: React.ReactNode;
}

export function PresenceProvider({ children }: PresenceProviderProps) {
  const [isConnected, setIsConnected] = useState(false);
  const [updateTrigger, setUpdateTrigger] = useState(0);

  // Connect on mount
  useEffect(() => {
    presenceService.connect();

    const handleEvent = (event: PresenceEvent) => {
      switch (event.type) {
        case 'connected':
          setIsConnected(true);
          break;
        case 'disconnected':
          setIsConnected(false);
          break;
        case 'presence_update':
        case 'user_online':
        case 'user_offline':
          // Trigger re-render when presence changes
          setUpdateTrigger(prev => prev + 1);
          break;
      }
    };

    presenceService.addListener(handleEvent);

    return () => {
      presenceService.removeListener(handleEvent);
      // Don't disconnect on unmount - let it stay connected
    };
  }, []);

  const isOnline = useCallback((userId: string): boolean => {
    return presenceService.isOnline(userId);
  }, [updateTrigger]); // eslint-disable-line react-hooks/exhaustive-deps

  const isOnlineOnServer = useCallback((userId: string, serverId: string): boolean => {
    return presenceService.isOnlineOnServer(userId, serverId);
  }, [updateTrigger]); // eslint-disable-line react-hooks/exhaustive-deps

  const onlineUserIds = useMemo(() => {
    return presenceService.getOnlineUsers();
  }, [updateTrigger]); // eslint-disable-line react-hooks/exhaustive-deps

  const onlineUserSet = useMemo(() => {
    return presenceService.getOnlineUserSet();
  }, [updateTrigger]); // eslint-disable-line react-hooks/exhaustive-deps

  const getOnlineUsersForServer = useCallback((serverId: string): string[] => {
    return presenceService.getOnlineUsersForServer(serverId);
  }, [updateTrigger]); // eslint-disable-line react-hooks/exhaustive-deps

  const subscribeToServer = useCallback((serverId: string) => {
    presenceService.subscribeToServer(serverId);
  }, []);

  const refresh = useCallback(() => {
    setUpdateTrigger(prev => prev + 1);
  }, []);

  const value = useMemo(() => ({
    isOnline,
    isOnlineOnServer,
    onlineUserIds,
    onlineUserSet,
    getOnlineUsersForServer,
    subscribeToServer,
    isConnected,
    refresh,
  }), [
    isOnline,
    isOnlineOnServer,
    onlineUserIds,
    onlineUserSet,
    getOnlineUsersForServer,
    subscribeToServer,
    isConnected,
    refresh,
  ]);

  return (
    <PresenceContext.Provider value={value}>
      {children}
    </PresenceContext.Provider>
  );
}

/**
 * Hook to access presence data
 */
export function usePresence(): PresenceContextValue {
  const context = useContext(PresenceContext);
  if (!context) {
    throw new Error('usePresence must be used within a PresenceProvider');
  }
  return context;
}

/**
 * Hook to check if a specific user is online
 */
export function useUserOnlineStatus(userId: string | undefined): boolean {
  const { isOnline, onlineUserSet } = usePresence();

  return useMemo(() => {
    if (!userId) return false;
    return isOnline(userId);
  }, [userId, isOnline, onlineUserSet]); // eslint-disable-line react-hooks/exhaustive-deps
}

/**
 * Hook to get online status for multiple users
 */
export function useUsersOnlineStatus(userIds: string[]): Map<string, boolean> {
  const { isOnline, onlineUserSet } = usePresence();

  return useMemo(() => {
    const statusMap = new Map<string, boolean>();
    for (const userId of userIds) {
      statusMap.set(userId, isOnline(userId));
    }
    return statusMap;
  }, [userIds, isOnline, onlineUserSet]); // eslint-disable-line react-hooks/exhaustive-deps
}

export default PresenceContext;
