import React from 'react';
import { ServerMember } from '../services/api';
import { getAvatarColor } from '../utils/avatarColors';
import { User } from '../types';
import { usePresence } from '../contexts/PresenceContext';

interface MemberListProps {
  members: ServerMember[];
  onlineUserIds?: Set<string>; // Deprecated: kept for backwards compatibility
  currentUserId?: string;
  onUserClick?: (user: User, position: { x: number; y: number }) => void;
}

const MemberList: React.FC<MemberListProps> = ({
  members,
  currentUserId,
  onUserClick,
}) => {
  // Use centralized presence service
  const { isOnline } = usePresence();

  //        
  const onlineMembers = members.filter(m => isOnline(m.id));
  const offlineMembers = members.filter(m => !isOnline(m.id));

  const handleMemberClick = (member: ServerMember, isOnline: boolean, e: React.MouseEvent) => {
    //     
    if (member.id === currentUserId) return;
    if (!onUserClick) return;

    const rect = (e.currentTarget as HTMLElement).getBoundingClientRect();
    //  ServerMember  User  UserProfilePopup
    const user: User = {
      id: member.id,
      username: member.username,
      email: '',
      avatar_url: member.avatar_url || undefined,
      created_at: new Date().toISOString(),
      updated_at: new Date().toISOString(),
    };
    //       (300px  + 10px )
    onUserClick(user, { x: rect.left - 310, y: rect.top });
  };

  const renderMember = (member: ServerMember, isOnline: boolean) => (
    <div
      key={member.id}
      style={{
        ...styles.memberItem,
        opacity: isOnline ? 1 : 0.5,
        cursor: member.id !== currentUserId ? 'pointer' : 'default',
      }}
      onClick={(e) => handleMemberClick(member, isOnline, e)}
    >
      <div style={styles.avatarWrapper}>
        <div
          style={{
            ...styles.avatar,
            backgroundColor: getAvatarColor(member.id || member.username),
          }}
        >
          {member.avatar_url ? (
            <img src={member.avatar_url} alt="" style={styles.avatarImg} />
          ) : (
            <span style={styles.avatarText}>
              {member.username[0].toUpperCase()}
            </span>
          )}
        </div>
        <div
          style={{
            ...styles.statusDot,
            backgroundColor: isOnline ? '#3ba55c' : '#747f8d',
          }}
        />
      </div>
      <div style={styles.memberInfo}>
        <span style={styles.memberName}>
          {member.username}
          {member.id === currentUserId && (
            <span style={styles.youBadge}>()</span>
          )}
        </span>
        {member.role === 'owner' && (
          <span style={styles.ownerBadge}></span>
        )}
      </div>
    </div>
  );

  return (
    <div style={styles.container}>
      {/*  */}
      {onlineMembers.length > 0 && (
        <div style={styles.section}>
          <div style={styles.sectionHeader}>
              {onlineMembers.length}
          </div>
          {onlineMembers.map(m => renderMember(m, true))}
        </div>
      )}

      {/*  */}
      {offlineMembers.length > 0 && (
        <div style={styles.section}>
          <div style={styles.sectionHeader}>
              {offlineMembers.length}
          </div>
          {offlineMembers.map(m => renderMember(m, false))}
        </div>
      )}
    </div>
  );
};

const styles: { [key: string]: React.CSSProperties } = {
  container: {
    width: '240px',
    minWidth: '240px',
    backgroundColor: 'var(--bg-secondary)',
    borderLeft: '1px solid var(--border)',
    display: 'flex',
    flexDirection: 'column',
    overflowY: 'auto',
    padding: '16px 8px',
  },
  section: {
    marginBottom: '16px',
  },
  sectionHeader: {
    color: 'var(--text-muted)',
    fontSize: '12px',
    fontWeight: 600,
    textTransform: 'uppercase',
    padding: '0 8px 8px',
    letterSpacing: '0.02em',
  },
  memberItem: {
    display: 'flex',
    alignItems: 'center',
    gap: '12px',
    padding: '8px 10px',
    borderRadius: 'var(--radius-sm)',
    cursor: 'pointer',
    transition: 'background-color 0.15s',
  },
  avatarWrapper: {
    position: 'relative',
    flexShrink: 0,
  },
  avatar: {
    width: '32px',
    height: '32px',
    borderRadius: '50%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    overflow: 'hidden',
  },
  avatarImg: {
    width: '100%',
    height: '100%',
    objectFit: 'cover',
  },
  avatarText: {
    color: '#fff',
    fontSize: '14px',
    fontWeight: 600,
  },
  statusDot: {
    position: 'absolute',
    bottom: '-2px',
    right: '-2px',
    width: '12px',
    height: '12px',
    borderRadius: '50%',
    border: '3px solid var(--bg-secondary)',
  },
  memberInfo: {
    display: 'flex',
    flexDirection: 'column',
    overflow: 'hidden',
  },
  memberName: {
    color: 'var(--text-primary)',
    fontSize: '14px',
    fontWeight: 500,
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  },
  youBadge: {
    color: 'var(--text-muted)',
    fontSize: '12px',
    marginLeft: '4px',
  },
  ownerBadge: {
    color: '#faa61a',
    fontSize: '11px',
    fontWeight: 500,
  },
};

export default MemberList;
