import React, { useState } from 'react';
import { updateProfile } from '../services/api';
import { getAvatarColor } from '../utils/avatarColors';
import { CloseIcon } from './Icons';

interface ProfileModalProps {
  isOpen: boolean;
  onClose: () => void;
  user: {
    id: string;
    username: string;
    email: string;
    avatar_url?: string;
  };
  onProfileUpdate: (updatedUser: { id: string; username: string; email: string; avatar_url?: string }) => void;
}

const ProfileModal: React.FC<ProfileModalProps> = ({ isOpen, onClose, user, onProfileUpdate }) => {
  const [username, setUsername] = useState(user.username);
  const [avatarUrl, setAvatarUrl] = useState(user.avatar_url || '');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  if (!isOpen) return null;

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      const data: { username?: string; avatar_url?: string | null } = {};

      if (username !== user.username) {
        data.username = username;
      }

      if (avatarUrl !== (user.avatar_url || '')) {
        data.avatar_url = avatarUrl || null;
      }

      if (Object.keys(data).length === 0) {
        onClose();
        return;
      }

      const updatedUser = await updateProfile(data);
      onProfileUpdate(updatedUser);
      onClose();
    } catch (err) {
      setError('Failed to update profile');
      console.error('Profile update error:', err);
    } finally {
      setLoading(false);
    }
  };

  const previewAvatarUrl = avatarUrl || undefined;

  return (
    <div style={styles.overlay} onClick={onClose}>
      <div style={styles.modal} onClick={(e) => e.stopPropagation()}>
        <div style={styles.header}>
          <h2 style={styles.title}>Edit Profile</h2>
          <button onClick={onClose} style={styles.closeBtn}>
            <CloseIcon size={20} />
          </button>
        </div>

        <form onSubmit={handleSubmit} style={styles.form}>
          <div style={styles.avatarSection}>
            <div style={styles.avatarPreview}>
              {previewAvatarUrl ? (
                <img src={previewAvatarUrl} alt="Avatar" style={styles.avatarImage} />
              ) : (
                <div style={{ ...styles.avatarPlaceholder, backgroundColor: getAvatarColor(user.id || user.username) }}>
                  {username[0]?.toUpperCase() || '?'}
                </div>
              )}
            </div>
            <div style={styles.avatarInfo}>
              <label style={styles.label}>Avatar URL</label>
              <input
                type="url"
                value={avatarUrl}
                onChange={(e) => setAvatarUrl(e.target.value)}
                placeholder="https://example.com/avatar.png"
                style={styles.input}
              />
              <span style={styles.hint}>Leave empty to use default avatar</span>
            </div>
          </div>

          <div style={styles.field}>
            <label style={styles.label}>Username</label>
            <input
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              minLength={3}
              maxLength={32}
              required
              style={styles.input}
            />
          </div>

          <div style={styles.field}>
            <label style={styles.label}>Email</label>
            <input
              type="email"
              value={user.email}
              disabled
              style={{ ...styles.input, opacity: 0.6, cursor: 'not-allowed' }}
            />
            <span style={styles.hint}>Email cannot be changed</span>
          </div>

          {error && <div style={styles.error}>{error}</div>}

          <div style={styles.buttons}>
            <button type="button" onClick={onClose} style={styles.cancelBtn}>
              Cancel
            </button>
            <button type="submit" disabled={loading} style={styles.saveBtn}>
              {loading ? 'Saving...' : 'Save Changes'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

const styles: { [key: string]: React.CSSProperties } = {
  overlay: {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.85)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 1001,
  },
  modal: {
    backgroundColor: 'var(--bg-primary)',
    borderRadius: '8px',
    width: '440px',
    maxHeight: '80vh',
    overflow: 'hidden',
    display: 'flex',
    flexDirection: 'column',
  },
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: '16px 20px',
    borderBottom: '1px solid var(--border)',
  },
  title: {
    margin: 0,
    color: 'var(--text-primary)',
    fontSize: '20px',
  },
  closeBtn: {
    backgroundColor: 'transparent',
    border: 'none',
    color: 'var(--text-muted)',
    cursor: 'pointer',
    padding: '4px 8px',
  },
  form: {
    padding: '20px',
  },
  avatarSection: {
    display: 'flex',
    gap: '20px',
    marginBottom: '20px',
    alignItems: 'flex-start',
  },
  avatarPreview: {
    flexShrink: 0,
  },
  avatarImage: {
    width: '80px',
    height: '80px',
    borderRadius: '50%',
    objectFit: 'cover',
  },
  avatarPlaceholder: {
    width: '80px',
    height: '80px',
    borderRadius: '50%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    fontSize: '32px',
    fontWeight: 'bold',
    color: '#fff',
  },
  avatarInfo: {
    flex: 1,
  },
  field: {
    marginBottom: '16px',
  },
  label: {
    display: 'block',
    color: 'var(--text-muted)',
    fontSize: '12px',
    fontWeight: 'bold',
    textTransform: 'uppercase',
    marginBottom: '8px',
  },
  input: {
    width: '100%',
    padding: '10px 12px',
    backgroundColor: 'var(--bg-tertiary)',
    border: '1px solid var(--border)',
    borderRadius: '4px',
    color: 'var(--text-primary)',
    fontSize: '14px',
    boxSizing: 'border-box',
  },
  hint: {
    display: 'block',
    color: 'var(--text-faded)',
    fontSize: '12px',
    marginTop: '4px',
  },
  error: {
    color: '#f04747',
    fontSize: '14px',
    marginBottom: '16px',
    padding: '10px',
    backgroundColor: 'rgba(240, 71, 71, 0.1)',
    borderRadius: '4px',
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
    gap: '12px',
    marginTop: '20px',
  },
  cancelBtn: {
    padding: '10px 20px',
    backgroundColor: 'transparent',
    border: 'none',
    borderRadius: '4px',
    color: 'var(--text-primary)',
    cursor: 'pointer',
    fontSize: '14px',
  },
  saveBtn: {
    padding: '10px 20px',
    backgroundColor: '#5865f2',
    border: 'none',
    borderRadius: '4px',
    color: '#fff',
    cursor: 'pointer',
    fontSize: '14px',
  },
};

export default ProfileModal;
