import React, { useState, useEffect } from 'react';
import {
  updateServerSettings,
  deleteServer,
  getServerBans,
  unbanUser,
  getServerLogs,
  ServerBan,
  ServerLog,
} from '../services/api';
import { Server, Channel } from '../types';
import { getAvatarColor } from '../utils/avatarColors';

interface ServerSettingsModalProps {
  server: Server;
  channels: Channel[];
  onClose: () => void;
  onServerDeleted: () => void;
  onServerUpdated: (server: Server) => void;
  onChannelRename: (channelId: string, newName: string) => void;
  onChannelDelete: (channelId: string) => void;
}

type Tab = 'overview' | 'channels' | 'bans' | 'logs' | 'delete';

const ServerSettingsModal: React.FC<ServerSettingsModalProps> = ({
  server,
  channels,
  onClose,
  onServerDeleted,
  onServerUpdated,
  onChannelRename,
  onChannelDelete,
}) => {
  const [activeTab, setActiveTab] = useState<Tab>('overview');
  const [serverName, setServerName] = useState(server.name);
  const [saving, setSaving] = useState(false);
  const [bans, setBans] = useState<ServerBan[]>([]);
  const [logs, setLogs] = useState<ServerLog[]>([]);
  const [loadingBans, setLoadingBans] = useState(false);
  const [loadingLogs, setLoadingLogs] = useState(false);
  const [deleteConfirmName, setDeleteConfirmName] = useState('');
  const [deleting, setDeleting] = useState(false);
  const [editingChannel, setEditingChannel] = useState<string | null>(null);
  const [editChannelName, setEditChannelName] = useState('');

  useEffect(() => {
    if (activeTab === 'bans') {
      loadBans();
    } else if (activeTab === 'logs') {
      loadLogs();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeTab]);

  const loadBans = async () => {
    setLoadingBans(true);
    try {
      const data = await getServerBans(server.id);
      setBans(data || []);
    } catch (error) {
      console.error('Failed to load bans:', error);
    } finally {
      setLoadingBans(false);
    }
  };

  const loadLogs = async () => {
    setLoadingLogs(true);
    try {
      const data = await getServerLogs(server.id);
      setLogs(data || []);
    } catch (error) {
      console.error('Failed to load logs:', error);
    } finally {
      setLoadingLogs(false);
    }
  };

  const handleSaveServerName = async () => {
    if (serverName.trim() === server.name) return;
    setSaving(true);
    try {
      await updateServerSettings(server.id, serverName.trim());
      onServerUpdated({ ...server, name: serverName.trim() });
    } catch (error) {
      console.error('Failed to update server:', error);
    } finally {
      setSaving(false);
    }
  };

  const handleUnban = async (userId: string) => {
    try {
      await unbanUser(server.id, userId);
      setBans(bans.filter(b => b.user_id !== userId));
    } catch (error) {
      console.error('Failed to unban user:', error);
    }
  };

  const handleDeleteServer = async () => {
    if (deleteConfirmName !== server.name) return;
    setDeleting(true);
    try {
      await deleteServer(server.id, deleteConfirmName);
      onServerDeleted();
    } catch (error) {
      console.error('Failed to delete server:', error);
      setDeleting(false);
    }
  };

  const handleChannelRename = (channelId: string) => {
    if (editChannelName.trim() && editChannelName.trim() !== channels.find(c => c.id === channelId)?.name) {
      onChannelRename(channelId, editChannelName.trim());
    }
    setEditingChannel(null);
    setEditChannelName('');
  };

  const getActionLabel = (action: string): string => {
    const labels: Record<string, string> = {
      'kick': '',
      'ban': '',
      'unban': '',
      'channel_create': ' ',
      'channel_delete': ' ',
      'channel_rename': ' ',
      'server_update': ' ',
    };
    return labels[action] || action;
  };

  const formatDate = (dateStr: string): string => {
    const date = new Date(dateStr);
    return date.toLocaleString('ru-RU', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  const tabs: { id: Tab; label: string; icon: JSX.Element }[] = [
    {
      id: 'overview',
      label: '',
      icon: (
        <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
          <circle cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="2"/>
          <path d="M12 6v6l4 2" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
        </svg>
      ),
    },
    {
      id: 'channels',
      label: '',
      icon: (
        <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
          <path d="M4 6h16M4 12h16M4 18h16" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
        </svg>
      ),
    },
    {
      id: 'bans',
      label: '',
      icon: (
        <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
          <circle cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="2"/>
          <path d="M4.93 4.93l14.14 14.14" stroke="currentColor" strokeWidth="2"/>
        </svg>
      ),
    },
    {
      id: 'logs',
      label: '',
      icon: (
        <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
          <path d="M14 2H6a2 2 0 00-2 2v16a2 2 0 002 2h12a2 2 0 002-2V8z" stroke="currentColor" strokeWidth="2"/>
          <path d="M14 2v6h6M16 13H8M16 17H8M10 9H8" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
        </svg>
      ),
    },
    {
      id: 'delete',
      label: '',
      icon: (
        <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
          <path d="M3 6h18M19 6v14a2 2 0 01-2 2H7a2 2 0 01-2-2V6m3 0V4a2 2 0 012-2h4a2 2 0 012 2v2" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
        </svg>
      ),
    },
  ];

  return (
    <div style={styles.overlay} onClick={onClose}>
      <div style={styles.modal} onClick={e => e.stopPropagation()}>
        <div style={styles.header}>
          <h2 style={styles.title}> </h2>
          <button style={styles.closeBtn} onClick={onClose}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
              <path d="M18 6L6 18M6 6l12 12" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
            </svg>
          </button>
        </div>

        <div style={styles.content}>
          <div style={styles.sidebar}>
            {tabs.map(tab => (
              <button
                key={tab.id}
                style={{
                  ...styles.tabBtn,
                  ...(activeTab === tab.id ? styles.tabBtnActive : {}),
                  ...(tab.id === 'delete' ? styles.tabBtnDanger : {}),
                }}
                onClick={() => setActiveTab(tab.id)}
              >
                {tab.icon}
                <span>{tab.label}</span>
              </button>
            ))}
          </div>

          <div style={styles.main}>
            {activeTab === 'overview' && (
              <div style={styles.section}>
                <h3 style={styles.sectionTitle}> </h3>
                <div style={styles.inputGroup}>
                  <input
                    type="text"
                    value={serverName}
                    onChange={e => setServerName(e.target.value)}
                    style={styles.input}
                    placeholder=" "
                  />
                  <button
                    style={styles.saveBtn}
                    onClick={handleSaveServerName}
                    disabled={saving || serverName.trim() === server.name}
                  >
                    {saving ? '...' : ''}
                  </button>
                </div>
              </div>
            )}

            {activeTab === 'channels' && (
              <div style={styles.section}>
                <h3 style={styles.sectionTitle}> </h3>
                <div style={styles.channelList}>
                  {channels.map(channel => (
                    <div key={channel.id} style={styles.channelItem}>
                      <div style={styles.channelIcon}>
                        {channel.type === 'voice' ? (
                          <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
                            <path d="M12 1a3 3 0 00-3 3v8a3 3 0 006 0V4a3 3 0 00-3-3z" stroke="currentColor" strokeWidth="2"/>
                            <path d="M19 10v2a7 7 0 01-14 0v-2M12 19v4M8 23h8" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
                          </svg>
                        ) : (
                          <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
                            <path d="M21 15a2 2 0 01-2 2H7l-4 4V5a2 2 0 012-2h14a2 2 0 012 2v10z" stroke="currentColor" strokeWidth="2"/>
                          </svg>
                        )}
                      </div>
                      {editingChannel === channel.id ? (
                        <input
                          type="text"
                          value={editChannelName}
                          onChange={e => setEditChannelName(e.target.value)}
                          onBlur={() => handleChannelRename(channel.id)}
                          onKeyDown={e => e.key === 'Enter' && handleChannelRename(channel.id)}
                          style={styles.channelInput}
                          autoFocus
                        />
                      ) : (
                        <span style={styles.channelName}>{channel.name}</span>
                      )}
                      <div style={styles.channelActions}>
                        <button
                          style={styles.iconBtn}
                          onClick={() => {
                            setEditingChannel(channel.id);
                            setEditChannelName(channel.name);
                          }}
                          title=""
                        >
                          <svg width="16" height="16" viewBox="0 0 24 24" fill="none">
                            <path d="M11 4H4a2 2 0 00-2 2v14a2 2 0 002 2h14a2 2 0 002-2v-7" stroke="currentColor" strokeWidth="2"/>
                            <path d="M18.5 2.5a2.121 2.121 0 013 3L12 15l-4 1 1-4 9.5-9.5z" stroke="currentColor" strokeWidth="2"/>
                          </svg>
                        </button>
                        <button
                          style={{ ...styles.iconBtn, ...styles.iconBtnDanger }}
                          onClick={() => onChannelDelete(channel.id)}
                          title=""
                          disabled={channels.length <= 1}
                        >
                          <svg width="16" height="16" viewBox="0 0 24 24" fill="none">
                            <path d="M3 6h18M19 6v14a2 2 0 01-2 2H7a2 2 0 01-2-2V6m3 0V4a2 2 0 012-2h4a2 2 0 012 2v2" stroke="currentColor" strokeWidth="2"/>
                          </svg>
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {activeTab === 'bans' && (
              <div style={styles.section}>
                <h3 style={styles.sectionTitle}> </h3>
                {loadingBans ? (
                  <div style={styles.loading}>...</div>
                ) : bans.length === 0 ? (
                  <div style={styles.empty}>  </div>
                ) : (
                  <div style={styles.banList}>
                    {bans.map(ban => (
                      <div key={ban.id} style={styles.banItem}>
                        <div
                          style={{
                            ...styles.avatar,
                            backgroundColor: getAvatarColor(ban.user?.id || ''),
                          }}
                        >
                          {ban.user?.username?.[0]?.toUpperCase() || '?'}
                        </div>
                        <div style={styles.banInfo}>
                          <div style={styles.banUsername}>{ban.user?.username || ''}</div>
                          <div style={styles.banDetails}>
                             {ban.banned_by_user?.username || ''}  {formatDate(ban.created_at)}
                          </div>
                          {ban.reason && <div style={styles.banReason}>: {ban.reason}</div>}
                        </div>
                        <button
                          style={styles.unbanBtn}
                          onClick={() => handleUnban(ban.user_id)}
                        >
                          
                        </button>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}

            {activeTab === 'logs' && (
              <div style={styles.section}>
                <h3 style={styles.sectionTitle}> </h3>
                {loadingLogs ? (
                  <div style={styles.loading}>...</div>
                ) : logs.length === 0 ? (
                  <div style={styles.empty}>   </div>
                ) : (
                  <div style={styles.logList}>
                    {logs.map(log => (
                      <div key={log.id} style={styles.logItem}>
                        <div style={styles.logAction}>{getActionLabel(log.action_type)}</div>
                        <div style={styles.logDetails}>
                          <>
                            <span style={styles.logActor}>{log.actor?.username || ''}</span>
                            {log.target_user && (
                              <>
                                <span style={styles.logArrow}></span>
                                <span style={styles.logTarget}>{log.target_user.username}</span>
                              </>
                            )}
                            {log.details?.reason && (
                              <span style={styles.logReason}>({log.details.reason as string})</span>
                            )}
                            {log.details?.old_name && log.details?.new_name && (
                              <span style={styles.logReason}>
                                ({log.details.old_name as string}  {log.details.new_name as string})
                              </span>
                            )}
                            {log.details?.channel_name && (
                              <span style={styles.logReason}>({log.details.channel_name as string})</span>
                            )}
                          </>
                        </div>
                        <div style={styles.logTime}>{formatDate(log.created_at)}</div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}

            {activeTab === 'delete' && (
              <div style={styles.section}>
                <h3 style={{ ...styles.sectionTitle, color: 'var(--danger)' }}> </h3>
                <div style={styles.dangerZone}>
                  <p style={styles.dangerText}>
                      .  ,     .
                  </p>
                  <p style={styles.dangerText}>
                        : <strong>{server.name}</strong>
                  </p>
                  <input
                    type="text"
                    value={deleteConfirmName}
                    onChange={e => setDeleteConfirmName(e.target.value)}
                    style={styles.input}
                    placeholder="  "
                  />
                  <button
                    style={styles.deleteBtn}
                    onClick={handleDeleteServer}
                    disabled={deleteConfirmName !== server.name || deleting}
                  >
                    {deleting ? '...' : ' '}
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

