//     

export interface LogEntry {
  id: string;
  timestamp: Date;
  type: 'info' | 'success' | 'warning' | 'error' | 'webrtc' | 'websocket' | 'screenshare' | 'chat' | 'user';
  message: string;
  details?: string;
}

type LogListener = (logs: LogEntry[]) => void;

class DebugLogger {
  private logs: LogEntry[] = [];
  private listeners: Set<LogListener> = new Set();
  private maxLogs = 500;
  private idCounter = 0;

  private generateId(): string {
    return `log_${Date.now()}_${this.idCounter++}`;
  }

  private addLog(type: LogEntry['type'], message: string, details?: string) {
    const entry: LogEntry = {
      id: this.generateId(),
      timestamp: new Date(),
      type,
      message,
      details,
    };

    this.logs.push(entry);

    //   
    if (this.logs.length > this.maxLogs) {
      this.logs = this.logs.slice(-this.maxLogs);
    }

    //  
    this.notifyListeners();

    //     
    const prefix = `[${type.toUpperCase()}]`;
    if (type === 'error') {
      console.error(prefix, message, details || '');
    } else if (type === 'warning') {
      console.warn(prefix, message, details || '');
    } else {
      console.log(prefix, message, details || '');
    }
  }

  private notifyListeners() {
    this.listeners.forEach(listener => listener([...this.logs]));
  }

  //    
  subscribe(listener: LogListener): () => void {
    this.listeners.add(listener);
    //    
    listener([...this.logs]);
    //   
    return () => this.listeners.delete(listener);
  }

  //   
  getLogs(): LogEntry[] {
    return [...this.logs];
  }

  //  
  clear() {
    this.logs = [];
    this.notifyListeners();
  }

  //  
  info(message: string, details?: string) {
    this.addLog('info', message, details);
  }

  success(message: string, details?: string) {
    this.addLog('success', message, details);
  }

  warning(message: string, details?: string) {
    this.addLog('warning', message, details);
  }

  error(message: string, details?: string) {
    this.addLog('error', message, details);
  }

  webrtc(message: string, details?: string) {
    this.addLog('webrtc', message, details);
  }

  websocket(message: string, details?: string) {
    this.addLog('websocket', message, details);
  }

  screenshare(message: string, details?: string) {
    this.addLog('screenshare', message, details);
  }

  chat(message: string, details?: string) {
    this.addLog('chat', message, details);
  }

  user(message: string, details?: string) {
    this.addLog('user', message, details);
  }
}

// Singleton instance
export const debugLogger = new DebugLogger();
