import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { getInvite, joinServer, Invite } from '../services/api';
import { getErrorMessage, getErrorStatus } from '../types';

const InvitePage: React.FC = () => {
  const { code } = useParams<{ code: string }>();
  const navigate = useNavigate();
  const [invite, setInvite] = useState<Invite | null>(null);
  const [loading, setLoading] = useState(true);
  const [joining, setJoining] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const isAuthenticated = !!localStorage.getItem('token');

  useEffect(() => {
    if (!code) {
      setError('  ');
      setLoading(false);
      return;
    }

    const loadInvite = async () => {
      try {
        const inviteData = await getInvite(code);
        setInvite(inviteData);
      } catch (err: unknown) {
        if (getErrorStatus(err) === 404) {
          setError('    ');
        } else {
          setError('   ');
        }
      } finally {
        setLoading(false);
      }
    };

    loadInvite();
  }, [code]);

  const handleJoin = async () => {
    if (!code) return;

    setJoining(true);
    setError(null);

    try {
      await joinServer(code);
      navigate('/');
    } catch (err: unknown) {
      if (getErrorStatus(err) === 400) {
        setError(getErrorMessage(err, '    '));
      } else {
        setError('    ');
      }
    } finally {
      setJoining(false);
    }
  };

  const handleLogin = () => {
    //       
    localStorage.setItem('pendingInvite', code || '');
    navigate('/login');
  };

  const handleRegister = () => {
    //       
    localStorage.setItem('pendingInvite', code || '');
    navigate('/register');
  };

  if (loading) {
    return (
      <div style={styles.container}>
        <div style={styles.card}>
          <div style={styles.loading}>...</div>
        </div>
      </div>
    );
  }

  return (
    <div style={styles.container}>
      <div style={styles.card}>
        {error && !invite ? (
          <>
            <div style={styles.errorIcon}>!</div>
            <h2 style={styles.title}>{error}</h2>
            <button onClick={() => navigate('/')} style={styles.button}>
               
            </button>
          </>
        ) : invite ? (
          <>
            <div style={styles.serverIcon}>
              {invite.server?.name?.[0]?.toUpperCase() || '?'}
            </div>
            <p style={styles.subtitle}>   </p>
            <h1 style={styles.serverName}>{invite.server?.name || ''}</h1>

            {error && <p style={styles.error}>{error}</p>}

            {isAuthenticated ? (
              <button
                onClick={handleJoin}
                disabled={joining}
                style={{
                  ...styles.button,
                  opacity: joining ? 0.7 : 1,
                }}
              >
                {joining ? '...' : ''}
              </button>
            ) : (
              <>
                <p style={styles.hint}>    </p>
                <div style={styles.buttonGroup}>
                  <button onClick={handleLogin} style={styles.button}>
                    
                  </button>
                  <button onClick={handleRegister} style={styles.buttonSecondary}>
                    
                  </button>
                </div>
              </>
            )}
          </>
        ) : null}
      </div>
    </div>
  );
};

const styles = {
  container: {
    minHeight: '100vh',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'var(--bg-primary)',
    padding: '20px',
  },
  card: {
    backgroundColor: 'var(--bg-secondary)',
    borderRadius: '8px',
    padding: '32px',
    textAlign: 'center' as const,
    maxWidth: '400px',
    width: '100%',
  },
  loading: {
    color: 'var(--text-muted)',
    fontSize: '16px',
  },
  errorIcon: {
    width: '64px',
    height: '64px',
    borderRadius: '50%',
    backgroundColor: '#f04747',
    color: '#fff',
    fontSize: '32px',
    fontWeight: 'bold' as const,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    margin: '0 auto 16px',
  },
  serverIcon: {
    width: '80px',
    height: '80px',
    borderRadius: '16px',
    backgroundColor: '#5865f2',
    color: '#fff',
    fontSize: '36px',
    fontWeight: 'bold' as const,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    margin: '0 auto 16px',
  },
  subtitle: {
    color: 'var(--text-muted)',
    fontSize: '14px',
    margin: '0 0 8px 0',
  },
  title: {
    color: 'var(--text-primary)',
    fontSize: '20px',
    margin: '0 0 24px 0',
  },
  serverName: {
    color: 'var(--text-primary)',
    fontSize: '24px',
    fontWeight: 'bold' as const,
    margin: '0 0 24px 0',
  },
  hint: {
    color: 'var(--text-muted)',
    fontSize: '14px',
    margin: '0 0 16px 0',
  },
  error: {
    color: '#f04747',
    fontSize: '14px',
    margin: '0 0 16px 0',
  },
  button: {
    width: '100%',
    padding: '12px',
    backgroundColor: '#5865f2',
    border: 'none',
    borderRadius: '4px',
    color: '#fff',
    fontSize: '16px',
    fontWeight: 'bold' as const,
    cursor: 'pointer',
  },
  buttonSecondary: {
    width: '100%',
    padding: '12px',
    backgroundColor: 'transparent',
    border: '1px solid #5865f2',
    borderRadius: '4px',
    color: '#5865f2',
    fontSize: '16px',
    fontWeight: 'bold' as const,
    cursor: 'pointer',
  },
  buttonGroup: {
    display: 'flex',
    flexDirection: 'column' as const,
    gap: '12px',
  },
};

export default InvitePage;
