// R1Gate Design System v3.0 - Modern Minimal Avatar Colors
const avatarColors = [
  '#818cf8', // Indigo (primary accent)
  '#22d3ee', // Cyan
  '#a855f7', // Purple
  '#fb923c', // Orange
  '#f472b6', // Pink
  '#4ade80', // Green
  '#38bdf8', // Blue
  '#facc15', // Yellow
];

// Generates a stable color based on a string (username or id)
export const getAvatarColor = (identifier: string): string => {
  let hash = 0;
  for (let i = 0; i < identifier.length; i++) {
    const char = identifier.charCodeAt(i);
    hash = ((hash << 5) - hash) + char;
    hash = hash & hash; // Convert to 32bit integer
  }
  const index = Math.abs(hash) % avatarColors.length;
  return avatarColors[index];
};

// Get gradient for avatar background
export const getAvatarGradient = (identifier: string): string => {
  const color = getAvatarColor(identifier);
  const colorIndex = avatarColors.indexOf(color);
  const nextColor = avatarColors[(colorIndex + 1) % avatarColors.length];
  return `linear-gradient(135deg, ${color} 0%, ${nextColor} 100%)`;
};

export default avatarColors;
