import { getNotificationsWebSocketUrl } from './dmApi';
import { NotificationPayload } from '../types';

class NotificationWebSocketService {
  private ws: WebSocket | null = null;
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 5;
  private reconnectDelay = 1000;
  private handlers: ((notification: NotificationPayload) => void)[] = [];
  private isConnecting = false;

  connect(): void {
    if (this.isConnecting || (this.ws && this.ws.readyState === WebSocket.OPEN)) {
      return;
    }

    const token = localStorage.getItem('token');
    if (!token) {
      console.log('No token, skipping notification WebSocket connection');
      return;
    }

    this.isConnecting = true;
    const url = getNotificationsWebSocketUrl();

    try {
      this.ws = new WebSocket(url);

      this.ws.onopen = () => {
        console.log('Notification WebSocket connected');
        this.reconnectAttempts = 0;
        this.isConnecting = false;
      };

      this.ws.onmessage = (event) => {
        try {
          const notification = JSON.parse(event.data) as NotificationPayload;
          this.handlers.forEach((handler) => handler(notification));
        } catch (error) {
          console.error('Failed to parse notification:', error);
        }
      };

      this.ws.onclose = () => {
        console.log('Notification WebSocket disconnected');
        this.isConnecting = false;
        this.attemptReconnect();
      };

      this.ws.onerror = (error) => {
        console.error('Notification WebSocket error:', error);
        this.isConnecting = false;
      };
    } catch (error) {
      console.error('Failed to create WebSocket:', error);
      this.isConnecting = false;
      this.attemptReconnect();
    }
  }

  private attemptReconnect(): void {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++;
      const delay = this.reconnectDelay * Math.pow(2, this.reconnectAttempts - 1);
      console.log(`Attempting to reconnect notification WebSocket in ${delay}ms (attempt ${this.reconnectAttempts})`);
      setTimeout(() => this.connect(), delay);
    } else {
      console.log('Max reconnection attempts reached for notification WebSocket');
    }
  }

  disconnect(): void {
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }
    this.reconnectAttempts = this.maxReconnectAttempts; // Prevent reconnection
  }

  onNotification(handler: (notification: NotificationPayload) => void): () => void {
    this.handlers.push(handler);
    return () => {
      this.handlers = this.handlers.filter((h) => h !== handler);
    };
  }

  addMessageHandler(handler: (notification: NotificationPayload) => void): void {
    this.handlers.push(handler);
  }

  removeMessageHandler(handler: (notification: NotificationPayload) => void): void {
    this.handlers = this.handlers.filter((h) => h !== handler);
  }

  isConnected(): boolean {
    return this.ws !== null && this.ws.readyState === WebSocket.OPEN;
  }
}

// Export singleton instance
export const notificationWebSocket = new NotificationWebSocketService();

export default notificationWebSocket;
