import React, { useState, useEffect, useRef } from 'react';
import { User, Friend } from '../types';
import { sendFriendRequest, removeFriend, getUserProfile, MutualServer } from '../services/friendsApi';
import { openDMChannel, startCall, sendDMMessage } from '../services/dmApi';
import { getAvatarColor } from '../utils/avatarColors';
import { usePresence } from '../contexts/PresenceContext';

interface UserProfilePopupProps {
  user: User | null;
  currentUserId: string;
  onClose: () => void;
  onOpenDM?: (channelId: string, channel?: { id: string; participants: User[] }) => void;
  position?: { x: number; y: number };
}

const UserProfilePopup: React.FC<UserProfilePopupProps> = ({
  user,
  currentUserId,
  onClose,
  onOpenDM,
  position,
}) => {
  // Use centralized presence service
  const { isOnline: checkOnline } = usePresence();
  const isOnline = user ? checkOnline(user.id) : false;
  const [isFriend, setIsFriend] = useState(false);
  const [loading, setLoading] = useState(false);
  const [actionMessage, setActionMessage] = useState('');
  const [mutualFriends, setMutualFriends] = useState<Friend[]>([]);
  const [mutualFriendsCount, setMutualFriendsCount] = useState(0);
  const [mutualServers, setMutualServers] = useState<MutualServer[]>([]);
  const [mutualServersCount, setMutualServersCount] = useState(0);
  const [profileLoading, setProfileLoading] = useState(true);
  const [messageText, setMessageText] = useState('');
  const popupRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (user && user.id !== currentUserId) {
      loadProfile();
    } else {
      setProfileLoading(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [user]);

  //      (  )
  useEffect(() => {
    if (!user || !position) return;

    const handleClickOutside = (event: MouseEvent) => {
      if (popupRef.current && !popupRef.current.contains(event.target as Node)) {
        onClose();
      }
    };

    //          
    const timer = setTimeout(() => {
      document.addEventListener('mousedown', handleClickOutside);
    }, 100);

    return () => {
      clearTimeout(timer);
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [user, position, onClose]);

  const loadProfile = async () => {
    if (!user) return;
    try {
      setProfileLoading(true);
      const profile = await getUserProfile(user.id);
      setIsFriend(profile.is_friend);
      setMutualFriends(profile.mutual_friends || []);
      setMutualFriendsCount(profile.mutual_friends_count);
      setMutualServers(profile.mutual_servers || []);
      setMutualServersCount(profile.mutual_servers_count);
    } catch (error) {
      console.error('Failed to load profile:', error);
    } finally {
      setProfileLoading(false);
    }
  };

  const handleSendMessage = async (e?: React.FormEvent) => {
    e?.preventDefault();
    if (!user) return;
    if (!messageText.trim()) return;

    try {
      setLoading(true);
      // / DM 
      const channel = await openDMChannel(user.id);
      //  
      await sendDMMessage(channel.id, messageText.trim());
      setMessageText('');
      //  
      onClose();
      //  DM    
      onOpenDM?.(channel.id, {
        id: channel.id,
        participants: channel.participants || [user, { id: currentUserId, username: '', email: '', created_at: '', updated_at: '' }],
      });
    } catch (error) {
      console.error('Failed to send message:', error);
      setActionMessage('   ');
      setLoading(false);
    }
  };

  const handleCall = async () => {
    if (!user) return;
    try {
      setLoading(true);
      const channel = await openDMChannel(user.id);
      await startCall(channel.id);
      onOpenDM?.(channel.id);
      onClose();
    } catch (error) {
      console.error('Failed to start call:', error);
      setActionMessage('Failed to start call');
    } finally {
      setLoading(false);
    }
  };

  const handleAddFriend = async () => {
    if (!user) return;
    try {
      setLoading(true);
      await sendFriendRequest(user.id);
      setActionMessage('Friend request sent!');
    } catch (error: unknown) {
      const errorMessage = (error as { response?: { data?: { error?: string } } })?.response?.data?.error || 'Failed to send request';
      setActionMessage(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  const handleRemoveFriend = async () => {
    if (!user) return;
    try {
      setLoading(true);
      await removeFriend(user.id);
      setIsFriend(false);
      setActionMessage('Friend removed');
    } catch (error) {
      console.error('Failed to remove friend:', error);
      setActionMessage('Failed to remove friend');
    } finally {
      setLoading(false);
    }
  };

  if (!user) return null;

  const avatarColor = getAvatarColor(user.id);
  const isCurrentUser = user.id === currentUserId;

  // Calculate position
  const popupStyle: React.CSSProperties = {
    position: 'fixed',
    zIndex: 1000,
    width: 300,
    background: 'var(--bg-floating)',
    borderRadius: 'var(--radius-lg)',
    boxShadow: 'var(--shadow-lg)',
    overflow: 'hidden',
    animation: 'scaleIn 0.2s ease-out',
  };

  if (position) {
    //        
    const popupWidth = 300;
    const popupHeight = 450;
    let x = position.x;
    let y = position.y;

    //     
    if (x < 10) x = 10;
    //     
    if (x + popupWidth > window.innerWidth - 10) {
      x = window.innerWidth - popupWidth - 10;
    }
    //     
    if (y + popupHeight > window.innerHeight - 10) {
      y = window.innerHeight - popupHeight - 10;
    }
    //     
    if (y < 10) y = 10;

    popupStyle.left = x;
    popupStyle.top = y;
  } else {
    popupStyle.left = '50%';
    popupStyle.top = '50%';
    popupStyle.transform = 'translate(-50%, -50%)';
  }

  return (
    <>
      {/* Backdrop -      */}
      {!position && (
        <div
          onClick={onClose}
          style={{
            position: 'fixed',
            inset: 0,
            zIndex: 999,
            background: 'rgba(0, 0, 0, 0.5)',
          }}
        />
      )}

      {/* Popup */}
      <div ref={popupRef} style={popupStyle}>
        {/* Banner */}
        <div
          style={{
            height: 60,
            background: `linear-gradient(135deg, ${avatarColor} 0%, ${avatarColor}88 100%)`,
          }}
        />

        {/* Avatar */}
        <div
          style={{
            width: 80,
            height: 80,
            borderRadius: '50%',
            background: user.avatar_url ? `url(${user.avatar_url}) center/cover` : avatarColor,
            border: '6px solid var(--bg-floating)',
            position: 'absolute',
            top: 20,
            left: 16,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: 'white',
            fontWeight: 700,
            fontSize: 28,
          }}
        >
          {!user.avatar_url && user.username[0].toUpperCase()}

          {/* Status indicator */}
          <div
            style={{
              position: 'absolute',
              bottom: 2,
              right: 2,
              width: 20,
              height: 20,
              borderRadius: '50%',
              background: isOnline ? 'var(--online)' : 'var(--offline)',
              border: '4px solid var(--bg-floating)',
            }}
          />
        </div>

        {/* Content */}
        <div style={{ padding: '48px 16px 16px' }}>
          {/* Username */}
          <h3
            style={{
              fontSize: 20,
              fontWeight: 600,
              color: 'var(--text-primary)',
              margin: 0,
            }}
          >
            {user.username}
          </h3>

          {/* Status */}
          <div
            style={{
              fontSize: 13,
              color: isOnline ? 'var(--online)' : 'var(--text-muted)',
              marginTop: 4,
            }}
          >
            {isOnline ? 'Online' : 'Offline'}
          </div>

          {/* Mutual Friends & Servers Section */}
          {!isCurrentUser && !profileLoading && (mutualFriendsCount > 0 || mutualServersCount > 0) && (
            <div
              style={{
                display: 'flex',
                gap: 12,
                marginTop: 12,
                paddingTop: 12,
                borderTop: '1px solid var(--border)',
              }}
            >
              {/* Mutual Friends */}
              {mutualFriendsCount > 0 && (
                <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                  <div style={{ display: 'flex' }}>
                    {mutualFriends.slice(0, 3).map((friend, index) => (
                      <div
                        key={friend.id}
                        style={{
                          width: 20,
                          height: 20,
                          borderRadius: '50%',
                          background: friend.avatar_url
                            ? `url(${friend.avatar_url}) center/cover`
                            : getAvatarColor(friend.id),
                          border: '2px solid var(--bg-floating)',
                          marginLeft: index > 0 ? -8 : 0,
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          fontSize: 10,
                          color: 'white',
                          fontWeight: 600,
                        }}
                      >
                        {!friend.avatar_url && friend.username[0].toUpperCase()}
                      </div>
                    ))}
                  </div>
                  <span style={{ fontSize: 12, color: 'var(--text-secondary)' }}>
                    {mutualFriendsCount}  {mutualFriendsCount === 1 ? '' : mutualFriendsCount < 5 ? '' : ''}
                  </span>
                </div>
              )}

              {/* Mutual Servers */}
              {mutualServersCount > 0 && (
                <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                  <div style={{ display: 'flex' }}>
                    {mutualServers.slice(0, 3).map((server, index) => (
                      <div
                        key={server.id}
                        style={{
                          width: 20,
                          height: 20,
                          borderRadius: 4,
                          background: server.icon_url
                            ? `url(${server.icon_url}) center/cover`
                            : getAvatarColor(server.id),
                          border: '2px solid var(--bg-floating)',
                          marginLeft: index > 0 ? -8 : 0,
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          fontSize: 10,
                          color: 'white',
                          fontWeight: 600,
                        }}
                      >
                        {!server.icon_url && server.name[0].toUpperCase()}
                      </div>
                    ))}
                  </div>
                  <span style={{ fontSize: 12, color: 'var(--text-secondary)' }}>
                    {mutualServersCount}  {mutualServersCount === 1 ? '' : mutualServersCount < 5 ? '' : ''}
                  </span>
                </div>
              )}
            </div>
          )}

          {/* Member since */}
          <div
            style={{
              fontSize: 12,
              color: 'var(--text-muted)',
              marginTop: 8,
              borderTop: '1px solid var(--border)',
              paddingTop: 12,
            }}
          >
              {new Date(user.created_at).toLocaleDateString('ru-RU', { month: 'long', year: 'numeric' })}
          </div>

          {/* Action message */}
          {actionMessage && (
            <div
              style={{
                padding: '8px 12px',
                borderRadius: 'var(--radius-sm)',
                background: actionMessage.includes('Failed') ? 'var(--danger)' : 'var(--success)',
                color: 'white',
                fontSize: 13,
                marginTop: 12,
              }}
            >
              {actionMessage}
            </div>
          )}

          {/* Actions */}
          {!isCurrentUser && (
            <div style={{ display: 'flex', flexDirection: 'column', gap: 8, marginTop: 16 }}>
              <form onSubmit={handleSendMessage} style={{ position: 'relative' }}>
                <input
                  type="text"
                  value={messageText}
                  onChange={(e) => setMessageText(e.target.value)}
                  placeholder={`  ${user.username}`}
                  disabled={loading}
                  style={{
                    width: '100%',
                    padding: '10px 40px 10px 12px',
                    background: 'var(--bg-tertiary)',
                    border: '1px solid var(--border)',
                    borderRadius: 'var(--radius-sm)',
                    color: 'var(--text-primary)',
                    fontSize: 14,
                    outline: 'none',
                    boxSizing: 'border-box',
                  }}
                />
                <button
                  type="submit"
                  disabled={loading || !messageText.trim()}
                  style={{
                    position: 'absolute',
                    right: 8,
                    top: '50%',
                    transform: 'translateY(-50%)',
                    background: 'transparent',
                    border: 'none',
                    padding: 4,
                    cursor: loading || !messageText.trim() ? 'not-allowed' : 'pointer',
                    opacity: loading || !messageText.trim() ? 0.4 : 1,
                    color: 'var(--primary)',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                  }}
                >
                  <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                    <path d="M2.01 21L23 12 2.01 3 2 10l15 2-15 2z"/>
                  </svg>
                </button>
              </form>

              <div style={{ display: 'flex', gap: 8 }}>
                <button
                  onClick={handleCall}
                  disabled={loading}
                  style={{
                    flex: 1,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    gap: 6,
                    padding: '10px 16px',
                    background: 'var(--bg-tertiary)',
                    border: 'none',
                    borderRadius: 'var(--radius-sm)',
                    color: 'var(--text-primary)',
                    fontWeight: 500,
                    fontSize: 14,
                    cursor: loading ? 'not-allowed' : 'pointer',
                    opacity: loading ? 0.7 : 1,
                  }}
                >
                  <svg width="16" height="16" viewBox="0 0 24 24" fill="none">
                    <path d="M22 16.92v3a2 2 0 01-2.18 2 19.79 19.79 0 01-8.63-3.07 19.5 19.5 0 01-6-6 19.79 19.79 0 01-3.07-8.67A2 2 0 014.11 2h3a2 2 0 012 1.72c.127.96.361 1.903.7 2.81a2 2 0 01-.45 2.11L8.09 9.91a16 16 0 006 6l1.27-1.27a2 2 0 012.11-.45c.907.339 1.85.573 2.81.7A2 2 0 0122 16.92z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                  </svg>
                  
                </button>

                {isFriend ? (
                  <button
                    onClick={handleRemoveFriend}
                    disabled={loading}
                    style={{
                      flex: 1,
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      gap: 6,
                      padding: '10px 16px',
                      background: 'var(--bg-tertiary)',
                      border: 'none',
                      borderRadius: 'var(--radius-sm)',
                      color: 'var(--danger)',
                      fontWeight: 500,
                      fontSize: 14,
                      cursor: loading ? 'not-allowed' : 'pointer',
                      opacity: loading ? 0.7 : 1,
                    }}
                  >
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none">
                      <path d="M16 21v-2a4 4 0 00-4-4H5a4 4 0 00-4 4v2" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                      <circle cx="8.5" cy="7" r="4" stroke="currentColor" strokeWidth="2"/>
                      <line x1="18" y1="8" x2="23" y2="13" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
                      <line x1="23" y1="8" x2="18" y2="13" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
                    </svg>
                    
                  </button>
                ) : (
                  <button
                    onClick={handleAddFriend}
                    disabled={loading}
                    style={{
                      flex: 1,
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      gap: 6,
                      padding: '10px 16px',
                      background: 'var(--bg-tertiary)',
                      border: 'none',
                      borderRadius: 'var(--radius-sm)',
                      color: 'var(--accent)',
                      fontWeight: 500,
                      fontSize: 14,
                      cursor: loading ? 'not-allowed' : 'pointer',
                      opacity: loading ? 0.7 : 1,
                    }}
                  >
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none">
                      <path d="M16 21v-2a4 4 0 00-4-4H5a4 4 0 00-4 4v2" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                      <circle cx="8.5" cy="7" r="4" stroke="currentColor" strokeWidth="2"/>
                      <line x1="20" y1="8" x2="20" y2="14" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
                      <line x1="23" y1="11" x2="17" y2="11" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
                    </svg>
                    
                  </button>
                )}
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default UserProfilePopup;
