import { getDMWebSocketUrl } from './dmApi';
import { DMWSMessage } from '../types';

class DMWebSocketService {
  private ws: WebSocket | null = null;
  private currentChannelId: string | null = null;
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 5;
  private reconnectDelay = 1000;
  private handlers: ((message: DMWSMessage) => void)[] = [];
  private isConnecting = false;

  connect(channelId: string): void {
    // Disconnect from previous channel if different
    if (this.currentChannelId && this.currentChannelId !== channelId) {
      this.disconnect();
    }

    if (this.isConnecting || (this.ws && this.ws.readyState === WebSocket.OPEN && this.currentChannelId === channelId)) {
      return;
    }

    const token = localStorage.getItem('token');
    if (!token) {
      console.log('No token, skipping DM WebSocket connection');
      return;
    }

    this.isConnecting = true;
    this.currentChannelId = channelId;
    const url = getDMWebSocketUrl(channelId);

    try {
      this.ws = new WebSocket(url);

      this.ws.onopen = () => {
        console.log(`DM WebSocket connected to channel ${channelId}`);
        this.reconnectAttempts = 0;
        this.isConnecting = false;
      };

      this.ws.onmessage = (event) => {
        try {
          const message = JSON.parse(event.data) as DMWSMessage;
          this.handlers.forEach((handler) => handler(message));
        } catch (error) {
          console.error('Failed to parse DM message:', error);
        }
      };

      this.ws.onclose = () => {
        console.log('DM WebSocket disconnected');
        this.isConnecting = false;
        if (this.currentChannelId === channelId) {
          this.attemptReconnect(channelId);
        }
      };

      this.ws.onerror = (error) => {
        console.error('DM WebSocket error:', error);
        this.isConnecting = false;
      };
    } catch (error) {
      console.error('Failed to create DM WebSocket:', error);
      this.isConnecting = false;
      this.attemptReconnect(channelId);
    }
  }

  private attemptReconnect(channelId: string): void {
    if (this.reconnectAttempts < this.maxReconnectAttempts && this.currentChannelId === channelId) {
      this.reconnectAttempts++;
      const delay = this.reconnectDelay * Math.pow(2, this.reconnectAttempts - 1);
      console.log(`Attempting to reconnect DM WebSocket in ${delay}ms (attempt ${this.reconnectAttempts})`);
      setTimeout(() => {
        if (this.currentChannelId === channelId) {
          this.connect(channelId);
        }
      }, delay);
    } else {
      console.log('Max reconnection attempts reached for DM WebSocket');
    }
  }

  disconnect(): void {
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }
    this.currentChannelId = null;
    this.reconnectAttempts = this.maxReconnectAttempts; // Prevent reconnection
  }

  onMessage(handler: (message: DMWSMessage) => void): () => void {
    this.handlers.push(handler);
    return () => {
      this.handlers = this.handlers.filter((h) => h !== handler);
    };
  }

  sendTyping(): void {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify({ type: 'typing' }));
    }
  }

  isConnected(): boolean {
    return this.ws !== null && this.ws.readyState === WebSocket.OPEN;
  }

  getCurrentChannelId(): string | null {
    return this.currentChannelId;
  }
}

// Export singleton instance
export const dmWebSocket = new DMWebSocketService();

export default dmWebSocket;
