import { WSMessage } from '../types';

//   WebSocket URL    
//      development,    production
const getWsUrl = () => {
  const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
  const host = window.location.host;
  return `${protocol}//${host}/api`;
};

class WebSocketService {
  private ws: WebSocket | null = null;
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 5;
  private reconnectDelay = 1000;
  private messageHandlers: ((message: WSMessage) => void)[] = [];
  private currentChannelId: string | null = null;
  private currentToken: string | null = null;
  private isManualDisconnect = false;
  private reconnectTimeoutId: ReturnType<typeof setTimeout> | null = null;

  connect(channelId: string, token: string) {
    //       ,   
    if (this.ws && this.ws.readyState === WebSocket.OPEN && this.currentChannelId === channelId) {
      console.log('WebSocket already connected to this channel');
      return;
    }

    //   
    if (this.ws) {
      this.isManualDisconnect = true;
      this.ws.close();
      this.ws = null;
    }

    //   
    if (this.reconnectTimeoutId) {
      clearTimeout(this.reconnectTimeoutId);
      this.reconnectTimeoutId = null;
    }

    this.currentChannelId = channelId;
    this.currentToken = token;
    this.isManualDisconnect = false;
    this.reconnectAttempts = 0;

    const wsUrl = `${getWsUrl()}/channels/${channelId}/ws?token=${token}`;
    console.log('WebSocket connecting to:', wsUrl);

    this.ws = new WebSocket(wsUrl);

    this.ws.onopen = () => {
      console.log('WebSocket connected to channel:', channelId);
      this.reconnectAttempts = 0;
    };

    this.ws.onmessage = (event) => {
      try {
        const message: WSMessage = JSON.parse(event.data);
        console.log('WebSocket received message:', message.type);
        this.messageHandlers.forEach(handler => handler(message));
      } catch (error) {
        console.error('Failed to parse WebSocket message:', error);
      }
    };

    this.ws.onerror = (error) => {
      console.error('WebSocket error:', error);
    };

    this.ws.onclose = (event) => {
      console.log('WebSocket disconnected, code:', event.code, 'manual:', this.isManualDisconnect);

      //      
      if (!this.isManualDisconnect && this.currentChannelId && this.currentToken) {
        this.handleReconnect();
      }
    };
  }

  private handleReconnect() {
    if (this.reconnectAttempts < this.maxReconnectAttempts && this.currentChannelId && this.currentToken) {
      this.reconnectAttempts++;
      const delay = this.reconnectDelay * this.reconnectAttempts;

      console.log(`WebSocket reconnecting in ${delay}ms... (attempt ${this.reconnectAttempts})`);

      this.reconnectTimeoutId = setTimeout(() => {
        if (this.currentChannelId && this.currentToken) {
          //    ,   connect   handlers
          const wsUrl = `${getWsUrl()}/channels/${this.currentChannelId}/ws?token=${this.currentToken}`;
          this.ws = new WebSocket(wsUrl);

          this.ws.onopen = () => {
            console.log('WebSocket reconnected to channel:', this.currentChannelId);
            this.reconnectAttempts = 0;
          };

          this.ws.onmessage = (event) => {
            try {
              const message: WSMessage = JSON.parse(event.data);
              console.log('WebSocket received message:', message.type);
              this.messageHandlers.forEach(handler => handler(message));
            } catch (error) {
              console.error('Failed to parse WebSocket message:', error);
            }
          };

          this.ws.onerror = (error) => {
            console.error('WebSocket error:', error);
          };

          this.ws.onclose = (event) => {
            console.log('WebSocket disconnected after reconnect, code:', event.code);
            if (!this.isManualDisconnect) {
              this.handleReconnect();
            }
          };
        }
      }, delay);
    } else {
      console.error('Max reconnection attempts reached');
    }
  }

  disconnect() {
    console.log('WebSocket manual disconnect');
    this.isManualDisconnect = true;

    //   
    if (this.reconnectTimeoutId) {
      clearTimeout(this.reconnectTimeoutId);
      this.reconnectTimeoutId = null;
    }

    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }

    this.currentChannelId = null;
    this.currentToken = null;
    this.messageHandlers = [];
    this.reconnectAttempts = 0;
  }

  onMessage(handler: (message: WSMessage) => void) {
    //   
    this.messageHandlers = [];
    this.messageHandlers.push(handler);
    console.log('WebSocket handler registered, total handlers:', this.messageHandlers.length);
  }

  send(message: Record<string, unknown>) {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify(message));
    } else {
      console.warn('WebSocket not ready, message not sent');
    }
  }

  isConnected(): boolean {
    return this.ws !== null && this.ws.readyState === WebSocket.OPEN;
  }
}

const websocketService = new WebSocketService();
export default websocketService;
