import React, { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider } from './contexts/ThemeContext';
import { PresenceProvider } from './contexts/PresenceContext';
import Login from './pages/Login';
import Register from './pages/Register';
import Home from './pages/Home';
import InvitePage from './pages/InvitePage';
import AdminPanel from './pages/AdminPanel';

//   -     
const AUTH_VERSION = '2';

//      (,  )
const checkAuthVersion = () => {
  const storedAuthVersion = localStorage.getItem('authVersion');
  if (storedAuthVersion !== AUTH_VERSION) {
    console.log('[Auth] Version mismatch, forcing re-login');
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    localStorage.setItem('authVersion', AUTH_VERSION);
  }
};
checkAuthVersion();

const App: React.FC = () => {
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(
    !!localStorage.getItem('token')
  );

  useEffect(() => {
    const handleStorageChange = () => {
      setIsAuthenticated(!!localStorage.getItem('token'));
    };

    window.addEventListener('storage', handleStorageChange);

    //     
    const checkAuth = () => {
      const hasToken = !!localStorage.getItem('token');
      if (hasToken !== isAuthenticated) {
        setIsAuthenticated(hasToken);
      }
    };

    const interval = setInterval(checkAuth, 100);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
      clearInterval(interval);
    };
  }, [isAuthenticated]);

  // Wrap authenticated content with PresenceProvider
  const AuthenticatedContent = (
    <PresenceProvider>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/invite/:code" element={<InvitePage />} />
        <Route path="/admin" element={<AdminPanel />} />
      </Routes>
    </PresenceProvider>
  );

  return (
    <ThemeProvider>
      <BrowserRouter>
        <Routes>
          <Route
            path="/login"
            element={isAuthenticated ? <Navigate to="/" /> : <Login />}
          />
          <Route
            path="/register"
            element={isAuthenticated ? <Navigate to="/" /> : <Register />}
          />
          <Route
            path="/*"
            element={isAuthenticated ? AuthenticatedContent : <Navigate to="/login" />}
          />
        </Routes>
      </BrowserRouter>
    </ThemeProvider>
  );
};

export default App;
