import axios from 'axios';
import { FriendRequest, Friend } from '../types';

const API_URL = process.env.REACT_APP_API_URL || '/api';

const api = axios.create({
  baseURL: API_URL,
});

api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Send friend request by user ID
export const sendFriendRequest = async (toUserId: string): Promise<FriendRequest> => {
  const response = await api.post('/friends/request', { to_user_id: toUserId });
  return response.data;
};

// Send friend request by username
export const sendFriendRequestByUsername = async (username: string): Promise<FriendRequest> => {
  const response = await api.post('/friends/request/username', { username });
  return response.data;
};

// Get pending friend requests (incoming)
export const getPendingRequests = async (): Promise<FriendRequest[]> => {
  const response = await api.get('/friends/requests');
  return response.data || [];
};

// Get outgoing friend requests
export const getOutgoingRequests = async (): Promise<FriendRequest[]> => {
  const response = await api.get('/friends/requests/outgoing');
  return response.data || [];
};

// Get pending request count
export const getPendingRequestCount = async (): Promise<number> => {
  const response = await api.get('/friends/requests/count');
  return response.data.count || 0;
};

// Accept friend request
export const acceptFriendRequest = async (requestId: string): Promise<FriendRequest> => {
  const response = await api.post(`/friends/requests/${requestId}/accept`);
  return response.data;
};

// Reject friend request
export const rejectFriendRequest = async (requestId: string): Promise<void> => {
  await api.post(`/friends/requests/${requestId}/reject`);
};

// Cancel outgoing friend request
export const cancelFriendRequest = async (requestId: string): Promise<void> => {
  await api.delete(`/friends/requests/${requestId}`);
};

// Get all friends
export const getFriends = async (): Promise<Friend[]> => {
  const response = await api.get('/friends');
  return response.data || [];
};

// Remove friend
export const removeFriend = async (friendId: string): Promise<void> => {
  await api.delete(`/friends/${friendId}`);
};

// User profile response
export interface MutualServer {
  id: string;
  name: string;
  icon_url?: string;
}

export interface UserProfileData {
  is_friend: boolean;
  mutual_friends: Friend[];
  mutual_friends_count: number;
  mutual_servers: MutualServer[];
  mutual_servers_count: number;
}

// Get user profile with mutual friends and servers
export const getUserProfile = async (userId: string): Promise<UserProfileData> => {
  const response = await api.get(`/users/${userId}/profile`);
  return response.data;
};

export default api;
