import React from 'react';
import { User, VoiceCallState } from '../types';
import { getAvatarColor } from '../utils/avatarColors';

interface CallIncomingProps {
  call: VoiceCallState;
  caller: User | null;
  onAccept: () => void;
  onDecline: () => void;
}

const CallIncoming: React.FC<CallIncomingProps> = ({ call, caller, onAccept, onDecline }) => {
  const avatarColor = caller ? getAvatarColor(caller.id) : '#666';

  return (
    <div
      style={{
        position: 'fixed',
        inset: 0,
        zIndex: 2000,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: 'rgba(0, 0, 0, 0.8)',
        backdropFilter: 'blur(8px)',
      }}
    >
      <div
        style={{
          background: 'var(--bg-floating)',
          borderRadius: 'var(--radius-xl)',
          padding: 32,
          textAlign: 'center',
          minWidth: 300,
          animation: 'scaleIn 0.3s ease-out',
        }}
      >
        {/* Caller avatar with ring animation */}
        <div
          style={{
            width: 100,
            height: 100,
            borderRadius: '50%',
            background: caller?.avatar_url
              ? `url(${caller.avatar_url}) center/cover`
              : avatarColor,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: 'white',
            fontWeight: 700,
            fontSize: 36,
            margin: '0 auto 24px',
            boxShadow: '0 0 0 0 var(--primary-glow)',
            animation: 'speaking-pulse 1.5s ease-in-out infinite',
          }}
        >
          {!caller?.avatar_url && caller?.username[0].toUpperCase()}
        </div>

        {/* Caller name */}
        <h2
          style={{
            fontSize: 24,
            fontWeight: 600,
            color: 'var(--text-primary)',
            margin: 0,
          }}
        >
          {caller?.username || 'Unknown'}
        </h2>

        {/* Call type */}
        <p
          style={{
            fontSize: 14,
            color: 'var(--text-secondary)',
            margin: '8px 0 24px',
          }}
        >
          Incoming voice call...
        </p>

        {/* Call icon ringing */}
        <div
          style={{
            margin: '0 auto 24px',
            color: 'var(--accent)',
          }}
          className="animate-ring"
        >
          <svg width="40" height="40" viewBox="0 0 24 24" fill="none">
            <path d="M22 16.92v3a2 2 0 01-2.18 2 19.79 19.79 0 01-8.63-3.07 19.5 19.5 0 01-6-6 19.79 19.79 0 01-3.07-8.67A2 2 0 014.11 2h3a2 2 0 012 1.72c.127.96.361 1.903.7 2.81a2 2 0 01-.45 2.11L8.09 9.91a16 16 0 006 6l1.27-1.27a2 2 0 012.11-.45c.907.339 1.85.573 2.81.7A2 2 0 0122 16.92z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </div>

        {/* Action buttons */}
        <div style={{ display: 'flex', gap: 16, justifyContent: 'center' }}>
          {/* Decline */}
          <button
            onClick={onDecline}
            style={{
              width: 60,
              height: 60,
              borderRadius: '50%',
              background: 'var(--danger)',
              border: 'none',
              color: 'white',
              cursor: 'pointer',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              transition: 'transform 0.2s, box-shadow 0.2s',
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.transform = 'scale(1.1)';
              e.currentTarget.style.boxShadow = '0 0 20px rgba(248, 113, 113, 0.4)';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = 'scale(1)';
              e.currentTarget.style.boxShadow = 'none';
            }}
            title="Decline"
          >
            <svg width="28" height="28" viewBox="0 0 24 24" fill="none">
              <path d="M23.27 16.7l-2.54-2.54a2 2 0 00-2.82 0l-1.02 1.02a16 16 0 01-6.08-6.08l1.02-1.02a2 2 0 000-2.82L9.3.73a2 2 0 00-2.83 0L4.58 2.62A3.88 3.88 0 003.5 5.2a23.91 23.91 0 0015.3 15.3 3.88 3.88 0 002.58-1.08l1.89-1.89a2 2 0 000-2.83z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" transform="rotate(135 12 12)"/>
            </svg>
          </button>

          {/* Accept */}
          <button
            onClick={onAccept}
            style={{
              width: 60,
              height: 60,
              borderRadius: '50%',
              background: 'var(--success)',
              border: 'none',
              color: 'white',
              cursor: 'pointer',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              transition: 'transform 0.2s, box-shadow 0.2s',
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.transform = 'scale(1.1)';
              e.currentTarget.style.boxShadow = '0 0 20px var(--primary-glow)';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = 'scale(1)';
              e.currentTarget.style.boxShadow = 'none';
            }}
            title="Accept"
          >
            <svg width="28" height="28" viewBox="0 0 24 24" fill="none">
              <path d="M22 16.92v3a2 2 0 01-2.18 2 19.79 19.79 0 01-8.63-3.07 19.5 19.5 0 01-6-6 19.79 19.79 0 01-3.07-8.67A2 2 0 014.11 2h3a2 2 0 012 1.72c.127.96.361 1.903.7 2.81a2 2 0 01-.45 2.11L8.09 9.91a16 16 0 006 6l1.27-1.27a2 2 0 012.11-.45c.907.339 1.85.573 2.81.7A2 2 0 0122 16.92z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
};

export default CallIncoming;
