import React, { useState, useEffect, useRef, useCallback } from 'react';
import { DMMessage, DMChannel, User, DMWSMessage, DMWSMessagePayload, DMWSMessageDeletedPayload, DMWSTypingPayload } from '../types';
import { getDMMessages, sendDMMessage, editDMMessage, deleteDMMessage, startCall } from '../services/dmApi';
import { dmWebSocket } from '../services/dmWebsocket';
import { getAvatarColor } from '../utils/avatarColors';

interface DMChatProps {
  channel: DMChannel;
  currentUser: User;
  onClose?: () => void;
  onStartCall?: () => void;
}

const DMChat: React.FC<DMChatProps> = ({ channel, currentUser, onClose, onStartCall }) => {
  const [messages, setMessages] = useState<DMMessage[]>([]);
  const [newMessage, setNewMessage] = useState('');
  const [loading, setLoading] = useState(true);
  const [sending, setSending] = useState(false);
  const [editingMessageId, setEditingMessageId] = useState<string | null>(null);
  const [editingContent, setEditingContent] = useState('');
  const [typingUsers, setTypingUsers] = useState<string[]>([]);
  const [contextMenu, setContextMenu] = useState<{ x: number; y: number; messageId: string } | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const typingTimeoutRef = useRef<NodeJS.Timeout | null>(null);

  // Get the other participant
  const otherUser = channel.participants.find((p) => p.id !== currentUser.id);
  const otherUserColor = otherUser ? getAvatarColor(otherUser.id) : '#666';

  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, []);

  useEffect(() => {
    loadMessages();
    dmWebSocket.connect(channel.id);

    const unsubscribe = dmWebSocket.onMessage(handleWebSocketMessage);

    return () => {
      unsubscribe();
      dmWebSocket.disconnect();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [channel.id]);

  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  const loadMessages = async () => {
    try {
      setLoading(true);
      const data = await getDMMessages(channel.id);
      setMessages(data);
    } catch (error) {
      console.error('Failed to load messages:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleWebSocketMessage = (wsMessage: DMWSMessage) => {
    switch (wsMessage.type) {
      case 'dm-message': {
        const payload = wsMessage.payload as DMWSMessagePayload;
        if (payload.dm_channel_id === channel.id) {
          setMessages((prev) => {
            if (prev.some((m) => m.id === payload.message.id)) return prev;
            return [...prev, payload.message];
          });
        }
        break;
      }
      case 'dm-message-updated': {
        const payload = wsMessage.payload as DMWSMessagePayload;
        if (payload.dm_channel_id === channel.id) {
          setMessages((prev) =>
            prev.map((m) => (m.id === payload.message.id ? payload.message : m))
          );
        }
        break;
      }
      case 'dm-message-deleted': {
        const payload = wsMessage.payload as DMWSMessageDeletedPayload;
        if (payload.dm_channel_id === channel.id) {
          setMessages((prev) => prev.filter((m) => m.id !== payload.message_id));
        }
        break;
      }
      case 'typing': {
        const payload = wsMessage.payload as DMWSTypingPayload;
        if (payload.dm_channel_id === channel.id && payload.user_id !== currentUser.id) {
          setTypingUsers((prev) => {
            if (prev.includes(payload.user_id)) return prev;
            return [...prev, payload.user_id];
          });

          // Clear typing after 3 seconds
          setTimeout(() => {
            setTypingUsers((prev) => prev.filter((id) => id !== payload.user_id));
          }, 3000);
        }
        break;
      }
    }
  };

  const handleSendMessage = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newMessage.trim() || sending) return;

    const content = newMessage.trim();
    setNewMessage('');
    setSending(true);

    try {
      await sendDMMessage(channel.id, content);
    } catch (error) {
      console.error('Failed to send message:', error);
      setNewMessage(content);
    } finally {
      setSending(false);
    }
  };

  const handleEditMessage = async (messageId: string) => {
    if (!editingContent.trim()) return;

    try {
      await editDMMessage(channel.id, messageId, editingContent.trim());
      setEditingMessageId(null);
      setEditingContent('');
    } catch (error) {
      console.error('Failed to edit message:', error);
    }
  };

  const handleDeleteMessage = async (messageId: string) => {
    try {
      await deleteDMMessage(channel.id, messageId);
      setContextMenu(null);
    } catch (error) {
      console.error('Failed to delete message:', error);
    }
  };

  const handleStartCall = async () => {
    try {
      await startCall(channel.id);
      onStartCall?.();
    } catch (error) {
      console.error('Failed to start call:', error);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setNewMessage(e.target.value);

    // Send typing indicator
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current);
    }

    dmWebSocket.sendTyping();

    typingTimeoutRef.current = setTimeout(() => {
      typingTimeoutRef.current = null;
    }, 2000);
  };

  const handleContextMenu = (e: React.MouseEvent, messageId: string, userId: string) => {
    if (userId !== currentUser.id) return;
    e.preventDefault();
    setContextMenu({ x: e.clientX, y: e.clientY, messageId });
  };

  // Format timestamp
  const formatTime = (dateStr: string) => {
    const date = new Date(dateStr);
    return date.toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit' });
  };

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr);
    const today = new Date();
    const yesterday = new Date(today);
    yesterday.setDate(yesterday.getDate() - 1);

    if (date.toDateString() === today.toDateString()) return 'Today';
    if (date.toDateString() === yesterday.toDateString()) return 'Yesterday';
    return date.toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric' });
  };

  // Group messages by date
  const groupedMessages: { date: string; messages: DMMessage[] }[] = [];
  let currentDate = '';
  messages.forEach((msg) => {
    const msgDate = formatDate(msg.created_at);
    if (msgDate !== currentDate) {
      currentDate = msgDate;
      groupedMessages.push({ date: msgDate, messages: [] });
    }
    groupedMessages[groupedMessages.length - 1].messages.push(msg);
  });

  return (
    <div style={{ display: 'flex', flexDirection: 'column', height: '100%', background: 'var(--bg-secondary)' }}>
      {/* Header */}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          padding: '12px 16px',
          borderBottom: '1px solid var(--border)',
          background: 'var(--bg-secondary)',
        }}
      >
        <div style={{ display: 'flex', alignItems: 'center', gap: 12 }}>
          {onClose && (
            <button
              onClick={onClose}
              style={{
                background: 'none',
                border: 'none',
                color: 'var(--text-secondary)',
                cursor: 'pointer',
                padding: 4,
                display: 'flex',
              }}
            >
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none">
                <path d="M19 12H5M12 19l-7-7 7-7" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </button>
          )}

          {/* User avatar */}
          <div
            style={{
              width: 36,
              height: 36,
              borderRadius: '50%',
              background: otherUser?.avatar_url
                ? `url(${otherUser.avatar_url}) center/cover`
                : otherUserColor,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              color: 'white',
              fontWeight: 600,
              fontSize: 14,
            }}
          >
            {!otherUser?.avatar_url && otherUser?.username[0].toUpperCase()}
          </div>

          <div>
            <div style={{ fontWeight: 600, fontSize: 15, color: 'var(--text-primary)' }}>
              {otherUser?.username || 'Unknown'}
            </div>
          </div>
        </div>

        {/* Call button */}
        <button
          onClick={handleStartCall}
          style={{
            width: 36,
            height: 36,
            borderRadius: '50%',
            background: 'var(--bg-tertiary)',
            border: 'none',
            color: 'var(--text-secondary)',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
          title="Start call"
        >
          <svg width="20" height="20" viewBox="0 0 24 24" fill="none">
            <path d="M22 16.92v3a2 2 0 01-2.18 2 19.79 19.79 0 01-8.63-3.07 19.5 19.5 0 01-6-6 19.79 19.79 0 01-3.07-8.67A2 2 0 014.11 2h3a2 2 0 012 1.72c.127.96.361 1.903.7 2.81a2 2 0 01-.45 2.11L8.09 9.91a16 16 0 006 6l1.27-1.27a2 2 0 012.11-.45c.907.339 1.85.573 2.81.7A2 2 0 0122 16.92z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </button>
      </div>

      {/* Messages */}
      <div style={{ flex: 1, overflowY: 'auto', padding: '16px' }}>
        {loading ? (
          <div style={{ textAlign: 'center', color: 'var(--text-muted)', padding: 24 }}>
            Loading messages...
          </div>
        ) : messages.length === 0 ? (
          <div style={{ textAlign: 'center', color: 'var(--text-muted)', padding: 24 }}>
            <div style={{ fontSize: 48, marginBottom: 12 }}>
              <svg width="48" height="48" viewBox="0 0 24 24" fill="none" style={{ color: 'var(--text-muted)' }}>
                <path d="M21 15a2 2 0 01-2 2H7l-4 4V5a2 2 0 012-2h14a2 2 0 012 2v10z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </div>
            <p>This is the beginning of your conversation with {otherUser?.username}</p>
          </div>
        ) : (
          groupedMessages.map(({ date, messages: dateMessages }) => (
            <div key={date}>
              {/* Date divider */}
              <div style={{ display: 'flex', alignItems: 'center', gap: 16, margin: '16px 0' }}>
                <div style={{ flex: 1, height: 1, background: 'var(--border)' }} />
                <span style={{ fontSize: 12, color: 'var(--text-muted)', fontWeight: 500 }}>{date}</span>
                <div style={{ flex: 1, height: 1, background: 'var(--border)' }} />
              </div>

              {dateMessages.map((message, index) => {
                const isOwn = message.user_id === currentUser.id;
                const showAvatar = index === 0 || dateMessages[index - 1]?.user_id !== message.user_id;
                const user = message.user || (isOwn ? currentUser : otherUser);
                const avatarColor = user ? getAvatarColor(user.id) : '#666';

                return (
                  <div
                    key={message.id}
                    onContextMenu={(e) => handleContextMenu(e, message.id, message.user_id)}
                    style={{
                      display: 'flex',
                      gap: 12,
                      marginBottom: showAvatar ? 16 : 4,
                      paddingLeft: showAvatar ? 0 : 48,
                    }}
                  >
                    {showAvatar && (
                      <div
                        style={{
                          width: 36,
                          height: 36,
                          borderRadius: '50%',
                          background: user?.avatar_url
                            ? `url(${user.avatar_url}) center/cover`
                            : avatarColor,
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          color: 'white',
                          fontWeight: 600,
                          fontSize: 14,
                          flexShrink: 0,
                        }}
                      >
                        {!user?.avatar_url && user?.username[0].toUpperCase()}
                      </div>
                    )}

                    <div style={{ flex: 1, minWidth: 0 }}>
                      {showAvatar && (
                        <div style={{ display: 'flex', alignItems: 'baseline', gap: 8, marginBottom: 4 }}>
                          <span style={{ fontWeight: 600, fontSize: 14, color: 'var(--text-primary)' }}>
                            {user?.username}
                          </span>
                          <span style={{ fontSize: 11, color: 'var(--text-muted)' }}>
                            {formatTime(message.created_at)}
                          </span>
                        </div>
                      )}

                      {editingMessageId === message.id ? (
                        <div style={{ display: 'flex', gap: 8 }}>
                          <input
                            type="text"
                            value={editingContent}
                            onChange={(e) => setEditingContent(e.target.value)}
                            onKeyDown={(e) => {
                              if (e.key === 'Enter') handleEditMessage(message.id);
                              if (e.key === 'Escape') {
                                setEditingMessageId(null);
                                setEditingContent('');
                              }
                            }}
                            autoFocus
                            style={{
                              flex: 1,
                              padding: '8px 12px',
                              background: 'var(--bg-tertiary)',
                              border: '1px solid var(--border)',
                              borderRadius: 'var(--radius-sm)',
                              color: 'var(--text-primary)',
                              fontSize: 14,
                              outline: 'none',
                            }}
                          />
                          <button
                            onClick={() => handleEditMessage(message.id)}
                            style={{
                              padding: '8px 16px',
                              background: 'var(--accent)',
                              border: 'none',
                              borderRadius: 'var(--radius-sm)',
                              color: 'white',
                              cursor: 'pointer',
                            }}
                          >
                            Save
                          </button>
                        </div>
                      ) : (
                        <p
                          style={{
                            margin: 0,
                            fontSize: 14,
                            color: 'var(--text-primary)',
                            wordBreak: 'break-word',
                          }}
                        >
                          {message.content}
                          {message.updated_at !== message.created_at && (
                            <span style={{ fontSize: 11, color: 'var(--text-muted)', marginLeft: 6 }}>
                              (edited)
                            </span>
                          )}
                        </p>
                      )}
                    </div>
                  </div>
                );
              })}
            </div>
          ))
        )}

        {/* Typing indicator */}
        {typingUsers.length > 0 && (
          <div style={{ fontSize: 13, color: 'var(--text-muted)', padding: '8px 0' }}>
            {otherUser?.username} is typing...
          </div>
        )}

        <div ref={messagesEndRef} />
      </div>

      {/* Input */}
      <form
        onSubmit={handleSendMessage}
        style={{
          padding: '12px 16px',
          borderTop: '1px solid var(--border)',
          background: 'var(--bg-secondary)',
        }}
      >
        <div
          style={{
            display: 'flex',
            gap: 8,
            background: 'var(--bg-tertiary)',
            borderRadius: 'var(--radius-md)',
            padding: '8px 16px',
          }}
        >
          <input
            type="text"
            placeholder={`Message @${otherUser?.username}`}
            value={newMessage}
            onChange={handleInputChange}
            style={{
              flex: 1,
              background: 'transparent',
              border: 'none',
              color: 'var(--text-primary)',
              fontSize: 14,
              outline: 'none',
            }}
          />
          <button
            type="submit"
            disabled={!newMessage.trim() || sending}
            style={{
              background: 'none',
              border: 'none',
              color: newMessage.trim() ? 'var(--accent)' : 'var(--text-muted)',
              cursor: newMessage.trim() ? 'pointer' : 'not-allowed',
              display: 'flex',
              alignItems: 'center',
            }}
          >
            <svg width="20" height="20" viewBox="0 0 24 24" fill="none">
              <path d="M22 2L11 13M22 2l-7 20-4-9-9-4 20-7z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>
        </div>
      </form>

      {/* Context menu */}
      {contextMenu && (
        <>
          <div
            onClick={() => setContextMenu(null)}
            style={{
              position: 'fixed',
              inset: 0,
              zIndex: 999,
            }}
          />
          <div
            style={{
              position: 'fixed',
              left: contextMenu.x,
              top: contextMenu.y,
              zIndex: 1000,
              background: 'var(--bg-floating)',
              borderRadius: 'var(--radius-sm)',
              boxShadow: 'var(--shadow-lg)',
              padding: 4,
              minWidth: 140,
            }}
          >
            <button
              onClick={() => {
                const msg = messages.find((m) => m.id === contextMenu.messageId);
                if (msg) {
                  setEditingMessageId(msg.id);
                  setEditingContent(msg.content);
                }
                setContextMenu(null);
              }}
              style={{
                width: '100%',
                padding: '8px 12px',
                background: 'transparent',
                border: 'none',
                borderRadius: 'var(--radius-sm)',
                color: 'var(--text-primary)',
                fontSize: 13,
                textAlign: 'left',
                cursor: 'pointer',
              }}
              onMouseEnter={(e) => (e.currentTarget.style.background = 'var(--bg-modifier-hover)')}
              onMouseLeave={(e) => (e.currentTarget.style.background = 'transparent')}
            >
              Edit
            </button>
            <button
              onClick={() => handleDeleteMessage(contextMenu.messageId)}
              style={{
                width: '100%',
                padding: '8px 12px',
                background: 'transparent',
                border: 'none',
                borderRadius: 'var(--radius-sm)',
                color: 'var(--danger)',
                fontSize: 13,
                textAlign: 'left',
                cursor: 'pointer',
              }}
              onMouseEnter={(e) => (e.currentTarget.style.background = 'var(--bg-modifier-hover)')}
              onMouseLeave={(e) => (e.currentTarget.style.background = 'transparent')}
            >
              Delete
            </button>
          </div>
        </>
      )}
    </div>
  );
};

export default DMChat;
