import React, { useEffect, useRef, useState } from 'react';
import { debugLogger, LogEntry } from '../services/debugLogger';

const ConsoleView: React.FC = () => {
  const [logs, setLogs] = useState<LogEntry[]>([]);
  const [autoScroll, setAutoScroll] = useState(true);
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const unsubscribe = debugLogger.subscribe(setLogs);
    return unsubscribe;
  }, []);

  useEffect(() => {
    if (autoScroll && containerRef.current) {
      containerRef.current.scrollTop = containerRef.current.scrollHeight;
    }
  }, [logs, autoScroll]);

  const handleScroll = () => {
    if (!containerRef.current) return;
    const { scrollTop, scrollHeight, clientHeight } = containerRef.current;
    const isAtBottom = scrollHeight - scrollTop - clientHeight < 50;
    setAutoScroll(isAtBottom);
  };

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString('ru-RU', {
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
    });
  };

  const getTypeIcon = (type: LogEntry['type']) => {
    switch (type) {
      case 'info': return '';
      case 'success': return '';
      case 'warning': return '';
      case 'error': return '';
      case 'webrtc': return '';
      case 'websocket': return '';
      case 'screenshare': return '';
      case 'chat': return '';
      case 'user': return '';
      default: return '';
    }
  };

  const getTypeColor = (type: LogEntry['type']) => {
    switch (type) {
      case 'info': return '#8e9297';
      case 'success': return '#3ba55c';
      case 'warning': return '#faa61a';
      case 'error': return '#f04747';
      case 'webrtc': return '#5865f2';
      case 'websocket': return '#9b59b6';
      case 'screenshare': return '#3ba55c';
      case 'chat': return '#00b0f4';
      case 'user': return '#eb459e';
      default: return '#8e9297';
    }
  };

  return (
    <div style={styles.container}>
      <div style={styles.header}>
        <div style={styles.headerLeft}>
          <span style={styles.headerIcon}></span>
          <h3 style={styles.headerTitle}></h3>
          <span style={styles.logCount}>{logs.length} </span>
        </div>
        <div style={styles.headerRight}>
          <label style={styles.autoScrollLabel}>
            <input
              type="checkbox"
              checked={autoScroll}
              onChange={(e) => setAutoScroll(e.target.checked)}
              style={styles.checkbox}
            />
            
          </label>
          <button style={styles.clearButton} onClick={() => debugLogger.clear()}>
            
          </button>
        </div>
      </div>

      <div
        ref={containerRef}
        style={styles.logsContainer}
        onScroll={handleScroll}
      >
        {logs.length === 0 ? (
          <div style={styles.emptyMessage}>
             .         .
          </div>
        ) : (
          logs.map((log) => (
            <div key={log.id} style={styles.logEntry}>
              <span style={styles.timestamp}>{formatTime(log.timestamp)}</span>
              <span style={styles.typeIcon}>{getTypeIcon(log.type)}</span>
              <span style={{ ...styles.message, color: getTypeColor(log.type) }}>
                {log.message}
              </span>
              {log.details && (
                <span style={styles.details}>{log.details}</span>
              )}
            </div>
          ))
        )}
      </div>
    </div>
  );
};

const styles: { [key: string]: React.CSSProperties } = {
  container: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    backgroundColor: 'var(--bg-primary)',
    height: '100%',
  },
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: '16px',
    borderBottom: '1px solid var(--border)',
    backgroundColor: 'var(--bg-secondary)',
  },
  headerLeft: {
    display: 'flex',
    alignItems: 'center',
    gap: '8px',
  },
  headerIcon: {
    fontSize: '20px',
  },
  headerTitle: {
    margin: 0,
    color: 'var(--text-primary)',
    fontSize: '16px',
    fontWeight: 600,
  },
  logCount: {
    fontSize: '12px',
    color: 'var(--text-muted)',
    backgroundColor: 'var(--bg-tertiary)',
    padding: '2px 8px',
    borderRadius: '10px',
  },
  headerRight: {
    display: 'flex',
    alignItems: 'center',
    gap: '12px',
  },
  autoScrollLabel: {
    display: 'flex',
    alignItems: 'center',
    gap: '6px',
    fontSize: '12px',
    color: 'var(--text-secondary)',
    cursor: 'pointer',
  },
  checkbox: {
    cursor: 'pointer',
  },
  clearButton: {
    padding: '6px 12px',
    backgroundColor: 'var(--bg-tertiary)',
    border: '1px solid var(--border)',
    borderRadius: '4px',
    color: 'var(--text-secondary)',
    fontSize: '12px',
    cursor: 'pointer',
  },
  logsContainer: {
    flex: 1,
    overflowY: 'auto',
    padding: '8px',
    fontFamily: 'Consolas, Monaco, "Courier New", monospace',
    fontSize: '12px',
    lineHeight: 1.6,
  },
  emptyMessage: {
    color: 'var(--text-muted)',
    textAlign: 'center',
    padding: '40px 20px',
    fontSize: '14px',
  },
  logEntry: {
    display: 'flex',
    alignItems: 'flex-start',
    gap: '8px',
    padding: '4px 8px',
    borderRadius: '4px',
    marginBottom: '2px',
  },
  timestamp: {
    color: 'var(--text-muted)',
    fontSize: '11px',
    minWidth: '70px',
    flexShrink: 0,
  },
  typeIcon: {
    fontSize: '12px',
    flexShrink: 0,
  },
  message: {
    flex: 1,
    wordBreak: 'break-word',
  },
  details: {
    color: 'var(--text-muted)',
    fontSize: '11px',
    marginLeft: '4px',
  },
};

export default ConsoleView;
